import React, { useState, useEffect } from 'react';
import {
  BrowserRouter as Router, Switch, Route, HashRouter, Redirect
} from "react-router-dom";
import { createMuiTheme } from '@material-ui/core/styles';
import { ThemeProvider } from '@material-ui/styles';
// unregister home
import Home from './components/home/home.component';
// login and create account
import Login from './components/login/login.component';
import Registration from './components/login/registration.component';
import Reset from './components/login/reset-password.component';
import PinCode from './components/login/pin-code.component';
import Confirmation from './components/login/email-confirmation.component';
// instractor
import InstractorDashboard from './components/instractor/instractor-dashboard/home-instractor.component';
import BasicInformation from './components/instractor/create-profile/basic.information.component';
import WorkExperience from './components/instractor/create-profile/work-experience.component';
import Requirements from './components/instractor/create-profile/requirements.component';
import EditProfile from './components/instractor/edit-profile';
import Header from './components/instractor/header/header.component';
import ViewProfile from './components/instractor/view-profile';
import Sessions from './components/instractor/sessions';
import Questions from './components/instractor/questions/index.component'
import Earnings from './components/instractor/earnings';
import AddPayment from './components/instractor/earnings/add-payment.component'
// student pages
import Nav from './components/student/nav/nav.component';
import DashboardStudent from './components/student/dashboard/index.component';
import ProfileStudent from './components/student/profile/index.component';
import Search from './components/student/search/index.component';
import Footer from './components/student/footer/footer.component';
import InstractorProfile from './components/student/instractor-profile';
import ReserveSession from './components/student/instractor-profile/reserve-session';
import CheckOut from './components/student/instractor-profile/reserve-session/check-out';
// both
import Notifications from './components/notifications';
import Cometchat from './components/cometchat';

import PageNotFound from './components/page-not-found/index.component';
import Auth from './Auth';

import './App.css';

const theme = createMuiTheme({
  palette: {
    primary: {
      light: '#afd2b2',
      main: '#3C7A50',
      dark: '',
      contrastText: '#fff',
    }
  },
});

function App() {
  const [userAuth, setUserAuth] = useState();
  const [show , setShow] = useState();



  let checkAuth = JSON.parse(localStorage.getItem('QA_jwt'));

 

  useEffect(() => {
    if (checkAuth) {
      setUserAuth(checkAuth);

    }   
  }, []);
  
  // if (!userAuth) {
  //   return (
  //     <HashRouter>
  //       <Login setUserAuth={setUserAuth} />
  //     </HashRouter>
  //   )
  // }
  return (
    <ThemeProvider theme={theme}>
      <HashRouter>
        <Switch>
          <Route exact path="/">
            <Home />
          </Route>
          <Route exact path="/login">
            <Login setUserAuth={setUserAuth} />
          </Route>
          <Route exact path="/registration">
            <Registration />
          </Route>
          <Route exact path="/reset">
            <Reset />
          </Route>
          <Route exact path="/pinCode">
            <PinCode />
          </Route>
          <Route exact path="/confirmation">
            <Confirmation />
          </Route>
          <Route path="/session">
            <Cometchat />
          </Route>
          {/* {userAuth?.Instructor || userAuth?.SubInstructor  && */}
          {/* } */}
          {userAuth?.Student ?
            <>
              <Nav />
              <main style={{ minHeight: '83vh' }}>
                <Route exact path="/dashboard" render={props => <Auth><DashboardStudent {...props} /></Auth>} />
                <Route exact path="/profile" render={props => <Auth><ProfileStudent {...props} /></Auth>} />
                <Route exact path="/search" render={props => <Auth><Search {...props} /></Auth>} />
                <Route exact path="/instractorProfile" render={props => <Auth><InstractorProfile {...props} /></Auth>} />
                <Route exact path="/reserveSession" render={props => <Auth><ReserveSession {...props} /></Auth>} />
                <Route exact path="/checkOut" render={props => <Auth><CheckOut {...props} /></Auth>} />
                <Route exact path="/notifications" render={props => <Auth><Notifications {...props} /></Auth>} />
              </main>
              <Footer />
            </> :
            <>
              <Route exact path="/basicInformation" render={props => <Auth><BasicInformation {...props} /></Auth>} />
              <Route exact path="/workExperience" render={props => <Auth><WorkExperience {...props} /></Auth>} />
              <Route exact path="/requirements" render={props => <Auth><Requirements {...props} /></Auth>} />
              <>
                <Header />
                <Route exact path="/dashboard" render={props => <Auth><InstractorDashboard {...props} /></Auth>} />
                <Route exact path="/editProfile" render={props => <Auth><EditProfile {...props} /></Auth>} />
                <Route exact path="/viewProfile" render={props => <Auth><ViewProfile {...props} /></Auth>} />
                <Route exact path="/sessions" render={props => <Auth><Sessions {...props} /></Auth>} />
                <Route exact path="/notifications" render={props => <Auth><Notifications {...props} /></Auth>} />
                <Route exact path="/questions" render={props => <Auth><Questions {...props} /></Auth>} />
                <Route exact path="/earnings" render={props => <Auth><Earnings {...props} /></Auth>} />
                <Route exact path="/addPayment" render={props => <Auth><AddPayment {...props} /></Auth>} />
              </>
            </>}
          <Route path="*" component={PageNotFound} />
        </Switch>
      </HashRouter>
    </ThemeProvider>
  );
}

export default App;
