export const LOGIN = ` mutation($email : Email! $password :  Password!){
    globalLogin(data:{
      email : $email
     password : $password
    }){
      BearerTokenResponse{
        access_token
        refresh_token
      }
      Student{
        id
        email
        first_name
      }
      Instructor{
        id
        email
        first_name
      }
      SubInstructor{
        id
        email
      }
      Status{
        code
        message
      }
    }
  }`
export const LOGIN_GOOGLE = `mutation($token : String! $provider : String!){
    globalLoginSocialite(data: {
      token :$token ,
      provider : $provider
    }){
      BearerTokenResponse{
        access_token
      }
      Student{
        email
      }
      Instructor{
        email
      }
      SubInstructor{
        email
      }
      Status{
        code
        message
      }
    }
  }
  `
export const SEND_PIN_CODE = `mutation($email : Email! $sendPinCodeFor : sendPinCodeFor!){
    globalSendPinCode(data:{
        email : $email
        sendPinCodeFor : $sendPinCodeFor
    }){
        Status{
        code
        message
        }
    }
    }`
export const VERFY_PIN_CODE = `
    mutation($email : Email! , $pin_code : String!){
        globalverifyPinCode(data:{
              email : $email
          pin_code : $pin_code
        }){
          Status{
            code ,
            message
          }
        }
     }`
export const PASSWORD_RESET = ` mutation( $password : Password! $email : Email! $pin_code : String !){
globalPasswordresets(data: {
password  : $password
verifyPinCodeInput : {
email : $email ,
pin_code : $pin_code
}
}){
BearerTokenResponse{
access_token
refresh_token
expires_in
token_type
}
Student{
email
}
Instructor{
email
}
SubInstructor{
email
}
Status{
code
message
}
}
}`