import React , {useState , useEffect} from 'react';

import ProfileStrength from '../../profile-strength/profile-strength.component';
import Summary from './summary.component';
import Certificates from './certificates.component';
import Other from './other.component';
import Info from './info.component';

import {Row , Col } from 'react-bootstrap';
import Skeleton from '@material-ui/lab/Skeleton';

import text from '../../../languages/english/translate.json';



export default function InstractorProfile(props){
    const [userData , setUserData] = useState(props.location?.state)
    let pageText = text.instractorHome.dashboard;

    useEffect(()=>{
        if(props.location.state){
            localStorage.setItem('QA-userProfile' , JSON.stringify(userData))

        }
        else{
            let data = JSON.parse(localStorage.getItem('QA-userProfile'))
            setUserData(data)
        }
    }, [])
    console.log(userData);

    if(!userData){
        return(
            <main className="container mt-4 mb-4">
                <Row>
                    <Col md={8}>
                        <Skeleton variant="rect" height={530} />
                    </Col>
                    <Col md={4}>
                        <Skeleton variant="rect" height={230} />
                    </Col>
                </Row>
            </main>
        )
    }
    else{
        return(
            <main className="container mt-4 mb-4">
                <Row>
                    <Col md={8}>
                        <Info data={userData}/>
                        <div className="mt-4"></div>
                        <Summary data={userData}/>
                        <div data={userData} className="mt-4"></div>
                        <Certificates data={userData} />
                        <div className="mt-4"></div>
                        <Other data={userData}/>
                    </Col>
                    <Col md={4}>
                        <ProfileStrength 
                                title={pageText.improveProfile}
                                description = {pageText.profileStrengthDesc}
                                percentage="63"
                                complete={pageText.completeProfile}
                                strength={pageText.profileStrength} 
                        />
                    </Col>
                </Row>
            </main>
        )
    }
}