import React, { Component } from 'react';
import { withRouter } from 'react-router';
import PopUp from '../../../../pop-up/pop-up.component';
// styles
import { FormControl, InputLabel, Select, TextField, FormControlLabel, Checkbox, Button } from '@material-ui/core';
import { Row, Col } from 'react-bootstrap';
import Skeleton from '@material-ui/lab/Skeleton';
import AddIcon from '@material-ui/icons/Add';
import Calendar from './calendar.component';
import $ from 'jquery';
// language for test
import text from '../../../../../languages/english/translate.json';

class Index extends Component {
    constructor(props) {
        // get the date of today
        let date_ob = new Date();
        // adjust 0 before single digit date
        let date = ("0" + date_ob.getDate()).slice(-2);
        // current month
        let month = ("0" + (date_ob.getMonth() + 1)).slice(-2);
        // current year
        let year = date_ob.getFullYear();
        let maxYear = year + 5;
        let reservedSession = localStorage.getItem('arryOfReservedSession') ? JSON.parse(localStorage.getItem('arryOfReservedSession')) : [];
        super(props);
        this.state = {
            arryOfReservedSession : reservedSession ,
            minDate: year + "-" + month + "-" + date,
            maxDate: maxYear + "-" + month + "-" + date,
            showReserveSession : false ,
            showReserve: false,
            showReservedSession: false,
            repeat: true,
            reservationType: 1,
            duration: 1,
            repeatNumber: 1,
            date: year + "-" + month + "-" + date,
            errors: {
                time: '',
                form: '',
                note : ''
            }

        };
    }
    componentWillReceiveProps(currentProps) {
        if (currentProps.data?.courseType) {
            // filter courses types to remove type both
            let courseType = currentProps.data.courseType.filter(course => course.id !== 3);
            this.setState({ ...this.state, courseType: courseType })
        }
        if (currentProps.data?.durations) {
            this.setState({ ...this.state, durations: currentProps.data.durations })
        }
    }
    render() {
        let data = text.student.instractorProfile.reserveSession.calendar;
        const handleCloseReserve = () => {
            let errors = this.state.errors;
            errors.time = '';
            errors.form = '';
            this.setState({ ...this.state, showReserve: false, errors: errors, date: '', time: '', showDate: false })
        }
        const handleCloseReservedSession = () => {
            let errors = this.state.errors;
            errors.time = '';
            errors.form = '';
            this.setState({ ...this.state, errors: errors, showReservedSession: false })
        }
        const handleCloseReserveSesion = () => {
            let errors = this.state.errors;
            errors.time = '';
            errors.form = '';
            this.setState({ ...this.state, errors: errors, showReserveSession: false, note : '' })
        }
        const handleCancelReservedSession = () => {

            let id = this.state.session.id;
            let cell = document.querySelector(`[data-id="${id}" ]`);
            cell.classList.add('bg-white');
            cell.classList.remove('bg-green');

            let arryOfReservedSession = this.state.arryOfReservedSession.filter(arr => parseInt(arr.id) !== parseInt(id));
            this.setState({ ...this.state, arryOfReservedSession : arryOfReservedSession ,showReservedSession : false , note : '' });
            localStorage.setItem(arryOfReservedSession ,  JSON.stringify(arryOfReservedSession))
        }
        const handleChange = props => event => {
            let errors = this.state.errors;
            errors.time = '';
            errors.form = '';
            if (props === 'repeat') {
                if (event.target.checked === false) {
                    this.setState({ ...this.state,  [props]: event.target.checked, repeatNumber: 0 });
                }
                else {
                    this.setState({ ...this.state, [props]: event.target.checked });
                }
            }
            else if (props === 'time') {
                let hour = event.target.value.substring(0, 2)
                errors.time = ''
                if (parseInt(hour[0]) === 0) {
                    if (parseInt(hour[1]) >= 0 && parseInt(hour[1]) < 8) {
                        errors.time = "This Field must be between 8AM and 11PM"
                    }
                }
                this.setState({ ...this.state,  [props]: event.target.value + ":00" });
            }
            else {
                this.setState({ ...this.state, [props]: event.target.value });
            }
        }
        const handleSaveReserveSession = () => {
            let sessionWithNote = this.state.session
            sessionWithNote['note'] = this.state.note ? this.state.note : '' ;
            this.setState({...this.state , arryOfReservedSession :  [...this.state.arryOfReservedSession , sessionWithNote] , showReserveSession : false , note : ''})
            let arr =  [...this.state.arryOfReservedSession , sessionWithNote]
            localStorage.setItem('arryOfReservedSession' , JSON.stringify(arr))
            let id = this.state.session.id;
            let cell = document.querySelectorAll(`[data-id="${id}" ]`);
            cell.forEach(ele =>{
                ele.classList.add('bg-green');
                ele.classList.add('text-white');
                ele.classList.remove('bg-white');
            });
        }
     
        const handleReserveSession = (session) => 
        {
            this.setState({ ...this.state, showReserveSession: true, session : session , sessionDetails : session});
        }
        const handleShowSessionDetails = (session) => this.setState({ ...this.state, showReservedSession: true, session: session })
            return (
                <div className=" mb-3 ">
                    <div>
                        <Calendar
                            reserveSession={handleReserveSession}
                            showSessionDetails={handleShowSessionDetails} 
                            reservedSession={this.state.arryOfReservedSession}/>
                    </div>
                    <div>
                        {/*  show reserve session */}
                        <PopUp
                            title={data.newReservation}
                            show={this.state.showReserveSession}
                            hide={handleCloseReserveSesion}
                            cancelBtn={data.cancelBtn}
                            cancelFun={handleCloseReserveSesion}
                            saveBtn={data.reserveSessionBtn}
                            saveFun={handleSaveReserveSession}
                            content={
                                this.state.session &&
                                <div>
                                    <Row className="border-bottom pt-2 pb-2 m-0">
                                        <Col className="text-left text-dark" xs={6}>
                                            <p style={{fontSize : '16px'}} className="bold mb-1">{data.date}</p></Col>
                                        <Col xs={6}>
                                            <p style={{fontSize : '16px'}} className="bold mb-1">{this.state.session.date}</p>
                                        </Col>
                                    </Row>
                                    <Row className="border-bottom pt-2 pb-2 m-0">
                                        <Col className="text-left  text-dark" xs={6}>
                                            <p style={{fontSize : '16px'}} className="bold mb-1">{data.time}</p>
                                        </Col> 
                                        <Col xs={6}>
                                            <p style={{fontSize : '16px'}} className="bold mb-1">{this.state.session.time}</p>
                                        </Col>
                                    </Row>
                                    <Row className="border-bottom pt-2 pb-2 m-0">
                                        <Col className="text-left  text-dark" xs={6}>
                                            <p style={{fontSize : '16px'}} className="bold mb-1">{data.duration}</p>
                                        </Col>
                                        <Col xs={6}>
                                            <p style={{fontSize : '16px'}} className="bold mb-1">{this.state.session.rate_minutes_type.name}</p>
                                        </Col>

                                    </Row>
                                    <Row className="pt-2 pb-2 m-0">
                                        <Col className="text-left  text-dark" xs={6}>
                                            <p style={{fontSize : '16px'}} className="bold mb-1">{data.CourseType}</p>
                                        </Col> 
                                        <Col xs={6}> 
                                            <p style={{fontSize : '16px'}} className="bold mb-1">{this.state.session.course_type.name}</p>
                                        </Col>
                                    </Row>
                                    <Row className="pt-2 pb-2 m-0">
                                        <Col md={12} className="text-left  text-dark">
                                            <InputLabel className="mb-1" htmlFor="component-outlined">{data.notes}</InputLabel>
                                            <TextField
                                                fullWidth
                                                size="small"
                                                multiline
                                                style={{fontSize : '16px'}}
                                                className="text-dark"
                                                placeholder = "Write here"
                                                variant="outlined"
                                                onChange={handleChange('note')}
                                            />
                                            <p className="text-danger mb-1">{this.state.errors.note}</p>
                                        </Col>

                                    </Row>
                                </div>
                            }
                        />
                        {/*  show reserved session */}
                        <PopUp
                            title={data.editReservation}
                            show={this.state.showReservedSession}
                            hide={handleCloseReservedSession}
                            cancelBtn={data.cancelReservationBtn}
                            cancelFun={handleCancelReservedSession}
                            content={
                                this.state.session &&
                                <div>
                                    <Row className="border-bottom pt-2 pb-2 m-0">
                                        <Col className="text-left bold text-dark" xs={6}>{data.date}</Col> <Col xs={6}>{this.state.session.date}</Col>
                                    </Row>
                                    <Row className="border-bottom pt-2 pb-2 m-0">
                                        <Col className="text-left bold text-dark" xs={6}>{data.time}</Col> <Col xs={6}>{this.state.session.time}</Col>
                                    </Row>
                                    <Row className="border-bottom pt-2 pb-2 m-0">
                                        <Col className="text-left bold text-dark" xs={6}>{data.duration}</Col> <Col xs={6}>{this.state.session.rate_minutes_type.name}</Col>
                                    </Row>
                                    <Row className="pt-2 pb-2 m-0">
                                        <Col className="text-left bold text-dark" xs={6}>{data.CourseType}</Col> <Col xs={6}>{this.state.session.course_type.name}</Col>
                                    </Row>
                                </div>
                            }
                        />
                    </div>
                </div>
            )
        }
}

    export default withRouter(Index)