import React , {useState} from 'react';

import {Row , Col , Button} from 'react-bootstrap';

import Text from '../../../../../languages/english/translate.json';


export default function Summary(props) {
    let pageText = Text.student.instractorProfile.checkOut;
    let data =  JSON.parse((localStorage.getItem('arryOfReservedSession')));
    let sessionFees = 0;
    data && data.map(session => sessionFees += session.rate_salary);
    return(
        <section className="border">
            <article className="">
                <div className='p-2 border-bottom'>
                    <h6 className="mb-1" style={{fontSize : '18px'}}>{pageText.summaryTitle}</h6>
                    <p style={{fontSize : '14px'}} className="mb-0 pb-2 text-grey">{pageText.summaryDescription}</p>
                </div>
                <div className='p-2 mt-1'>
                    {data && data.map(ele=>
                        { return(
                            <Row className=" m-0 border-list mb-1">
                                    <Col xs={4} className="text-green pl-1">
                                        <p className="mb-1" style={{fontSize : '14px'}}>{ele.date}</p>
                                    </Col>
                                    <Col xs={7} className="text-green pr-0">
                                        <p className="mb-1" style={{fontSize : '14px'}}>
                                            <span className="pr-2">From</span>
                                            {ele.time}
                                            <span className="pl-2 pr-2">To</span>{ele.to_time}</p>
                                    </Col>
                            </Row>
                            )
                        }
                    )}
                </div>
            </article>
            {data && 
                <div className='mt-1'>
                    <p style={{fontSize : '14px'}} className="text-dark bg-light mb-0 p-2 bold">{pageText.payment}</p>
                    <article className=" p-2">
                        <Row className="m-0">
                            <Col xs={6} className="text-grey pl-0" ><p className="mt-2 mb-2" style={{fontSize : '14px'}}>Total sessions</p></Col>
                            <Col xs={6} className="text-grey pr-0 text-right" ><p className="mt-2 mb-2" style={{fontSize : '14px'}}>{data.length}</p></Col>
                            <Col xs={6} className="text-grey pl-0" ><p className="mt-2 mb-2" style={{fontSize : '14px'}}>Total payment</p></Col>
                            <Col xs={6} className="text-grey pr-0 text-right" ><p className="mt-2 mb-2" style={{fontSize : '14px'}}>{data[0].currency_type?.symbol} {sessionFees}</p></Col>
                        </Row>
                        <div className="w-100 mt-3">
                            <Button type="submit" className="btn-green w-100 mb-2">{pageText.completePayment}</Button>
                            <p style={{fontSize : '12px'}} className="text-center text-grey">{pageText.completDescription}</p>
                        </div>
                    </article>
                </div>
            }
        </section>
    )
}