import React, { Component } from 'react';
import { withRouter } from 'react-router';

// styles icons 
import {Row, Col } from 'react-bootstrap';

// language for test
import text from '../../../languages/english/translate.json';

function Welcome(props){
        let data = text.studentHome.dashboard;
        return (
            <>
                <Row className="m-0">
                    <Col>
                        <p className="mb-0">{data.hi} {props.data.first_name}</p>
                        <h3 className="text-green bold">{data.welcome}</h3>
                    </Col>
                </Row>
            </>
        )
    }

export default withRouter(Welcome)