import React from 'react';
import { withStyles, makeStyles } from '@material-ui/core/styles';
// style
import { FormGroup, FormControlLabel, Checkbox , Tooltip } from '@material-ui/core';
import Slider from '@material-ui/core/Slider'

export default function FilterByCourseType(props) {
    function valuetext() {
        return `${props.defaultValue}C`;
      }
    return (
        <>
            <article className="pr-3 pl-3">
                <p style={{fontSize : '14px'}} className="mb-3 bold">Filter by Price range</p>
                 <Slider
                    value={props.defaultValue}
                     onChange={(e , newValue)=>props.handleChange(newValue)}
                    valueLabelDisplay="on"
                    aria-labelledby="range-slider"
                    getAriaValueText={valuetext}
                    max="1000"
                    className="mt-2 mb-3"
                />
            </article>
            <hr className="mt-1 " style={{ backgroundColor: '#F3F3F3', height: '15px', border: '0' }} />
        </>
    )
}