import React, { Component } from 'react';
import { withRouter } from 'react-router';

// styles images and icons
import { Row, Col } from 'react-bootstrap';
import {
    TextField, InputLabel, FormControl, Select,
    Button, Chip
} from '@material-ui/core';

import { checkCv, checkImage, checkValidateForm } from '../../../validations/validation';

// language for test
import text from '../../../languages/english/translate.json';

class workExperienceForm extends Component {
    constructor(props) {
        super(props);
        this.state = {
            certificates: [],
            errors: {
                description: ''
            }
        }
    }
    componentDidMount() {
        if (this.props.data) {
            this.setState({
                first_name: this.props.data.first_name, last_name: this.props.data.last_name,
                gender: this.props.data.gender, date_of_birth: this.props.data.date_of_birth,
                email: this.props.data.email, profile: this.props.data.profile
            })
        }
        let data = JSON.parse(localStorage.getItem('workExperience'));
        if (data) {
            this.setState({ description: data.description, yearsOfExperience: data.yearsOfExperience })
        }
    }
    render() {
        let data = text["instractor-profile"].create.workExperience;

        const handleChange = props => event => {
            const value = event.target.value;
            let errors = this.state.errors;
            errors.certificates = '';
            errors.video = '';
   
            if (props === 'certificates' && event.target.files[0]) {
                // make sure that the user attache image
                let file = event.target.files[0].type.split('/').pop().toLowerCase();

                if (this.state.certificates.length < 5 && (file === "jpeg" || file === "jpg" ||
                    file === "png" || file === "bmp" || file === "gif" || file === "svg+xml"
                    || file === "pdf")) {
                    this.setState({ ...this.state, [props]: [...this.state.certificates, event.target.files[0]] })
                }
                else {
                    errors.certificates = 'Please Enter a valid files';
                    this.setState({ ...this.state, errors: errors });

                }
            }
            else if (props === 'video' && event.target.files[0]) {

                let fileType = event.target.files[0].type.split('/').pop().toLowerCase();
                let fileSize = event.target.files[0].size / 1024 / 1024;
                if(fileType === "m4v" || fileType === "avi" ||
                    fileType === "mpg" || fileType === "mp4" || fileType === "webm") {
                    this.setState({ ...this.state, video: event.target.files[0] })
                } 
                else if(fileSize > 120) {

                    errors.video = 'The Video Upload must be 50MB maximum';
                }
                else {
                    errors.video = 'Please Enter a valid video.';
                }
                this.setState({ ...this.state, errors: errors });

            }
            else if (event.target.value) {
                this.setState({ ...this.state, [props]: event.target.value });
            }
            switch (props) {
                case 'description':
                    errors.description =
                        value.length <= 5000
                            ? ''
                            : text.validation.description;
                    break;
                default:
                    break;
            }

        }
        const validateForm = (errors) => {
            let valid = true;
            Object.values(errors).forEach(
                // if we have an error string set valid to false
                (val) => val.length > 0 && (valid = false)
            );
            return valid;
        }
        const handleSubmit = (event) => {
            event.preventDefault();

            // fileRequired("certificates", this.state.certificates);
            fileRequired("video", this.state.video);

            if (validateForm(this.state.errors)) {
                localStorage.setItem('workExperience', JSON.stringify(this.state));
                window.scrollTo({ top: "0", behavior: "smooth" })
                this.props.history.push({
                    pathname: "/requirements",
                    state: this.state
                })
            }
        }
        const fileRequired = (name, file) => {
            let errors = this.state.errors;
            errors[name] = file === undefined || file.length === 0 ? text.validation.required : ''
            this.setState({ ...this.state, errors: errors });
        }
        const handleDelete = (chipToDelete , type) => (e) => {
            switch (type) {
                case 'certificates':
                    let newCertificates = this.state.certificates.filter(file =>
                        file !== chipToDelete
                        )
                        this.setState({...this.state , certificates : newCertificates })
                    break;
                default:
                    break;
            }
        }
        return (
            <div className="shadow border">
                <div className="border-bottom p-4 text-dark">
                    <Row className="">
                        <Col>
                            <h4>{data.title}</h4>
                            <p className="mb-0">{data.description}</p>
                        </Col>
                    </Row>
                </div>
                <div className="p-4">
                    <div className=" pb-3 text-lef">
                        <form onSubmit={handleSubmit}>
                            {/* brief */}
                            <Row className="m-0 mb-2 w-100">
                                <Col className="p-0">
                                    <InputLabel required>
                                        {data.briefInput.name}
                                    </InputLabel>
                                    <TextField
                                        required
                                        multiline
                                        helperText={data.briefInput.validationText}
                                        rows={4}
                                        fullWidth
                                        size="small"
                                        variant="outlined"
                                        defaultValue={this.state.description && this.state.description}
                                        onChange={handleChange('description')}
                                    />
                                    <div className="text-danger text-left"><p className="mb-1">{this.state.errors.description}</p></div>
                                </Col>
                            </Row>
                            {/* Years of experience */}
                            <Row className="m-0 mb-0 w-100">
                                <Col className="pr-0 pl-1">
                                    <InputLabel required>
                                        {data.yearsOfExperience}
                                    </InputLabel>
                                    <FormControl
                                        required
                                        className="mb-3"
                                        variant="outlined" fullWidth size="small">
                                        <Select
                                            native
                                            name={data.yearsOfExperience}
                                            inputProps={{ value: this.state.yearsOfExperience && this.state.yearsOfExperience }}
                                            onChange={handleChange('yearsOfExperience')}
                                        >
                                            <option value=""></option>
                                            <option value="1">1</option>
                                            <option value="2">2</option>
                                            <option value="3">3</option>
                                            <option value="4">4</option>
                                            <option value="5">5</option>
                                            <option value="6">6</option>
                                            <option value="7">7</option>
                                            <option value="8">8</option>
                                            <option value="9">9</option>
                                            <option value="10">10</option>
                                        </Select>
                                    </FormControl>
                                </Col>
                            </Row>
                            {/* Uploads*/}
                            <Row className="m-0 mb-3">
                                <Col md={12} className="p-0  mb-2">
                                    <p className="mb-1">{data.uploadText}</p>
                                    <p className="text-center"> {this.state.certificates?.map(file =>
                                        <Chip onDelete={handleDelete(file , 'certificates')} deleteIcon className="mt-1 bg-danger-outline mr-1" label={file.name} />)}
                                    </p>
                                    <label
                                        className="mb-0 pt-1 pb-1 pr-3 pl-3 btn-green"
                                        style={{ cursor: 'pointer' }}
                                        htmlFor="uploadText">
                                        {data.browseBtn}
                                    </label>
                                    <input
                                        onChange={handleChange('certificates')}
                                        type="file"
                                        accept='*'
                                        className="d-none"
                                        id="uploadText"
                                    />
                                    <div className="text-danger text-left"><p className="mb-1">{this.state.errors.certificates}</p></div>
                                </Col>
                                <Col md={12} className="p-0">
                                    <p className="mb-1">{data.uploadVideo}</p>
                                    <p className="text-center">
                                        {
                                            this.state.video?.name &&
                                            <Chip  className=" bg-danger-outline mr-1" label={this.state.video.name} />
                                        }
                                    </p>
                                    <div className="text-danger text-left"><p className="mb-1">{this.state.errors.video}</p></div>
                                    <div>
                                        <label
                                            className="mb-0 pt-1 pb-1 pr-3 pl-3 btn-green"
                                            style={{ cursor: 'pointer' }}
                                            htmlFor="uploadVideo">
                                            {data.browseBtn}
                                        </label>
                                    </div>
                                    <input
                                        onChange={handleChange('video')}
                                        type="file"
                                        accept='*'
                                        className="d-none"
                                        id="uploadVideo"
                                    />
                                </Col>
                            </Row>
                            {/* form errors */}
                            <div className="text-danger text-left"><p className="mb-1">{this.state.errors.form}</p></div>
                            {/* back and continue buttons */}
                            <Row className="m-0 align-items-center w-100 border-top pt-3">
                                <Col className="text-left p-0">
                                    <Button
                                        onClick={e => {
                                            window.scrollTo({ top: "0", behavior: "smooth" })
                                            this.props.history.push({
                                                pathname: "/basicInformation"
                                            })
                                        }}
                                        className="btn-green-outline pl-4 pr-4">
                                        {data.backBtn}
                                    </Button>
                                </Col>
                                <Col className="text-right p-0">
                                    <Button
                                        type="submit"
                                        className="btn-green pl-4 pr-4">
                                        {data.continueBtn}
                                    </Button>
                                </Col>
                            </Row>
                        </form>
                    </div>
                </div>
            </div>
        )
    }
}

export default withRouter(workExperienceForm)
