import React from 'react';


import text from '../../../languages/english/translate.json';

import Container from './container.component';

export default function Other(props) {
    let pageText = text['instractor-profile'].view;
    let userData = props.data;

    return(
        <Container>
            {userData.Languages &&
                <article >
                    <h5 className="text-dark">{pageText.language}</h5>
                    {userData.Languages.map(ele=>
                        <p className="text-dark d-inline-block pr-2">{ele.name}</p>
                    )}
                </article>
            }
            {userData.student_gender_types &&
                <article>
                    <h5 className="text-dark">{pageText.preferredStudents}</h5>
                    <p className="text-dark">{userData.student_gender_types.name}</p>
                </article>
            }
            {userData.student_age_types &&
                <article>
                    <h5 className="text-dark">{pageText.preferredStudentAge}</h5>
                    <p className="text-dark">{userData.student_age_types.name}</p>
                </article>
            }
        </Container>
    )
}