import React from 'react';
import { Link } from 'react-router-dom';
// styles images and icons 
import { Container , Row , Col} from 'react-bootstrap';
import tajweed from '../../images/home/tajweed.png';
import tahfeez from '../../images/home/tahfeez.png';
import both from '../../images/home/both.png';
// language for test
import text from '../../languages/english/translate.json';

export default function Services() {
    let data = text?.home.services;
    return (
        <div className="max-h-100-responsive bg-white  pb-2 pt-5" id="services">
            <Container className="pt-5">
                <Row className="align-items-center m-0">
                    <Col md={9} sm={10} className="">
                        <div className="mb-2">
                            <small className="d-block text-grey">{data.smallTitle}</small>
                            <h2 className="text-dark pr-2 pl-2 title-line d-inline-block">
                                <span className="text-green">
                                    {data.title}
                                </span> 
                            </h2>
                            <div className="pt-3">
                                <p className=" green-line">
                                    {data.description}
                                </p>
                            </div>
                        </div>
                    </Col>
                    <Col md={4} sm={4} className="text-center pt-2 mb-3">
                        <div className="w-100">
                            <img src={tahfeez} className="img-fluid " alt="slide" />
                        </div>
                        <div>
                            <div className="w-100 text-center">
                                <h4>
                                    <span  className="border-green">{data.tahfeez}</span>
                                </h4>
                            </div>
                            <p className="text-center ">
                                {data.tahfeezDesc}
                            </p>
                        </div>
                        <button className="btn-green-outline pr-4 pl-4">
                            {data.btn}
                        </button>
                    </Col>
                    <Col  md={4} sm={4} className="text-center pt-2 mb-3">
                        <div className="w-100">
                            <img src={tajweed} className="img-fluid " alt="slide"/>
                        </div>
                        <div>
                            <div className="w-100 text-center">
                                <h4 > 
                                        <span  className="border-green">{data.tajweed}</span>
                                </h4>
                            </div>
                            <p className="text-center">
                                {data.tajweedDesc}
                            </p>
                        </div>
                        <button className="btn-green-outline pr-4 pl-4">
                                    {data.btn}
                         </button>
                    </Col>
                    <Col  md={4} sm={4} className="text-center pt-2 mb-3">
                        <div className="w-100">
                            <img src={both} className="img-fluid " alt="slide"/>
                        </div>
                        <div>
                            <div className="w-100 text-center">
                                <h4 > 
                                        <span  className="border-green">{data.tahfeezAndTajweed}</span>
                                </h4>
                            </div>
                            <p className="text-center">
                                {data.tahfeezAndTajweedDesc}
                            </p>
                        </div>
                        <button className="btn-green-outline pr-4 pl-4">
                                    {data.btn} 
                         </button>
                    </Col>
                </Row>
            </Container>
        </div>
    )
}