import React from 'react';
import { withRouter} from 'react-router-dom'

import Text from '../../languages/english/translate.json';
import NotificationListItem from './notification-list-item.component';

function Notifications(props) {
    if(props.location.state){
        localStorage.setItem('QA_notification' , JSON.stringify(props.location.state?.data))
    }
    let pageText = Text.notifications;
    let notifications = props.location.state?.data ||JSON.parse(localStorage.getItem('QA_notification'));
    return(
        <main className="container mt-5 mb-5">
            <header>
                <h5>{pageText.title}</h5>
                <p className="text-grey">{pageText.description}</p>
            </header>
            {notifications?.map(notif=>
                <NotificationListItem key={notif.id} data={notif}/>
            )}

        </main>
    )
}

export default withRouter(Notifications);