import React,{useEffect} from 'react';
import { withRouter } from 'react-router';
import { CometChat } from "@cometchat-pro/chat";

function Cometchat(props) {
  
  localStorage.setItem("sessionId" , props.location.state)

    useEffect(()=>{
      console.log(props)
      let sessionIdFromLocalstorage = localStorage.getItem('sessionId');
      let sessionID = props.location.state?  props.location.state : sessionIdFromLocalstorage;

      let audioOnly = false;
      let deafaultLayout = true;
      console.log(JSON.parse(localStorage.getItem('QA_jwt')))
      let to = JSON.parse(localStorage.getItem('QA_jwt')).Student  ? 'profile' : 'sessions' ;
      console.log(to);
      let callSettings = new CometChat.CallSettingsBuilder()
                          .enableDefaultLayout(deafaultLayout)
                          .setSessionID(sessionID)
                          .setIsAudioOnlyCall(audioOnly)
                          .build();

      CometChat.startCall(
      callSettings,
      document.getElementById("callScreen"),
      new CometChat.OngoingCallListener({
              onUserListUpdated: userList => {
                  console.log("user list:", userList);
          },
          onCallEnded: call => {
            console.log("Call ended:", call);
              props.history.push({
                pathname : to
            })
          },
          onError: error => {
            alert("!")
          console.log("Error :", error);
          },
          error: e => console.log(e),
          onMediaDeviceListUpdated: deviceList => {
          console.log("Device List:", deviceList);
          },
      })
      );
    },[])
    return (
      <div className="w-100" style={{height : '100vh'}} id="callScreen">
      </div>
    );
}

export default withRouter(Cometchat)