import React, { useState, useEffect } from 'react';
import { Link } from 'react-router-dom';
import { withRouter } from 'react-router';
import $ from 'jquery';

import { GET_INFO } from '../apis/apis';
import { Url } from '../../../url';

// styles and images
import { makeStyles } from '@material-ui/core/styles';
import InputBase from '@material-ui/core/InputBase';
import EditIcon from '@material-ui/icons/Edit';
import SettingsIcon from '@material-ui/icons/Settings';
import HelpIcon from '@material-ui/icons/Help';
import LanguageIcon from '@material-ui/icons/Language';
import ExitToAppIcon from '@material-ui/icons/ExitToApp';
import { Navbar, Nav, Dropdown } from 'react-bootstrap';
import { Avatar } from '@material-ui/core';
import SearchIcon from '@material-ui/icons/Search';
import logoWeb from '../../../images/logo/logo-1.svg';
import Skeleton from '@material-ui/lab/Skeleton';

// language for test
import text from '../../../languages/english/translate.json';
// search bar style
const useStyles = makeStyles(theme => ({
    search: {
        position: 'relative',
        borderRadius: theme.shape.borderRadius,
        backgroundColor: 'rgb(247, 245, 245)',
        border: '1px solid #E2E2E2',
        '&:hover': {
            backgroundColor: 'rgb(247, 245, 245)',
        },
        marginLeft: 0,
        width: '100%',
        [theme.breakpoints.up('sm')]: {
            marginLeft: theme.spacing(1),
            width: 'auto',
        },
    },
    searchIcon: {
        width: theme.spacing(7),
        height: '100%',
        position: 'absolute',
        pointerEvents: 'none',
        display: 'flex',
        alignItems: 'center',
        justifyContent: 'center',
        color: '#A3A3A3'
    },
    inputRoot: {
        color: '#606060',
    },
    inputInput: {
        padding: theme.spacing(1, 1, 1, 7),
        transition: theme.transitions.create('width'),
        width: '100%',
        [theme.breakpoints.up('sm')]: {
            width: 120,
            '&:focus': {
                width: 200,
            },
        },
    },
}));
function Header(props) {
    const [userData, setUserData] = useState();

    let data = text?.home.header;
    const classes = useStyles();
    const geStudentData = () => {
        if (localStorage.QA_jwt) {
            const token = JSON.parse(localStorage.getItem('QA_jwt')).BearerTokenResponse.access_token;
            //get user data
            $.ajax({
                url: Url,
                method: 'post',
                headers: {
                    authorization: `Bearer ${token}`
                },
                data: {
                    query: GET_INFO
                },
                dataType: 'json',
                success: res => {
                    if (res.data.meStudents) {
                        setUserData(res.data.meStudents)
                    }
                },
                cache: err => console.log(err)
            });
        }
    }
    useEffect(() => {
        geStudentData();
    },[])
    useEffect(() => {
        // handle get user data
        let searchInput = document.querySelector('#searchInput');
        if (searchInput) {
            searchInput.addEventListener('keypress', (e) => {
                if (e.key === 'Enter') {
                    let searchKey = document.querySelector('#searchInput').value;
                    props.history.push({
                        pathname: '/search',
                        state: searchKey,
                        search: searchKey
                    });
                    // searchInput.value = '';
                }
            });
        }
    }, []);

        return (
            <Navbar collapseOnSelect expand="lg" bg="white" className="shadow pr-5 pl-5" variant="light">
                <span className="pr-0 pl-0 container">
                    <Navbar.Brand className="pr-5"  >
                        <img src={logoWeb} className="img-fluid" alt="logo" />
                    </Navbar.Brand>
                    <Navbar.Toggle aria-controls="responsive-navbar-nav" />
                    <Navbar.Collapse id="responsive-navbar-nav">
                        <Nav className=" mr-auto ">
                            <Link
                                to='/dashboard'
                                className="pr-3 pl-3 nav-link"
                            >
                                Dashboard
                                    </Link>
                            <Link
                                to='/profile'
                                className="pr-3 pl-3 nav-link"
                            >
                                My profile
                                    </Link>
                            <Link
                                to='/message'
                                className="pr-3 pl-3 nav-link"
                            >
                                Messages
                            </Link>
                            <Link
                                to={{pathname : '/notifications' , state : userData?.notifications}}
                                className="pr-3 pl-3 nav-link">
                                Notifications
                            </Link>
                        </Nav>
                        <Nav className="row m-0 align-items-end">
                            <div className={classes.search}>
                                <div className={classes.searchIcon}>
                                    <SearchIcon />
                                </div>
                                <InputBase
                                    placeholder="Search"
                                    classes={{
                                        root: classes.inputRoot,
                                        input: classes.inputInput,
                                    }}
                                    className="search mr-sm-2"
                                    id="searchInput"
                                    inputProps={{ 'aria-label': 'search' }}
                                />
                            </div>
                            {userData &&
                            <>
                                <Nav.Link className="pb-0">
                                    <Avatar src={userData.profile_image? userData.profile_image.url : ''}  alt="profile"  />
                                </Nav.Link>
                                <Dropdown>
                                    <Dropdown.Toggle className="pb-0" variant="" id="dropdown-basic"> </Dropdown.Toggle>
                                    <Dropdown.Menu
                                        className="dropdown-shadow border-0 pt-0 pb-0"
                                        style={{ left: '-135px', top: '42px' }}>
                                        <div style={{ padding: '.25rem 1.5rem' }}>
                                            <p className="mb-0 text-capitalize bold" style={{ fontSize: '16px', color: '#333333' }}>{userData.first_name}</p>
                                            <p className="mb-1 text-grey" style={{ fontSize: '15px' }}>{userData.email}</p>
                                        </div>
                                        <Dropdown.Item eventKey="1">
                                            <button
                                                style={{ fontSize: '14px' }}
                                                className=" text-center w-100 btn-green-outline pt-1 pb-1 text-green"
                                                onClick={e => {
                                                    props.history.push({
                                                        pathname: '/profile',
                                                        state: userData
                                                    })
                                                }}
                                            >View Profile</button>
                                        </Dropdown.Item>
                                        <Dropdown.Divider className="mt-2 mb-2" style={{ borderTop: '11px solid #F2F2F2' }} />
                                        <Dropdown.Item eventKey="3" >
                                            <Link>
                                                <SettingsIcon className="mr-3" style={{ width: '18px' }} />
                                                <p style={{ fontSize: '14px' }} className="mb-0 d-inline-block">Account Settings</p>
                                            </Link>
                                        </Dropdown.Item>
                                        <Dropdown.Item eventKey="4" >
                                            <Link>
                                                <HelpIcon className="mr-3" style={{ width: '18px' }} />
                                                <p style={{ fontSize: '14px' }} className="mb-0 d-inline-block">Help center</p>
                                            </Link>
                                        </Dropdown.Item>
                                        <Dropdown.Item eventKey="5">
                                            <Link>
                                                <LanguageIcon className=" mr-3" style={{ width: '18px' }} />
                                                <p style={{ fontSize: '14px' }} className="mb-0 d-inline-block">Language</p>
                                            </Link>
                                        </Dropdown.Item>
                                        <Dropdown.Divider className="mt-2 mb-2" style={{ borderTop: '11px solid #F2F2F2' }} />
                                        <Dropdown.Item eventKey="6" >
                                            <Link
                                                to="/"
                                                onClick={e => {
                                                    localStorage.clear();
                                                }}>
                                                <ExitToAppIcon className="text-danger mr-3" style={{ width: '18px' }} />
                                                <p style={{ fontSize: '14px' }} className="mb-0 d-inline-block text-danger">Logout</p>
                                            </Link>
                                        </Dropdown.Item>
                                    </Dropdown.Menu>
                                </Dropdown>
                            </>
                            }
                        </Nav>
                    </Navbar.Collapse>
                </span>
            </Navbar>
        )
}
export default withRouter(Header)