export const EARNINGS = `
query{
  meInstructors{
    total_earning_counter
    current_balance
    sessionsCanEarning(first: 1000){
      paginatorInfo{count currentPage firstItem lastItem perPage}
      data{
        id
        date
        course_type{id name}
        rate_salary
        currency_type{id symbol}
      }
    }
  }
    InstructorGetTransfers(first: 1000){
      data{
        id
        amount
        step
        accountType
        created_at
      }
    }
}`

export const TRANSFERE =  `
mutation($amount : Int! $accountHolderName : Name! $abartn: Name! $accountNumber : Name! $accountType : AccountType! $postCode : Name! $address : Name! $city : Name! $country : String!){
  PersonTransfer(data: {
    amount : $amount
    accountHolderName : $accountHolderName
    abartn : $abartn
    accountNumber : $accountNumber
    accountType : $accountType
    postCode : $postCode
    address : $address
    city : $city
    country : $country
  }){
    StatusTransfer{title message}
    Status{code message}
    Transfer{id}
  }
}
`