import React from 'react';
import { Link } from 'react-router-dom';
// styles images and icons
import {Row, Col , Navbar, Nav } from 'react-bootstrap';
import LinearProgress from '@material-ui/core/LinearProgress';
import logo from '../../../images/logo/logo-f-sm.svg';

// language for test
import text from '../../../languages/english/translate.json';

export default function Main(props) {
    let data = text["instractor-profile"].create.main;
    return (
        <div className="">
            <div style={{ minHeight: "94.8vh" }}>
                <Row >
                    <Col xs={12} className="">
                        <div >
                            <Navbar collapseOnSelect expand="lg" bg="white" className="shadow pr-5 pl-5" variant="light">
                                <Navbar.Brand className="" href="#intro">
                                    <img src={logo} className="img-fluid" alt="logo" />
                                </Navbar.Brand>
                                <Navbar.Toggle aria-controls="responsive-navbar-nav" />
                                <Nav className="mr-auto border-left pl-3">
                                    <div className="mb-0 text-muted font-weight-normal Uppercase">
                                        <p className="mb-0 d-inline-block pr-1">{data.step.partOne} </p>
                                        <h4 className="d-inline-block mb-0 text-green ">
                                             {props.step}
                                        </h4>
                                        <p className="mb-0 d-inline-block"> {data.step.partTwo}</p>
                                    </div>
                                </Nav>
                                <Nav className="inline-block ">
                                    <Link className="text-muted font-weight-norma"
                                        to="/dashboard">
                                        {data.Skip}</Link>
                                </Nav>
                            </Navbar>
                            <div>
                                <LinearProgress style={{ height: '10px' }} value={parseInt(props.value)} color="primary" variant="determinate" />
                            </div>
                        </div>
                    </Col>

                </Row>
                {props.content}
            </div>
            <Row className="bg-light text-center pt-2 sticky-bottom ">
                <Col><small>{text.home.getInTouch.copyRight}</small></Col>
            </Row>
        </div>
    )
}
