import React, { useEffect, useState } from 'react';
import $ from 'jquery';

import {Url} from '../../../url';
import {GET_INFO , USER_SESSIONS} from '../apis/apis';
import Info from './profile-info.component'
import Sessions from './sessions.component';
import CardSkeleton from '../../loaders/card.component';

// styles
import Box from '@material-ui/core/Box';
import Skeleton from '@material-ui/lab/Skeleton';
import './index.styles.css';


export default function Profile(props) {
    const [userData, setUserData] = useState();
    const [upcomingSessions, setUpcomingSessions] = useState();
    const [pastSessions, setPastSessions] = useState();
    

    const geStudentData = () => {
        if (localStorage.QA_jwt) {
            const token = JSON.parse(localStorage.getItem('QA_jwt')).BearerTokenResponse.access_token;
            //get user data
            $.ajax({
                url: Url,
                method: 'post',
                headers: {
                    authorization: `Bearer ${token}`
                },
                data: {
                    query: GET_INFO,
                },
                dataType: 'json',
                success: res => {
                    if (res.data.meStudents) {
                        setUserData(res.data.meStudents);
                    }
                },
                cache: err => console.log(err)
            });
        }
    }
    const getUpcomingSessions = () => {
        if (localStorage.QA_jwt) {
            const token = JSON.parse(localStorage.getItem('QA_jwt')).BearerTokenResponse.access_token;
            //get user data
            $.ajax({
                url: Url,
                method: 'post',
                headers: {
                    authorization: `Bearer ${token}`
                },
                data: {
                    query: USER_SESSIONS,
                    variables : {orderBy : "ASC"}
                },
                dataType: 'json',
                success: res => {
                    if (res.data.meStudents) {
                        setUpcomingSessions(res.data.meStudents.sessionsGet.data);
                    }
                },
                cache: err => console.log(err)
            });
        }
    }
    const getPastSessions = () => {
        if (localStorage.QA_jwt) {
            const token = JSON.parse(localStorage.getItem('QA_jwt')).BearerTokenResponse.access_token;
            //get user data
            $.ajax({
                url: Url,
                method: 'post',
                headers: {
                    authorization: `Bearer ${token}`
                },
                data: {
                    query: USER_SESSIONS,
                    variables : {orderBy : "DESC"}
                },
                dataType: 'json',
                success: res => {
                    if (res.data.meStudents) {
                        setPastSessions(res.data.meStudents.sessionsGet.data);
                    }
                },
                cache: err => console.log(err)
            });
        }
    }
    useEffect(() => {
        geStudentData();
        getUpcomingSessions();
        getPastSessions();
    },[]);
    if (!userData && !upcomingSessions && !pastSessions) {
        return (
            <section className="mt-5 container">
                 <main className="row align-items-center justify-content-center">
                    <figure className="col-md-2 col-4 text-center  mb-5">
                        <Skeleton className="img-fluid" animation="wave" variant="circle" height={150} />
                    </figure>
                    <article className="col-md-10 col-8">
                        <Skeleton animation="wave" height={30} width={120}/>
                        <Skeleton animation="wave" height={20} width={180}/>
                        <Skeleton animation="wave" height={25} width={130}/>
                    </article>
                    <CardSkeleton />
                    <CardSkeleton />
                    <CardSkeleton />
                    <CardSkeleton />

                </main>
            </section>
        )
    }
    else {
        return (
            <>
                <Info getUserData={geStudentData} data={userData} />
                <Sessions 
                    getUpcomingSessions={getUpcomingSessions} 
                    getPastSessions={getPastSessions}
                    upcomingSessions={upcomingSessions} 
                    pastSessions={pastSessions}
                />
            </>
        )
    }
}