import React, { useState } from 'react';
// style
import { FormControlLabel, RadioGroup, Radio } from '@material-ui/core';

export default function FilterByGender(props) {
    return (
        <>
            <RadioGroup
                className="pr-3 pl-3"
                aria-label="type"
                name="gender"
                value={props.defaultValue}
                onChange={props.handleChange('gender')}>
                <FormControlLabel
                    className="mb-1"
                    value={false}
                    control={<Radio color="primary" />}
                    label="Male"
                    labelPlacement="male"
                    defaultValue
                />
                <FormControlLabel
                    className="mb-1"
                    value={true}
                    control={<Radio color="primary" />}
                    label="female"
                    labelPlacement="female"
                />
            </RadioGroup>
            <hr className="mt-1 " style={{ backgroundColor: '#F3F3F3', height: '15px', border: '0' }} />
        </>
    )
}