import React,{useState , useEffect } from 'react';
import { Mutation } from 'react-apollo';
import { InMemoryCache } from 'apollo-cache-inmemory';
import { createUploadLink } from 'apollo-upload-client';
import { ApolloClient } from "apollo-client";
import { ApolloProvider } from "react-apollo";
import Loader from 'react-loader-spinner';
import $ from 'jquery';

import { EDIT_PROFILE_UPLOAD , EDIT_PROFILE} from './api';
import {Url} from '../../../url';

import Container from './container.component';
import text from '../../../languages/english/translate.json';

import { Row, Col } from 'react-bootstrap';
import {
    TextField, InputLabel, FormControl, Select,
    Button, Chip
} from '@material-ui/core';

export default function Basic(props){
        const [state , setState] = useState({
            deleteIds : [],
            video : [] , 
            certificates: [],
            errors : []
        })
    useEffect(()=>{
        let userData = props.data;
        setState({...state , description : userData.description,
             yearsOfExperience : userData.years_of_experience , certificates : userData.certificates ,
             video : [userData.profile_vidoe]  , loading : false});    
    },[props])
    let pageText = text["instractor-profile"].create.workExperience;

    const apolloCache = new InMemoryCache();

    const token = (localStorage.getItem('QA_jwt')) ? JSON.parse(localStorage.getItem('QA_jwt')).BearerTokenResponse.access_token : '';

    const uploadLink = createUploadLink({
        uri: 'https://quranacademy.black-squares.com/graphql',
        headers: {
            authorization: token ? `Bearer ${token}` : ""
        }
    })
    const client = new ApolloClient({
        cache: apolloCache,
        link: uploadLink
    })
    const handleChange = props => event => {
        const value = event.target.value;
        let errors = state.errors;
        errors.certificates = '';
        errors.video = '';
        if (props === 'certificates' && event.target.files[0]) {
            // make sure that the user attache image
            let file = event.target.files[0].type.split('/').pop().toLowerCase();

            if (state.certificates.length < 5 && (file === "jpeg" || file === "jpg" ||
                file === "png" || file === "bmp" || file === "gif" || file === "svg+xml"
                || file === "pdf")) {
                setState({ ...state, [props]: [...state.certificates, event.target.files[0]] })
            }
            else {
                errors.certificates = 'Please Enter a valid files';
                setState({ ...state, errors: errors });

            }
        }
        else if (props === 'video' && event.target.files[0]) {

            let fileType = event.target.files[0].type.split('/').pop().toLowerCase();
            let fileSize = event.target.files[0].size / 1024 / 1024;
            if(fileType === "m4v" || fileType === "avi" ||
                fileType === "mpg" || fileType === "mp4" || fileType === "webm") {
                   
                    setState({ ...state, [props]: [event.target.files[0]]})
            } 
            else if(fileSize > 120) {

                errors.video = 'The Video Upload must be 50MB maximum';
                setState({ ...state, errors: errors });
                
            }
            else {
                errors.video = 'Please Enter a valid video.';
                setState({ ...state, errors: errors });

            }

        }
        else if (event.target.value) {
            setState({ ...state, [props]: event.target.value });
        }
        switch (props) {
            case 'description':
                errors.description =
                    value.length <= 5000
                        ? ''
                        : text.validation.description;
                break;
            default:
                break;
        }

    }
    const validateForm = (errors) => {
        let valid = true;
        Object.values(errors).forEach(
            // if we have an error string set valid to false
            (val) => val.length > 0 && (valid = false)
        );
        return valid;
    }
 
    const fileRequired = (name, file) => {
        let errors = state.errors;
        errors[name] = file === undefined || file.length === 0 ? text.validation.required : ''
        setState({ ...state, errors: errors });
    }
    const handleDelete = (chipToDelete , type , deleteIds = [] ) => (e) => {
        switch (type) {
            case 'certificates':
                let newCertificates = state.certificates.filter(file =>
                    file !== chipToDelete
                    )
                    setState({...state , certificates : newCertificates ,  deleteIds : [...state.deleteIds , deleteIds]})
                break;
            case 'video':
                let newVideo = state.video.filter(file =>
                    file !== chipToDelete
                    )
                    setState({...state , video : newVideo })
                break;
            default:
                break;
        }
    }
    const handleSaveChangesWithoutUpload = () => {
        setState({...state , loading : true });
        $.ajax({
            url: Url,
            method: 'post',
            headers: {
                authorization: `Bearer ${token}`
            },
            data: {
                query: EDIT_PROFILE,
                variables: JSON.stringify({
                    description : state.description,
                    years_of_experience : state.yearsOfExperience,
                    delete_certificates_ids : state.deleteIds
                })
            },
             dataType : "json",
            success: res => {
                if (res.data.PersonEdit.Status.check) {
                    setState({ ...state, loading: false , deleteIds : []});
                    props.refetch();
                }
            },
            cache: err => console.log(err)
        });
    }
    return(
        <Container 
            title={pageText.title}
            content ={
                <ApolloProvider client={client}>
                     <div className=" pb-3 text-left">
                        <Mutation mutation={EDIT_PROFILE_UPLOAD}>
                            {(upload, { d, loading }) => {
                                return (
                                    <form onSubmit={e=>{
                                        e.preventDefault();
                                        var newCertificates = [] , oldCertificates = [];
                                        if(state.certificates){
                                            newCertificates = state.certificates.filter(ele => ! ele.id)
                                            oldCertificates = state.certificates.filter(ele =>  ele.id)
                                        }
                                        if (validateForm(state.errors)) {
                                            if(! state.video[0]?.id || newCertificates.length > 0){
                                            upload({
                                                variables : {
                                                    description : state.description,
                                                    profile_vidoe  : state.video[0]?.type && state.video[0],
                                                    certificates :  newCertificates && newCertificates,
                                                    years_of_experience : state.yearsOfExperience,
                                                    delete_certificates_ids : state.deleteIds
                                                }
                                            }).then(res=>{
                                                if (res.data.PersonEdit.Status.check) 
                                                {
                                                    props.refetch();
                                                    setState({...state , deleteIds : []})
                                                }}).catch(err=>console.log(err))
                                            }
                                            else{
                                                handleSaveChangesWithoutUpload();
                                            }
                                        }
                          
                                    }}>
                                        {/* brief */}
                                        <Row className="m-0 mb-2 w-100">
                                            <Col className="p-0">
                                                <InputLabel required>
                                                    {pageText.briefInput.name}
                                                </InputLabel>
                                                <TextField
                                                    required
                                                    multiline
                                                    helperText={pageText.briefInput.validationText}
                                                    rows={4}
                                                    fullWidth
                                                    size="small"
                                                    variant="outlined"
                                                    defaultValue={state.description && state.description}
                                                    onChange={handleChange('description')}
                                                />
                                                <div className="text-danger text-left"><p className="mb-1">{state.errors.description}</p></div>
                                            </Col>
                                        </Row>
                                        {/* Years of experience */}
                                        <Row className="m-0 mb-0 w-100">
                                            <Col className="pr-0 pl-1">
                                                <InputLabel required>
                                                    {pageText.yearsOfExperience}
                                                </InputLabel>
                                                <FormControl
                                                    required
                                                    className="mb-3"
                                                    variant="outlined" fullWidth size="small">
                                                    <Select
                                                        native
                                                        name={pageText.yearsOfExperience}
                                                        inputProps={{ value: state.yearsOfExperience && state.yearsOfExperience }}
                                                        onChange={handleChange('yearsOfExperience')}
                                                    >
                                                        <option value=""></option>
                                                        <option value="1">1</option>
                                                        <option value="2">2</option>
                                                        <option value="3">3</option>
                                                        <option value="4">4</option>
                                                        <option value="5">5</option>
                                                        <option value="6">6</option>
                                                        <option value="7">7</option>
                                                        <option value="8">8</option>
                                                        <option value="9">9</option>
                                                        <option value="10">10</option>
                                                    </Select>
                                                </FormControl>
                                            </Col>
                                        </Row>
                                        {/* Uploads*/}
                                        <Row className="m-0 mb-3">
                                            <Col md={12} className="p-0  mb-2">
                                                <p className="mb-1">{pageText.uploadText}</p>
                                                <p className="text-center">
                                                    {state.certificates?.map(file =>
                                                        <Chip   onDelete={handleDelete(file , 'certificates', file.id && file.id )} deleteIcon className="mt-1 bg-danger-outline mr-1" label={file.file? file.file.name : file.name} />)}
                                                </p>
                                                <label
                                                    className="mb-0 pt-1 pb-1 pr-3 pl-3 btn-green"
                                                    style={{ cursor: 'pointer' }}
                                                    htmlFor="uploadText">
                                                    {pageText.browseBtn}
                                                </label>
                                                <input
                                                    onChange={handleChange('certificates')}
                                                    type="file"
                                                    accept='*'
                                                    className="d-none"
                                                    id="uploadText"
                                                />
                                                <div className="text-danger text-left"><p className="mb-1">{state.errors.certificates}</p></div>
                                            </Col>
                                            <Col md={12} className="p-0">
                                                <p className="mb-1">{pageText.uploadVideo}</p>
                                                <p className="text-center">
                                                    {state.video?.map(file =>
                                                        <Chip   className="mt-1 bg-danger-outline mr-1" label={file?.name} />)}
                                                </p>
                                                <div className="text-danger text-left"><p className="mb-1">{state.errors.video}</p></div>
                                                <div>
                                                    <label
                                                        className="mb-0 pt-1 pb-1 pr-3 pl-3 btn-green"
                                                        style={{ cursor: 'pointer' }}
                                                        htmlFor="uploadVideo">
                                                        {pageText.browseBtn}
                                                    </label>
                                                </div>
                                                <input
                                                    onChange={handleChange('video')}
                                                    type="file"
                                                    accept='*'
                                                    className="d-none"
                                                    id="uploadVideo"
                                                />
                                            </Col>
                                        </Row>
                                        {/* form errors */}
                                        <div className="text-danger text-left"><p className="mb-1">{state.errors.form}</p></div>
                                        {/* save changes button */}
                                        <Row className="m-0 align-items-center w-100 border-top pt-3">

                                            <Col className="text-right p-0">
                                                <Button
                                                    type="submit"
                                                    className="btn-green pl-4 pr-4">
                                                    {pageText.saveChanges}
                                                </Button>
                                            </Col>
                                        </Row>
                                        {(loading || state.loading) && 
                                            <Loader
                                                className="loading"
                                                type="TailSpin"
                                                color="white"
                                                height={50}
                                                width={50}
                                            />
                                        }
                                    </form>)
                            }} 
                   
                      </Mutation> 
                </div>
                </ApolloProvider>    
            }
        />
    )
}