import React, { useState, useEffect } from 'react';
import $ from 'jquery';

import { url, DURATION } from '../apis/apis';
import Filter from '../../loaders/filter.component';
// style
import { FormControlLabel, RadioGroup, Radio } from '@material-ui/core';


export default function FilterByCourseType(props) {
    const [state, setState] = useState({
        duration: "",
        loading: false
    });
    const getData = () => {
        setState({ ...state, loading: true })

        $.ajax({
            url: url,
            method: 'post',
            data: {
                query: DURATION
            },
            dataType: 'json',
            success: res => {
                if (res) {
                    console.log(res);
                    setState({ data: res.data.rateMinutesTypes, loading: false })
                }
            },
            cache: err => { console.log(err) }
        });
    }
    useEffect(() => {
        getData();
    }, [])
    return (
        <>
            <RadioGroup
                className="pr-3 pl-3"
                aria-label="type"
                name="duration"
                value={props.defaultValue}
                onChange={props.handleChange('duration')}>
                <p style={{ fontSize: '14px' }} className="mb-1 bold">Filter by Duration</p>
                {(state.loading) ? 
                    <Filter /> :
                    state.data?.map(course =>
                        <FormControlLabel
                            className="mb-1"
                            value={course.id}
                            control={<Radio color="primary" />}
                            label={course.name}
                            labelPlacement={course.name}
                            defaultValue
                        />
                    )
                }
            </RadioGroup>
            <hr className="mt-1 mb-1" style={{ backgroundColor: '#F3F3F3', height: '15px', border: '0' }} />

        </>
    )
}