import React from 'react';
import { Link } from 'react-router-dom';
// styles images and icons 
import { Container , Row , Col , Button} from 'react-bootstrap';
import logo from '../../images/logo/logo.svg';
import slide from '../../images/home/illustration.svg'
import googlePlay from '../../images/home/google.png';
import apple from '../../images/home/apple.png';

// language for test
import text from '../../languages/english/translate.json';

export default function intro() {
    let data = text?.home.intro;
    return (
        <div  className="max-h-100-responsive pb-5 pt-5" id="intro">
            <Container className="pt-5">
                <Row className="align-items-center m-0 mt-5">
                    <Col xs={12} className="order-md-1 order-1">
                        <div className="">
                            <img src={logo} className="img-fluid" alt="Quraan Acedemy Logo"/>
                        </div>
                    </Col>
                    <Col md={6} sm={10} className="order-md-2 order-3">
                        <div className="mb-2">
                            <h1 className="text-dark"><span className="text-green">{data.title.partOne} </span> {data.title.partTwo}</h1>
                            <p className="pt-3">
                                {data.description}
                            </p>
                        </div>
                        <div className="mb-2 text-center-responsive">
                            <div className="mb-3">
                                <b className="text-dark"> {data.otherVersion} </b>
                            </div>
                            <div className="d-inline-block m-1">
                                <button className="bg-transparent border-0 p-0"><img src={googlePlay} alt="google play" className="img-fluid"/></button>
                            </div>
                            <div className="d-inline-block m-1">
                                <button className="bg-transparent border-0 p-0"><img src={apple} alt="apple play" className="img-fluid"/></button>
                            </div>
                        </div>
                    </Col>
                    <Col  md={6} sm={2} className="order-md-3 order-2">
                        <div className="w-100 mb-3">
                            <img src={slide} className="img-fluid " alt="slide"/>
                        </div>
                    </Col>
                </Row>
            </Container>
        </div>
    )
}