import React , {useEffect , useState} from 'react';
import $ from 'jquery';

import ProfileStrength from '../../profile-strength/profile-strength.component';
import Basic from './basic-info.component';
import WorkExperienc from './work-exp.component';
import Other from './other.component';
import {GET_INFO} from './api';
import {Url }  from '../../../url'; 
import EditProfileLoader from '../../loaders/edit-profile.component';
import MakeProfilePublic from '../make-profile-public';

import {Row , Col} from 'react-bootstrap';
import {Tab, Nav} from 'react-bootstrap';

import text from '../../../languages/english/translate.json';


export default function EditProfile(){
    const [userData, setUserData] = useState()

    let pageText = text.instractorHome.dashboard;
    
    const geStudentData = () => {
        if (localStorage.QA_jwt) {
            const token = JSON.parse(localStorage.getItem('QA_jwt')).BearerTokenResponse.access_token;
            //get user data
            $.ajax({
                url: Url,
                method: 'post',
                headers: {
                    authorization: `Bearer ${token}`
                },
                data: {
                    query: GET_INFO
                },
                dataType: 'json',
                success: res => {
                    if (res.data.meInstructors) {
                        setUserData(res.data.meInstructors)
                    }
                },
                cache: err => console.log(err)
            });
        }
    }
    useEffect(() => {
        geStudentData();
    },[]);
    if(!userData){
        return(<EditProfileLoader />)
    }
    else{
        return(
            <main className="mt-5 mb-4 container">
                <Row>
                    <Col md={8}>
                        <Tab.Container  className="" defaultActiveKey="first">
                                <Nav variant="pills" className="text-grey custom-nav">
                                    <Nav.Item>
                                        <Nav.Link eventKey="first" className="mr-3 bg-transparent text-center p-0" style={{ outline: 'none' }}>
                                            <h6 style={{fontSize : '16px'}} className="mb-2 font-weight-normal">{text['instractor-profile'].create.basicInfo.title}</h6>
                                        </Nav.Link>
                                    </Nav.Item>
                                    <Nav.Item >
                                        <Nav.Link eventKey="second" className="mr-3 bg-transparent text-center p-0" style={{ outline: 'none' }}>
                                            <h6 style={{fontSize : '16px'}} className="mb-2 font-weight-normal">{text['instractor-profile'].create.workExperience.title}</h6>
                                        </Nav.Link>
                                    </Nav.Item>
                                    <Nav.Item >
                                        <Nav.Link eventKey="third" className=" bg-transparent text-center p-0" style={{ outline: 'none' }}>
                                            <h6 style={{fontSize : '16px'}} className="mb-2 font-weight-normal">{text['instractor-profile'].create.requirements.smallTitle}</h6>
                                        </Nav.Link>
                                    </Nav.Item>
                                </Nav>
                            <Tab.Content className="text-grey container p-0 mt-2">
                                <Tab.Pane eventKey="first" className="">
                                    <Basic data={userData} refetch={geStudentData}/>
                                </Tab.Pane>
                                <Tab.Pane eventKey="second" className="">
                                    <WorkExperienc data={userData} refetch={geStudentData}/>
                                </Tab.Pane>
                                <Tab.Pane eventKey="third" className="">
                                    <Other data={userData} refetch={geStudentData}/>
                                </Tab.Pane>
                            </Tab.Content>
                        </Tab.Container>
                    </Col>
                    <Col md={4}>
                        <ProfileStrength
                            title={pageText.improveProfile}
                            description = {pageText.profileStrengthDesc}
                            percentage="63"
                            complete={pageText.completeProfile}
                            strength={pageText.profileStrength} 
                        />
                        <MakeProfilePublic privacy={userData.is_profile_public}/>
                    </Col>
                </Row>
            </main>
        )
    }
}