import React,{useState , useEffect} from 'react';
import {Link} from 'react-router-dom';
import $ from 'jquery';

import Calendar from './calendar';
import {Url} from '../../../../url';
import {COURSE_TYPE_AND_DURATION} from '../../apis/apis';

import Text from '../../../../languages/english/translate.json';
import {Breadcrumb} from 'react-bootstrap';

export default function ReserveSession(props) {
    console.log(props);
    let pageText = Text.student.instractorProfile.reserveSession;
    const [state , setState] = useState();
    return(
    <>
        <div className='bg-lightGreen'>
            <nav aria-label="breadcrumb" className='container'>
                    <ol className="breadcrumb bg-transparent">
                            <li className="breadcrumb-item"><Link to='/instractorProfile'>Instructor profile </Link></li>
                            <li className="breadcrumb-item active" aria-current="page">Reserve session</li>
                    
                    </ol>
            </nav>
        </div>
        <section className="container">
            <header className="col-12 mt-3">
                <h6 >{pageText.title}</h6>
                <p className="text-grey mb-0">{pageText.description}</p>
            </header>
            <main >
                <Calendar data={state}/>
            </main>
        </section>
    </>
    )
}