import React from 'react';

import text from '../../../languages/english/translate.json';

import Container from './container.component';

import icon from '../../../images/icons/attachment.svg';

export default function Certificates(props) {

    let certificates = props.data.certificates;
    let pageText = text['instractor-profile'].view.certificates;

    if(certificates.length > 0 )
    {
        return(
            <Container>
                <article>
                    <h5 className="text-dark">{pageText.title}</h5>
                    {/* <p className="text-dark">- Bachelor's degree, faculty of engineering, Ain shams university.</p> */}
                    <img height="20px" src={icon} alt="icon"/>
                    {certificates.map(ele=>
                        <a  href={ele.file.url} className="d-inline-block" >Media (1)</a>
                    )}
                </article>
            </Container>
        )
    }
    else{
        return(<div></div>)
    }
}