import React , {useState , useEffect} from 'react';
import { Mutation } from 'react-apollo';
import { InMemoryCache } from 'apollo-cache-inmemory';
import { createUploadLink  } from 'apollo-upload-client';
import { createHttpLink } from 'apollo-link-http';
import { ApolloClient } from "apollo-client";
import { ApolloProvider } from "react-apollo";
import Loader from 'react-loader-spinner';
import $ from 'jquery';

import Container from './container.component';
import text from '../../../languages/english/translate.json';
import {checkName  ,  checkImage , isFutureDate, checkValidateForm} from '../../../validations/validation';
import {EDIT_PROFILE_UPLOAD , EDIT_PROFILE} from './api';
import {Url }  from '../../../url';

import {Row , Col} from 'react-bootstrap';
import {TextField, InputLabel, FormControl, Select,
    Button
} from '@material-ui/core';
import pp from '../../../images/intstractor-profile/pp.png'


export default function Basic(props) {
    const [state , setState] = useState({
        loading : false ,
        errors : []
    });
    let pageText = text['instractor-profile'].create.basicInfo;
    let dataForm = text.registration

    useEffect(()=>{
        let userData = props.data;

        setState({...state , profile : userData.profile_image?.url ,
             first_name : userData.first_name , last_name : userData.last_name ,
             email : userData.email , date_of_birth : userData.date_of_birth , gender : userData.gender});    
    },[props])
    
    const handleChange = props => event => {
        const value = event.target.value;
        let errors = state.errors;
        let gender = state.gender;

        if (props === 'profile' && event.target.files[0]) {
            // make sure that the user attache image
            if (!checkImage(event.target.files[0])) {
                setState({...state, [props]: event.target.files[0] })
            }
            else {
                setState({ ...state, [props]: '' })
            }
        }
        else if (value || value === '') {
            setState({ ...state, [props]: event.target.value.trim() });
        }

        errors.form = ''
        errors.profile = ''
        switch (props) {
            case 'first_name':
                errors.first_name = checkName(value)
                break;
            case 'last_name':
                errors.last_name = checkName(value)
                break;
            case 'profile':
                errors.profile = checkImage(event.target.files[0])
                break;
            case 'date_of_birth':
                errors.date = isFutureDate(value)
                break;
            case 'gender':
                    if(value === "true") gender = true;
                    else if(value === "false") gender = false;
                    else gender = "";

                    setState({ ...state, gender: gender })
                break;
            default:
                break;
        }
    }
    const fileRequired = () =>{
        let file = state.profile;
        let errors = state.errors;
        errors.profile = file === undefined ? text.validation.required : ''
        setState({...state , errors : errors});
    }
    const apolloCache = new InMemoryCache();

    const token = (localStorage.getItem('QA_jwt')) ? JSON.parse(localStorage.getItem('QA_jwt')).BearerTokenResponse.access_token : '';

    const uploadLink = createUploadLink({
        uri: 'https://quranacademy.black-squares.com/graphql',
        headers: {
            authorization: token ? `Bearer ${token}` : ""
        }
    })
    const clientUpload = new ApolloClient({
        cache: apolloCache,
        link: uploadLink
    });
    const handleSaveChangesWithoutUpload = () => {
        setState({...state , loading : true });
        $.ajax({
            url: Url,
            method: 'post',
            headers: {
                authorization: `Bearer ${token}`
            },
            data: {
                query: EDIT_PROFILE,
                variables: JSON.stringify({
                    first_name : state.first_name,
                    last_name : state.last_name ,
                    date_of_birth : state.date_of_birth,
                     gender : state.gender ,
                })
            },
             dataType : "json",
            success: res => {
                console.log(res);
                if (res.data.PersonEdit.Status.check) {
                    setState({ ...state, loading: false });
                    props.refetch();
                }
            },
            cache: err => console.log(err)
        });
    }
    return(
        <Container 
            title={pageText.title}
            content ={
                <div>
                <ApolloProvider client={clientUpload}>
                    <Row className="align-items-center">
                        <Col md={3} className="">
                            <div className="text-center">
                                <img
                                    src={state.profile && state.profile.type ? URL.createObjectURL(state.profile) : state.profile || pp}
                                    alt="profile" className="rounded-circle"  width="150px" height="150px"/>
                            </div>
                            <div className="text-danger text-center"><p className="mb-1">{state.errors.profile}</p></div>
                        </Col>
                        <Col md={9}>
                            <div>
                                <h5 className="mb-0 text-dark">{pageText.profileTitle}</h5>
                                <p className="text-muted">{pageText.profileDesc}</p>
                            </div>
                            <div className="mt-4">
                                <label
                                    required
                                    className="mb-0 pt-1 pb-1 pr-3 pl-3 btn-green"
                                    style={{ cursor: 'pointer' }}
                                    for="uploadProfile">
                                    {pageText.uploadBtn}
                                </label>
                                <input
                                    required
                                    onChange={handleChange('profile')}
                                    type="file"
                                    accept='image/*'
                                    className="d-none"
                                    id="uploadProfile"
                                />
                            </div>
                        </Col>
                    </Row>
                    <div className="mt-4 pb-3 text-lef">
                    <Mutation mutation={EDIT_PROFILE_UPLOAD}>
                        {(upload, { d, loading }) => {
                            return(
                                <form onSubmit={e=>{
                                        e.preventDefault();
                                        fileRequired();
                                        if (checkValidateForm(state.errors) && state.profile) {
                                                if(state.profile?.type){
                                                    upload({
                                                        variables : {
                                                            profile_image: state.profile , 
                                                            first_name : state.first_name,
                                                            last_name : state.last_name ,
                                                            gender : state.gender ,
                                                            date_of_birth : state.date_of_birth
                                                        }
                                                    }).then(res=>{
                                                        console.log(res);
                                                        if (res.data.PersonEdit.Status.check) 
                                                        {
                                                            props.refetch();
                                                        }})
                                                    .catch(err=>console.log(err));
                                                }
                                                else{
                                                    handleSaveChangesWithoutUpload();
                                                }
                                        }
                                    }}>
                                    {/* name */}
                                    <Row className="m-0 mb-2 w-100">
                                        {/* first name */}
                                        <Col className="pr-1 pl-0 t">
                                            <InputLabel required>
                                                {dataForm.firstName}
                                            </InputLabel>
                                            <TextField
                                                fullWidth
                                                required
                                                className=""
                                                size="small"
                                                variant="outlined"
                                                value={state.first_name && state.first_name}
                                                onChange={handleChange('first_name')}
                                            />
                                            <div className="text-danger"><p className="mb-1">{state.errors.first_name}</p></div>
                                        </Col>
                                        {/* last name */}
                                        <Col className="pr-0 pl-1 ">
                                            <InputLabel required>
                                                {dataForm.lastName}
                                            </InputLabel>
                                            <TextField
                                                fullWidth
                                                className=""
                                                size="small"
                                                variant="outlined"
                                                value={state.last_name && state.last_name}
                                                onChange={handleChange('last_name')}
                                            />
                                            <div className="text-danger"><p className="mb-1">{state.errors.last_name}</p></div>
                                        </Col>
                                    </Row>
                                    {/* email */}
                                    <Row className="m-0 mb-2">
                                        <Col className="pr-0 pl-0">
                                            <InputLabel required>
                                                {dataForm.email}
                                            </InputLabel>
                                            <TextField
                                                size="small"
                                                fullWidth
                                                type="email"
                                                disabled
                                                inputProps={{
                                                    value: state.email
                                                }}
                                                variant="outlined"
                                            />
                                            <div className="text-danger"><p className="mb-1">{state.errors.email}</p></div>
                                        </Col>
                                    </Row>
                                    {/* gender and data of birth */}
                                    <Row className="m-0 mb-2 w-100">
                                        {/* data of birth */}
                                        <Col className="pr-1 pl-0">
                                            <div className="">
                                                <InputLabel 
                                                    required
                                                    className="">{dataForm.dateOfBirth}</InputLabel>
                                                <TextField
                                                    required
                                                    fullWidth
                                                    size="small"
                                                    type="date"
                                                    inputProps={{value : state.date_of_birth && state.date_of_birth}}
                                                    variant="outlined"
                                                    onChange={handleChange('date_of_birth')}
                                                />
                                            </div>
                                            <div className="text-danger"><p className="mb-1">{state.errors.date}</p></div>
                                        </Col>
                                        {/* gender */}
                                        <Col className="pr-0 pl-1">
                                            <InputLabel >
                                                {dataForm.gender}
                                            </InputLabel>
                                            <FormControl
                                                className="mb-3"
                                                variant="outlined" fullWidth size="small">
                                                <Select
                                                    native
                                                    name={dataForm.gender}
                                                    onChange={handleChange('gender')}
                                                    value={state.gender}
                                                    inputProps={{
                                                        name: dataForm.gender,
                                                        id: 'outlined-age-native-simple',
                                                    }}
                                                >
                                                    <option value=""></option>
                                                    <option value={true}>Male</option>
                                                    <option value={false}>Female</option>
                                                </Select>
                                            </FormControl>
                                        </Col>
                                    </Row>
                                    {/* form errors */}
                                    <div className="text-danger"><p className="mb-1">{state.errors.form}</p></div>
                                    {/* continue button */}
                                    <div className="m-0 align-items-center w-100 border-top pt-3">
                                <div className="text-right ">
                                    <Button
                                        type="submit"
                                        className="btn-green pl-3 pr-3">
                                        {pageText.saveChanges}
                                    </Button>
                                </div>
                            </div>
                                    {(loading || state.loading) &&
                                            <Loader
                                                className="loading"
                                                visible={state.loading}
                                                type="TailSpin"
                                                color="white"
                                                height={50}
                                                width={50}
                                            />
                                    }
                                </form>
                             )}} 
                     </Mutation> 
                    </div>
                 </ApolloProvider> 
                </div>
            }
        />
    )
}