import React,{useEffect , useState} from 'react';
import { withRouter } from 'react-router';
import {Link} from 'react-router-dom';
import $ from 'jquery';

import {GET_INFO} from '../edit-profile/api';
import {Url} from '../../../url';

// styles and images
import { Navbar, Nav , Dropdown} from 'react-bootstrap';
import {Avatar} from '@material-ui/core'
import logoWeb from '../../../images/logo/logo-1.svg';
import EditIcon from '@material-ui/icons/Edit';
import SettingsIcon from '@material-ui/icons/Settings';
import HelpIcon from '@material-ui/icons/Help';
import LanguageIcon from '@material-ui/icons/Language';
import ExitToAppIcon from '@material-ui/icons/ExitToApp';
import Skeleton from '@material-ui/lab/Skeleton';

// language for test
import text from '../../../languages/english/translate.json';

function Header(props) {
    const [userData, setUserData] = useState();

    let data = text?.home.header;
    useEffect(()=>{
        geStudentData();
    
    },[props]);

    const geStudentData = () => {
        if (localStorage.QA_jwt) {
            const token = JSON.parse(localStorage.getItem('QA_jwt')).BearerTokenResponse.access_token;
            //get user data
            $.ajax({
                url: Url,
                method: 'post',
                headers: {
                    authorization: `Bearer ${token}`
                },
                data: {
                    query: GET_INFO
                },
                dataType: 'json',
                success: res => {
                    if (res.data.meInstructors) {
                        setUserData(res.data.meInstructors)
                    }
                },
                cache: err => console.log(err)
            });
        }
    }
    if(!userData){
        return(<Skeleton variant="rect"  width="100%" height={77}/>)
    }   
    else{
        return (
            <Navbar collapseOnSelect  expand="lg" bg="white" className="shadow " variant="light">
                <span  className="pr-0 pl-0 container">
                    <Link className="navbar-brand pr-2 d-mob" to="/dashboard">
                        <img src={logoWeb} className="img-fluid" alt="logo" />
                    </Link>
                    <Link className="navbar-brand pr-2 d-sm-mob " to="/dashboard">
                        <img src={logoWeb} className="img-fluid" alt="logo" />
                    </Link>
                    <Link className="navbar-brand d-web pr-5 " to="/dashboard">
                        <img src={logoWeb} className="img-fluid" alt="logo" />
                    </Link>
                    <Nav.Link className="pr-1 d-all-mob">
                                    <Avatar src={userData.profile_image? userData.profile_image.url : ''} style={{width: "45px", height: "45px"}} alt="profile"  />
                    </Nav.Link>
                    <Navbar.Toggle aria-controls="responsive-navbar-nav" />
                        <Navbar.Collapse id="responsive-navbar-nav">
                            <Nav   className=" mr-auto ">
                                <div className="d-all-mob">
                                    <p className="mb-0 text-capitalize bold" style={{fontSize : '16px' , color :'#333333'}}>{userData.first_name}</p>
                                    <p className="mb-1 text-grey" style={{fontSize : '15px'}}>{userData.email}</p>
                                </div>
                                <Nav.Link className="d-all-mob" eventKey="1"  >
                                    <button 
                                        style={{fontSize : '14px'}}  
                                        className=" text-center w-100 btn-green-outline pt-1 pb-1 text-green"
                                        onClick={e=>{
                                            props.history.push({
                                                pathname : '/viewProfile',
                                                state : userData
                                            })
                                        }}
                                    >View Profile</button>
                                </Nav.Link>
                                <Link eventKey="2"  to="/dashboard" className="pr-3 pl-3 nav-link">
                                    dashboard
                                </Link>
                                <Link eventKey="3"  to="/earnings" className="pr-3 pl-3 nav-link">
                                    Earnings
                                </Link>
                                <Link eventKey="4" to="/sessions" className="pr-3 pl-3 nav-link">
                                    Sessions
                                </Link>
                                <Link eventKey="5"  to="/questions" className="pr-3 pl-3 nav-link">
                                    Questions
                                </Link>
                                <Link eventKey="6"  to={{pathname : '/notifications' , state : userData.notifications}} className="pr-3 pl-3 nav-link">
                                    Notifications
                                </Link>
                                <Link to='/editProfile' className="d-all-mob pr-3 pl-3 nav-link" eventKey="7"  >
                                    {/* <EditIcon className="mr-3" style={{width : '18px'}}/> */}
                                    <p className="mb-0 d-inline-block" >Edit Profile</p>
                                </Link>
                                <Link className="d-all-mob pr-3 pl-3 nav-link" eventKey="8">
                                    {/* <SettingsIcon className="mr-3" style={{width : '18px'}}/> */}
                                    <p  className="mb-0 d-inline-block">Account Settings</p>
                                </Link>
                                <Link  className="d-all-mob pr-3 pl-3 nav-link" eventKey="9">
                                    {/* <HelpIcon className="mr-3" style={{width : '18px'}}/> */}
                                    <p style={{fontSize : '14px'}} className="mb-0 d-inline-block">Help center</p>
                                </Link>
                                <Link  className="d-all-mob pr-3 pl-3 nav-link" eventKey="10">
                                    {/* <LanguageIcon className=" mr-3" style={{width : '18px'}}/> */}
                                    <p style={{fontSize : '14px'}} className="mb-0 d-inline-block">Language</p>
                                </Link>
                                <Link  className="d-all-mob pr-3 pl-3 nav-link"
                                    to="/"
                                    eventKey="11"
                                    onClick={e=>{
                                        localStorage.clear();
                                    }}>
                                        {/* <ExitToAppIcon className="text-danger mr-3" style={{width : '18px'}}/> */}
                                        <p style={{fontSize : '14px'}} className="mb-0 d-inline-block text-danger">Logout</p>
                                </Link>
                            </Nav>
                            <Nav className="align-items-end ">
                                <Nav.Link className="pr-1 d-web">
                                    <Avatar src={userData.profile_image? userData.profile_image.url : ''} style={{width: "45px", height: "45px"}} alt="profile"  />
                                </Nav.Link>
                                <Dropdown className="d-web">
                                    <Dropdown.Toggle className="pb-0  nav-link"  variant="" id="dropdown-basic"> </Dropdown.Toggle>
                                    <Dropdown.Menu 
                                        className="dropdown-shadow border-0 pt-0 pb-0" 
                                        style={{left : '-135px' , top :'42px'}}>
                                        <div style={{padding : '.25rem 1.5rem'}}>
                                        <p className="mb-0 text-capitalize bold" style={{fontSize : '16px' , color :'#333333'}}>{userData.first_name}</p>
                                        <p className="mb-1 text-grey" style={{fontSize : '15px'}}>{userData.email}</p>
                                    </div>
                                        <Dropdown.Item  eventKey="1">
                                        <button 
                                            style={{fontSize : '14px'}}  
                                            className=" text-center w-100 btn-green-outline pt-1 pb-1 text-green"
                                            onClick={e=>{
                                                props.history.push({
                                                    pathname : '/viewProfile',
                                                    state : userData
                                                })
                                            }}
                                        >View Profile</button>
                                    </Dropdown.Item>
                                        <Dropdown.Divider className="mt-2 mb-2" style={{borderTop : '11px solid #F2F2F2'}}/>
                                        <Dropdown.Item  eventKey="2">
                                            <Link to='/editProfile' >
                                                <EditIcon className="mr-3" style={{width : '18px'}}/>
                                                <p className="mb-0 d-inline-block"  style={{fontSize : '14px'}}>Edit Profile</p>
                                            </Link>
                                        </Dropdown.Item>
                                        <Dropdown.Item eventKey="3" >
                                            <Link>
                                                <SettingsIcon className="mr-3" style={{width : '18px'}}/>
                                                <p style={{fontSize : '14px'}} className="mb-0 d-inline-block">Account Settings</p>
                                            </Link>
                                        </Dropdown.Item>
                                        <Dropdown.Item eventKey="4" >
                                            <Link>
                                                <HelpIcon className="mr-3" style={{width : '18px'}}/>
                                                <p style={{fontSize : '14px'}} className="mb-0 d-inline-block">Help center</p>
                                            </Link>
                                        </Dropdown.Item>
                                        <Dropdown.Item eventKey="5">
                                            <Link>
                                                <LanguageIcon className=" mr-3" style={{width : '18px'}}/>
                                                <p style={{fontSize : '14px'}} className="mb-0 d-inline-block">Language</p>
                                            </Link>
                                        </Dropdown.Item>
                                        <Dropdown.Divider className="mt-2 mb-2" style={{borderTop : '11px solid #F2F2F2'}}/>
                                        <Dropdown.Item eventKey="6" >
                                            <Link  
                                            to="/"
                                            onClick={e=>{
                                                localStorage.clear();
                                            }}>
                                                    <ExitToAppIcon className="text-danger mr-3" style={{width : '18px'}}/>
                                                    <p style={{fontSize : '14px'}} className="mb-0 d-inline-block text-danger">Logout</p>
                                            </Link>
                                    </Dropdown.Item>
                                    </Dropdown.Menu>
                                </Dropdown>
                            </Nav>   
                            
                    </Navbar.Collapse>
                </span>
            </Navbar>
        )
   }
}
export default withRouter(Header)