import React from 'react';
import {Link } from 'react-router-dom';

import profilePlaceHolder from '../../images/intstractor-profile/pp.png';
import {Row , Col} from 'react-bootstrap';

export default function listItem(props) {
    let data = props.data;
    let to = '';
    if(parseInt(data.kind_number) === 1 || parseInt(data.kind_number) === 3 ||
        parseInt(data.kind_number) === 4 || parseInt(data.kind_number) === 5 ||
        parseInt(data.kind_number) === 7 ){
            to='/sessions'
    }
    else if(parseInt(data.kind_number) === 8 || parseInt(data.kind_number) === 9){
        to = '/dashboard'
    }
    else if(parseInt(data.kind_number) === 10 || parseInt(data.kind_number) === 11 
    || parseInt(data.kind_number) === 13){
        to = '/profile'
    }
    console.log(data)
    console.log(data.instructor )
    let profile = data.instructor ? (data.instructor.profile_image? data.instructor.profile_image.url :  profilePlaceHolder) : (data.student?.profile_image? data.student.profile_image.url :  profilePlaceHolder)
    if(data.message) 
    { return(
            <Link className="w-100"  to={to ? to : null} style={{cursor : to ? 'pointer' : 'default'}}> 
                <Row className="border-list mb-2 text-center-responsive">
                    <Col md={10}>
                        <img width="37px" height="37px" className="rounded-circle " src={profile && profile} alt="profile placeholder"/>
                        <p className="d-inline-block ml-2 ">{data.message}</p>
                    </Col>
                    <Col md={2}><p className="text-grey ">{data.created_at}</p></Col>
                </Row>
            </Link> 
        
    )}
    else{
        return null;
    }
    
}