import React from 'react';


import text from '../../../languages/english/translate.json';

import Container from './container.component';

export default function Summary(props) {
let pageText = text['instractor-profile'].view.summary;
    let userData = props.data
    return(
        <Container>
            <article>
                <h5 className="text-dark">{pageText.title}</h5>
                <p className="text-dark">{userData.description}</p>
            </article>
            {userData.profile_vidoe &&
                <div>
                    <h5 className="text-dark">{pageText.videoTitle}</h5>
                    <iframe 
                        width="100%" 
                        height="395px"
                        src={userData.profile_vidoe.url} 
                        title="Your Profile Video" 
                        frameBorder="0" 
                        allow="accelerometer;  clipboard-write; encrypted-media; gyroscope; picture-in-picture" 
                        allowFullScreen
                    ></iframe>
                </div>
            }
        </Container>
    )
}