import React , {useState} from 'react';
import { Link } from 'react-router-dom';
import { withRouter } from 'react-router';
import $ from 'jquery';
import Loader from 'react-loader-spinner';


import { Form, Button } from 'react-bootstrap';
import logo from '../../../images/logo/logo.svg';
import shield from '../../../images/icons/shield.svg'
import PopUp from '../../pop-up/pop-up.component';
import {checkName , checkValidateForm , checkCardnumber} from '../../../validations/validation';
import {Url} from '../../../url';
import {TRANSFERE} from './api';


function AddPayment(props) {

    const [show , setShow] = useState(false);
    const [state , setState] = useState({
        loading : false ,
        accType : 'SAVINGS' ,
        errors : {}
    });
    const handleClose = () => setShow(false);
    const handleChange = props => event =>{
        let value  = event.target.value;
        setState({...state , [props] : value});
        let errors = state.errors;
        errors.form = ''
        switch (props) {
            case 'holderName':
                errors.holderName = checkName(value)
                break;
            case 'accNumber':
                errors.accNumber = checkCardnumber(value)
                break;
            case 'postCode':
                errors.postCode = (value <= 0 || value.length === 0 ||value.length >= 10 )? 'Please enter a valid input.' : ''
                break;
            case 'address':
                errors.address = checkName(value)
                break;
            case 'city':
                errors.city = checkName(value)
                break;
            case 'country':
                errors.country = value.length > 2 ? 'This Field must be only two digit.' : ''
                break;
            case 'rNumber':
                errors.rNumber = value <= 0 || value.length === 0 || value.length >= 10 ? 'Please enter a valid input.' : ''
                break;
            default:
                break;
        }
    } 
    const handleSave = () => {
        setShow(false);
        setState({ ...state, loading: true });
        const token = JSON.parse(localStorage.getItem('QA_jwt')).BearerTokenResponse.access_token;
        $.ajax({
            url: Url,
            method: 'post',
            headers: {
                authorization: `Bearer ${token}`
            },
            data: {
                query: TRANSFERE,
                variables: {
                    "amount" : 12,
                    "accountHolderName" : state.holderName,
                    "abartn" : state.rNumber,
                    "accountNumber" : state.accNumber,
                    "accountType" : state.accType,
                    "postCode" : state.postCode ,
                    "address" : state.address ,
                    "city" : state.city ,
                    "country" : state.country
                }
            },
            dataType: 'json',
            success: res => {
                setState({ ...state, loading: false });
                if(res.data){
                    if(res.data.PersonTransfer.Status.code === 200){
                            props.history.push('/earnings');
                    }
                    else if (res.data.PersonTransfer.Status.code === 404){
                            let errors = state.errors ;
                            errors.form = res.data.PersonTransfer.StatusTransfer.message;
                            setState({...state , errors : errors})
                    }
                }
                else{
                    console.log(res);
                }
            },
            cache: err => {
                setState({ ...state, loading: false });
                let errors = state.errors ;
                errors.form = err;
                setState({...state , errors : errors})}
        });
    }
    const handleSupmit = (e) => {
        e.preventDefault();
        if (checkValidateForm(state.errors)) {
            if (localStorage.QA_jwt) {
                setShow(true)
            }
        }
    }
    return (
        <main>
            {/* loader */}
            <div>
                <Loader
                    className="loading"
                    visible={state.loading}
                    type="TailSpin"
                    color="white"
                    height={50}
                    width={50}
                />
            </div>
            {/* bread crumb */}
            <nav className="bg-lightGreen">
                <ol class="breadcrumb bg-transparent container">
                    <li class="breadcrumb-item text-green bold" style={{ fontSize: '14px' }}><Link href="">Earnings</Link></li>
                    <li class="breadcrumb-item bold active" style={{ fontSize: '14px' }} aria-current="page">Add payment Method</li>
                </ol>
            </nav>
            {/* title */}
            <header className="container mt-4">
                <div className="row justify-content-center ">
                    <figure className="col-1">
                        <img src={logo} alt="logo" width="47px" />
                    </figure>
                    <article className="col-11 text-left ">
                        <h4 style={{ fontSize: '20px' }} className=" mb-0">Local Bank (EGP) </h4>
                        <p className="text-lightgrey" style={{ fontSize: '14px' }}>Send money directly to your bank.</p>
                    </article>
                </div>
            </header>
            {/* form */}
            <section className=" container mt-2 mb-5">
                <Form   
                    className="border-rounded border bg-light p-3 col-md-9"
                    onSubmit={handleSupmit}>
                    <Form.Group >
                        <Form.Label>Account Holder Name</Form.Label>
                        <Form.Control placeholder="ex:john adam" required onChange={handleChange('holderName')} type="text" />
                    </Form.Group>
                    <div className="text-danger"><p className="mb-1">{state.errors.holderName}</p></div>
                    <Form.Group controlId="exampleForm.ControlSelect1">
                        <Form.Label>Account type</Form.Label>
                        <Form.Control required onChange={handleChange('accType')}  as="select">
                            <option>SAVINGS</option>
                            <option>CHECKING</option>
                        </Form.Control>
                    </Form.Group>
                    <Form.Group >
                        <Form.Label>Account number</Form.Label>
                        <Form.Control required onChange={handleChange('accNumber')} type="text" />
                    </Form.Group>
                    <div className="text-danger"><p className="mb-1">{state.errors.accNumber}</p></div>
                    <Form.Group >
                        <Form.Label>Post code</Form.Label>
                        <Form.Control required onChange={handleChange('postCode')} type="number" />
                    </Form.Group>
                    <div className="text-danger"><p className="mb-1">{state.errors.postCode}</p></div>
                    <Form.Group >
                        <Form.Label>Routing Number</Form.Label>
                        <Form.Control required onChange={handleChange('rNumber')} type="number" />
                    </Form.Group>
                    <div className="text-danger"><p className="mb-1">{state.errors.rNumber}</p></div>
                    <Form.Group >
                        <Form.Label>Address</Form.Label>
                        <Form.Control required onChange={handleChange('address')} type="text" />
                    </Form.Group>
                    <div className="text-danger"><p className="mb-1">{state.errors.address}</p></div>
                    <div className="row">
                        <Form.Group className="col-6">
                            <Form.Label>City</Form.Label>
                            <Form.Control required onChange={handleChange('city')} type="text" />
                        <div className="text-danger"><p className="mb-1">{state.errors.city}</p></div>
                        </Form.Group>
                        <Form.Group className="col-6">
                            <Form.Label>Country</Form.Label>
                            <Form.Control required placeholder="Enter only first two digit of your country  ex: eg" onChange={handleChange('country')}  type="text" />
                            <div className="text-danger"><p className="mb-1">{state.errors.country}</p></div>
                        </Form.Group>
                    </div>
                    <div className="row">
                        <div className="col-12">
                            <div className="text-danger"><p className="mb-1">{state.errors.form}</p></div>
                        </div>
                    </div>
                    <Button 
                        type="Submit" 
                        className="bg-green w-100 border-0" 
                    >
                        Submit
                    </Button>
                    <div className="w-100 text-center">
                        <Link className="text-dark pt-2" to='/earnings'>Cancel</Link>
                    </div>
                </Form>
            </section>
            {/* Verification popU */}
            <PopUp 
               title={
                <div className="row justify-content-center p-2">
                    <figure className="col-3 pr-0 mb-0">
                        <img src={shield} alt="logo" className="img-fluid" />
                    </figure>
                    <article className="col-9">
                        <h4 style={{ fontSize: '18px' }} className=" mb-0">Verification </h4>
                        <p className="text-lightgrey mb-0" style={{ fontSize: '14px' }}>Your Payout Summary</p>
                    </article>
                </div>
               }
               show={show}
               hide={handleClose}
               cancelBtn="Cancel"
               cancelFun={handleClose}
               saveBtn="Payout"
               saveFun={handleSave}
               content = {
                   <div className="container">
                       <div className="row border-bottom pb-2  m-0 ">
                           <div className="col-6">From</div>
                           <div className="col-6">Qraan academy</div>
                       </div>
                       <div className="row border-bottom pb-2 m-0 mt-2">
                           <div className="col-6">Holder Name</div>
                           <div className="col-6">{state.holderName}</div>
                       </div>
                       <div className="row border-bottom pb-2 m-0 mt-2">
                           <div className="col-6">Account Number</div>
                           <div className="col-6">{state.accNumber}</div>
                       </div>
                       <div className="row border-bottom pb-2 m-0 mt-2">
                           <div className="col-6">Post Code</div>
                           <div className="col-6">{state.holderName}</div>
                       </div>
                       <div className="row border-bottom pb-2 m-0 mt-2">
                           <div className="col-6">Routing Number</div>
                           <div className="col-6">{state.rNumber}</div>
                       </div>
                       <div className="row border-bottom pb-2 m-0 mt-2">
                           <div className="col-6">Address</div>
                           <div className="col-6">{state.address}</div>
                       </div>
                       <div className="row border-bottom pb-2 m-0 mt-2">
                           <div className="col-6">City</div>
                           <div className="col-6">{state.city}</div>
                       </div>
                       <div className="row border-bottom pb-2 m-0 mt-2">
                           <div className="col-6">Country</div>
                           <div className="col-6">{state.country}</div>
                       </div>
                       <div className="mt-3 mb-3 bg-light p-2 m-0 mt-2">
                            <p style={{fontSize : '14px'}} className="mb-1">Please recheck your account information</p>
                            <p className="text-grey mb-1" style={{fontSize : '14px'}}>This payment method will become active in 3 days</p>
                       </div>
                   </div>
               }
            />
        </main>
    )
}
export default withRouter(AddPayment);
