import React from 'react';
import { withRouter } from 'react-router';

// styles icons 
import { Row, Col } from 'react-bootstrap';
import dots from '../../images/instractor-home/more.svg';
import clock from '../../images/instractor-home/vip.svg';

// language for test
import text from '../../languages/english/translate.json';

function TodaySessions() {
    let data = text.instractorHome.dashboard;
    return (
        <>
            <div className="rounded border m-0 mt-3">
                <div className="p-3 pb-1">
                    <h6 style={{fontSize : '18px'}}>{data.todaySession.title}</h6>
                    <p style={{fontSize : '14px'}} className="text-grey mb-0">{data.todaySession.description}</p>
                </div>
                <div className="w-100 border-top"></div>
                <div className="p-3">
                    <Row className="mb-2 align-items-center">
                        <Col xs={3} className="">
                            <img src="https://via.placeholder.com/150" className="rounded-circle img-fluid" alt="profile" />
                        </Col>
                        <Col xs={6} className="p-0 ">
                            <div>
                                <p style={{fontSize : '16px'}} className="mb-0">Titus Deckow</p>
                                <div>
                                    <p  className="mb-0">
                                        <span style={{fontSize : '14px'}} className="mr-3">Tahfeez</span>
                                        <span style={{fontSize : '14px'}}><img src={clock} className="img-fluid pr-1 pl-1" alt="clock" />3:53 AM</span>
                                    </p>
                                </div>
                            </div>
                        </Col>
                        <Col xs={3} className="text-right">
                            <button className="bg-transparent border-0">
                                <img src={dots} className="img-fluid" alt="clock icon" />
                            </button>
                        </Col>
                    </Row>
                    <Row className="mb-2 align-items-center">
                        <Col xs={3} className="">
                            <img src="https://via.placeholder.com/150" className="rounded-circle img-fluid" alt="profile" />
                        </Col>
                        <Col xs={6} className="p-0">
                            <div>
                                <p style={{fontSize : '16px'}} className="mb-0">Titus Deckow</p>
                                <div>
                                    <p className="mb-0">
                                        <span style={{fontSize : '14px'}} className="mr-3">Tahfeez</span>
                                        <span style={{fontSize : '14px'}}><img src={clock} className="img-fluid pr-1 pl-1" alt="clock" />3:53 AM</span>
                                    </p>
                                </div>
                            </div>
                        </Col>
                        <Col xs={3} className="text-right">
                            <button className="bg-transparent border-0">
                                <img src={dots} className="img-fluid" alt="clock icon" />
                            </button>
                        </Col>
                    </Row>
                </div>
            </div>
        </>
    )
}

export default withRouter(TodaySessions)