import React, { Component } from 'react';
import { withRouter } from 'react-router';
import PopUp from '../../pop-up/pop-up.component';
// styles
import { FormControl, InputLabel, Select, TextField, FormControlLabel, Checkbox, Button } from '@material-ui/core';
import { Row, Col } from 'react-bootstrap';
import Skeleton from '@material-ui/lab/Skeleton';
import AddIcon from '@material-ui/icons/Add';
import Calendar from './calendar.component';
import $ from 'jquery';
// language for test
import text from '../../../languages/english/translate.json';

class Index extends Component {
    constructor(props) {
        // get the date of today
        let date_ob = new Date();
        // adjust 0 before single digit date
        let date = ("0" + date_ob.getDate()).slice(-2);
        // current month
        let month = ("0" + (date_ob.getMonth() + 1)).slice(-2);
        // current year
        let year = date_ob.getFullYear();
        let maxYear = year + 5;

        super(props);
        this.state = {
            minDate: year + "-" + month + "-" + date,
            maxDate: maxYear + "-" + month + "-" + date,
            showReserve: false,
            showReservedSlot: false,
            showReschedule: false,
            refreshData: false,
            repeat: true,
            reservationType: 1,
            duration: 1,
            repeatNumber: 1,
            date: year + "-" + month + "-" + date,
            errors: {
                time: '',
                form: ''
            }

        };
    }
    GetUserData =()=>{
        const token = (localStorage.getItem('QA_jwt')) ? JSON.parse(localStorage.getItem('QA_jwt')).BearerTokenResponse.access_token : '';
        
        $.ajax({
            url: 'https://quranacademy.black-squares.com/graphql',
            method: 'post',
            headers: {
                authorization: `Bearer ${token}`
            },
            data: {
                query: `query{
                    meInstructors{
                        id
                      courses_type{id name}
                    }
          }
                    `,
            },
            dataType: 'json',
            success: res => {
                if (res.data.meInstructors) {
                    let instractorCourses = [];
                    if(res.data.meInstructors.courses_type.id === 1 || res.data.meInstructors.courses_type.id === 2 ){
                         instractorCourses = this.props.data?.courseType.filter(course=>course.id === res.data.meInstructors.courses_type.id)
                    }
                    else if(res.data.meInstructors.courses_type.id === 3){
                         instractorCourses = this.props.data?.courseType.filter(course=>course.id === 1 || course.id === 2)
                    }
                    this.setState({ ...this.state, courseType: instractorCourses, refreshData: false })
                }
            },
            cache: err => console.log(err)
        });
    }
    componentDidMount(){
        this.GetUserData();
    }
    componentWillReceiveProps(currentProps) {
        if (currentProps.data?.durations) {
            this.setState({ ...this.state, durations: currentProps.data.durations, refreshData: false })
        }
    }
    render() {
        let data = text.instractorHome.dashboard;

        const handleShow = (date, time) =>
            this.setState({ ...this.state, showReserve: true, date: date, time: time, refreshData: false });
        const handleCloseReserve = () => {
            let errors = this.state.errors;
            errors.time = '';
            errors.form = '';
            this.setState({ ...this.state, showReserve: false, errors: errors, refreshData: false, date: '', time: '', showDate: false })
        }
        const handleCloseReservedSlot = () => {
            let errors = this.state.errors;
            errors.time = '';
            errors.form = '';
            this.setState({ ...this.state, errors: errors, showReservedSlot: false, refreshData: false, date: '', time: '' })
        }
        const handleCancelReservedSlot = () => {
            let id = this.state.slot.id
            if (id) {
                const token = (localStorage.getItem('QA_jwt')) ? JSON.parse(localStorage.getItem('QA_jwt')).BearerTokenResponse.access_token : '';

                if (!token) {
                    console.log("no token")
                }
                else {
                    $.ajax({
                        url: 'https://quranacademy.black-squares.com/graphql',
                        method: 'post',
                        headers: {
                            authorization: `Bearer ${token}`
                        },
                        data: {
                            query: `
                            mutation($id : Int!){
                                sessionsCancel(data: {
                                id : $id
                                }
                            ){
                                Status{
                                    code
                                    message
                                }
                                session{
                                    id
                                    date
                                    time
                                    rate_minutes_type{
                                    id name
                                    }
                                    course_type{
                                    id name
                                    }
                                }
                                }
                            }
                                `,
                            variables: {
                                id: id
                            }
                        },
                        dataType: 'json',
                        success: res => {
                            console.log(res);
                            if (res.data.sessionsCancel.Status.code === 200) {
                                this.setState({ ...this.state, loading: false, showReservedSlot: false, refreshData: true, date: '', time: '' })
                            }
                            else {
                                console.log("there is something wrong!")
                            }
                        },
                        cache: err => console.log(err)
                    });
                }
            }

        }
        const handleChange = props => event => {
            let errors = this.state.errors;
            errors.time = '';
            errors.form = '';
            if (props === 'repeat') {
                if (event.target.checked === false) {
                    this.setState({ ...this.state, refreshData: false, [props]: event.target.checked, repeatNumber: 0 });
                }
                else {
                    this.setState({ ...this.state, refreshData: false, [props]: event.target.checked });
                }
            }
            else if (props === 'time') {
                let hour = event.target.value.substring(0, 2)
                errors.time = ''
                if (parseInt(hour[0]) === 0) {
                    if (parseInt(hour[1]) >= 0 && parseInt(hour[1]) < 8) {
                        errors.time = "This Field must be between 8AM and 11PM"
                    }
                }
                this.setState({ ...this.state, refreshData: false, [props]: event.target.value + ":00" });
            }
            else {
                this.setState({ ...this.state, refreshData: false, [props]: event.target.value });
            }
        }
        const validateForm = (errors) => {
            let valid = true;
            Object.values(errors).forEach(
                // if we have an error string set valid to false
                (val) => val.length > 0 && (valid = false)
            );
            return valid;
        }
        const handleSave = () => {
            let that = this;
            if (validateForm(this.state.errors)) {
                const token = (localStorage.getItem('QA_jwt')) ? JSON.parse(localStorage.getItem('QA_jwt')).BearerTokenResponse.access_token : '';

                if (!token) {
                    alert("Your session is expired, Please login first.")
                    this.props.history.push({
                        pathname: '/login'
                    })
                }
                else {
                    let repeatData = { "repeat": this.state.repeatNumber ? this.state.repeatNumber : 0, "type": 'weekly' }
                    let errors = this.state.errors;
                    console.log(this.state)
                    if (this.state.time && this.state.date) {
                        $.ajax({
                            url: 'https://quranacademy.black-squares.com/graphql',
                            method: 'post',
                            headers: {
                                authorization: `Bearer ${token}`
                            },
                            data: {
                                query: `
                                    mutation($date : Date!  $course_type_id : Int! 
                                        $rate_minutes_type_id : Int!  $time :  Time! 
                                        $repeat : sessionsRepeatInput){
                                        sessionsCreate(data: {
                                        date :$date
                                        course_type_id : $course_type_id
                                        rate_minutes_type_id : $rate_minutes_type_id
                                        time : $time
                                        repeat : $repeat
                                        }){
                                        Status{
                                            code
                                            message
                                        }
                                        sessions{
                                            instructor{
                                            email
                                            }
                                            date
                                            time
                                            DateTime
                                            rate_salary
                                        }
                                        }
                                    }
                                    `,
                                variables: {
                                    date: this.state.date,
                                    course_type_id: this.state.reservationType,
                                    rate_minutes_type_id: this.state.duration,
                                    time: this.state.time,
                                    repeat: this.state.repeat ? repeatData : null
                                }
                            },
                            dataType: 'json',
                            success: res => {
                                if (res.data.sessionsCreate.Status.code === 200) {
                                    that.setState({ ...this.state, showReserve: false, showDate: false, reservedSessions: res.data.sessionsCreate.sessions, refreshData: true });
                                }
                                else if (res.data.sessionsCreate.Status.code === 502) {
                                    let errors = this.state.errors;
                                    errors.form = "You can't reserve this slot right now."
                                    that.setState({ ...this.state, errors: errors, refreshData: false, showDate: false });
                                }
                                else {
                                    let errors = this.state.errors;
                                    errors.form = "There is something wrong, Please try again later"
                                    that.setState({ ...this.state, errors: errors, refreshData: false, showDate: false });
                                }
                            },
                            cache: err => console.log(err)
                        });
                    }
                    else {
                        errors.form = 'All fields are required!';
                        this.setState({ ...this.state, errors: errors })
                    }
                }
            }
        }
        const handleShowReschedule = () => this.setState({ ...this.state, showReservedSlot: false, showReschedule: true, refreshData: false });
        const handleReschedule = () => {

            if (validateForm(this.state.errors)) {
                const token = (localStorage.getItem('QA_jwt')) ? JSON.parse(localStorage.getItem('QA_jwt')).BearerTokenResponse.access_token : '';
                if (!token) {
                    console.log("no token")
                }
                else {
                    let date, time, courseType;
                    date = document.querySelector("#scheduleReservationDate").value;
                    time = document.querySelector("#scheduleReservationTime").value;
                    if (time.length < 8) {
                        time = time + ":00";
                    }
                    courseType = document.querySelector("#scheduleReservationType").value;
                    this.state.slot.id &&
                        $.ajax({
                            url: 'https://quranacademy.black-squares.com/graphql',
                            method: 'post',
                            headers: {
                                authorization: `Bearer ${token}`
                            },
                            data: {
                                query: `
                                mutation( $id : Int!  $course_type_id  : Int   $date : Date  $time : Time){
                                    sessionsEdit(data:{
                                    id : $id 
                                    course_type_id : $course_type_id  
                                    time : $time
                                    date : $date
                                    }){
                                    Status{
                                        code
                                        message
                                    }
                                    session{
                                        id
                                    }
                                    }
                                }
                                    `,
                                variables: {
                                    id: this.state.slot.id,
                                    date: date,
                                    time: time,
                                    course_type_id: courseType
                                }
                            },
                            dataType: 'json',
                            success: res => {
                                console.log(res);
                                if (res.data?.sessionsEdit.Status.code === 200) {
                                    this.setState({ ...this.state, loading: false, showReschedule: false, refreshData: true })
                                }
                                else if (res.data?.sessionsEdit.Status.code === 502) {
                                    let errors = this.state.errors;
                                    errors.form = "This slot is not available."
                                    this.setState({ ...this.state, errors: errors, refreshData: false });
                                }
                                else {
                                    let errors = this.state.errors;
                                    errors.form = "There is something wrong, Please try again later"
                                    this.setState({ ...this.state, errors: errors, refreshData: false });
                                }
                            },
                            cache: err => console.log(err)
                        });
                }
            }
        }
        const handleCloseReschedule = () => {
            let errors = this.state.errors;
            errors.time = '';
            errors.form = '';
            this.setState({ ...this.state, errors: errors, showReschedule: false, refreshData: false })
        }
        const handleReserveSlot = (date, time) => handleShow(date, time)
        const handleShowSlotDetails = (slot) => this.setState({ ...this.state, showReservedSlot: true, slot: slot, refreshData: false, date: '', time: '' })
        if (!this.state.courseType || !this.state.durations) {
            return (
                <Row className="m-0 mt-3">
                    <Col md={12}>
                         <Skeleton variant="rect" className="mb-2" height={30}/>
                    </Col>
                    <Col md={3}>
                         <Skeleton  variant="rect"  height={118} />
                    </Col>
                    <Col md={9}>
                         <Skeleton   variant="rect" height={500}/>
                    </Col>
                </Row>
            )
        }
        else {
            return (
                <div className=" mb-3 ">
                    <Row className="m-0 align-items-end justify-content-end">
                        <Col xs={12} className="text-right p-0">
                            <Button
                                onClick={e =>
                                    this.setState({ ...this.state, showReserve: true, showDate: true, refreshData: false })}
                                    className="border-0 bg-transparent  text-green">
                                <h5 className="mb-0"  style={{fontSize : '16px'}} > <AddIcon />{data.newSlot}</h5>
                            </Button>
                        </Col>
                    </Row>
                    <div>
                        <Calendar
                            reserveSlot={handleReserveSlot}
                            showSlotDetails={handleShowSlotDetails}
                            refreshData={this.state.refreshData} />
                    </div>
                    <div>
                        {/* reserve a slot */}
                        <PopUp
                            title={data.reserveSlot}
                            show={this.state.showReserve}
                            hide={handleCloseReserve}
                            cancelBtn={data.cancelBtn}
                            cancelFun={handleCloseReserve}
                            saveBtn={data.addBtn}
                            saveFun={handleSave}
                            content={
                                <Row>
                                    <Col md={12} className="mb-3">
                                        <InputLabel
                                            fullWidth>
                                            {data.reservationType}
                                        </InputLabel>
                                        <FormControl
                                            className=""
                                            variant="outlined" fullWidth size="small">
                                            <Select
                                                native
                                                onChange={handleChange('reservationType')}
                                            >
                                                {this.state.courseType.map(course =>
                                                    <option value={course.id}>{course.name}</option>
                                                )}
                                            </Select>
                                        </FormControl>
                                    </Col>
                                    {this.state.showDate &&
                                        <Col md={12} className="mb-3">
                                            <InputLabel className="mb-1" htmlFor="component-outlined">{data.date}</InputLabel>
                                            <TextField
                                                fullWidth
                                                size="small"
                                                type="date"
                                                defaultValue={this.state.minDate}
                                                inputProps={{ min: this.state.minDate, max: this.state.maxDate }}
                                                variant="outlined"
                                                onChange={handleChange('date')}
                                            />
                                            <p className="text-danger mb-1">{this.state.errors.date}</p>
                                        </Col>
                                    }
                                    <Col md={6} className="mb-3">
                                        <InputLabel className="mb-1" htmlFor="component-outlined">{data.time}</InputLabel>
                                        <TextField
                                            defaultValue={this.state.time}
                                            fullWidth
                                            size="small"
                                            type="time"
                                            variant="outlined"
                                            onChange={handleChange('time')}
                                        />
                                        <p className="text-danger mb-1">{this.state.errors.time}</p>
                                    </Col>
                                    <Col md={6} className="mb-3">
                                        <InputLabel
                                            fullWidth>
                                            {data.duration}
                                        </InputLabel>
                                        <FormControl
                                            className=""
                                            variant="outlined" fullWidth size="small">
                                            <Select
                                                native
                                                name=""
                                                onChange={handleChange('duration')}
                                            >{this.state.durations.map(duration =>
                                                <option value={duration.id}>{duration.name}</option>
                                            )}
                                            </Select>
                                        </FormControl>
                                    </Col>
                                    <Col md={6}>
                                        <FormControlLabel
                                            control={<Checkbox
                                                checked={this.state.repeat}
                                                color="primary"
                                                onChange={handleChange('repeat')}
                                                name="repeat" />}
                                            label={data.repeat}
                                        />
                                    </Col>
                                    {this.state.repeat &&
                                        <Col md={6} className="text-right w-100">
                                            <FormControl
                                                className=""
                                                fullWidth
                                                variant="outlined" size="small">
                                                <Select
                                                    native
                                                    name=""
                                                    fullWidth
                                                    value={this.state.repeatNumber}
                                                    onChange={handleChange('repeatNumber')}
                                                >
                                                    <option value={1}>1 Week </option>
                                                    <option value={2}>2 Weeks </option>
                                                    <option value={3}>3 Weeks </option>
                                                    <option value={4}>4 Weeks </option>
                                                    <option value={5}>5 Weeks </option>
                                                    <option value={6}>6 Weeks </option>
                                                    <option value={7}>7 Weeks </option>
                                                    <option value={8}>8 Weeks </option>
                                                </Select>
                                            </FormControl>
                                        </Col>
                                    }
                                    <Col><p className="text-danger mb-1">{this.state.errors.form}</p></Col>
                                </Row>
                        } />
                        {/* reschedule  a slot*/}
                        <PopUp
                            title={data.reschedule}
                            show={this.state.showReschedule}
                            hide={handleCloseReschedule}
                            cancelBtn={data.cancelBtn}
                            cancelFun={handleCloseReschedule}
                            saveBtn={data.reschedule}
                            saveFun={handleReschedule}
                            content={
                                <Row>
                                    <Col md={12} className="mb-3">
                                        <InputLabel
                                            fullWidth>
                                            {data.reservationType}
                                        </InputLabel>
                                        <FormControl
                                            className=""
                                            disabled
                                            variant="outlined" fullWidth size="small">
                                            <Select
                                                id="scheduleReservationType"
                                                defaultValue={this.state.slot?.course_type.id}
                                                native
                                            >
                                                {this.state.courseType.map(course =>
                                                    <option value={course.id}>{course.name}</option>
                                                )}
                                            </Select>
                                        </FormControl>
                                    </Col>
                                    <Col md={12} className="mb-3">
                                        <InputLabel className="mb-1" htmlFor="component-outlined">{data.date}</InputLabel>
                                        <TextField
                                            fullWidth
                                            id="scheduleReservationDate"
                                            size="small"
                                            type="date"
                                            inputProps={{ min: this.state.minDate, max: this.state.maxDate }}
                                            variant="outlined"
                                            defaultValue={this.state.slot?.date}
                                        />
                                    </Col>
                                    <Col md={6} className="mb-3">
                                        <InputLabel className="mb-1" htmlFor="component-outlined">{data.time}</InputLabel>
                                        <TextField
                                            defaultValue={this.state.slot?.time}
                                            id="scheduleReservationTime"
                                            fullWidth
                                            size="small"
                                            type="time"
                                            variant="outlined"
                                        />
                                        <p className="text-danger mb-1">{this.state.errors.time}</p>
                                    </Col>
                                    <Col md={6} className="mb-3">
                                        <InputLabel
                                            fullWidth>
                                            {data.duration}
                                        </InputLabel>
                                        <FormControl
                                            className=""
                                            variant="outlined" fullWidth size="small">
                                            <Select
                                                disabled
                                                native
                                                defaultValue={this.state.slot?.rate_minutes_type.id}
                                            >{this.state.durations.map(duration =>
                                                <option value={duration.id}>{duration.name}</option>
                                            )}
                                            </Select>
                                        </FormControl>
                                    </Col>
                                    <Col><p className="text-danger mb-1">{this.state.errors.form}</p></Col>
                                </Row>
                            }
                        />
                        {/*  show reserved session */}
                        <PopUp
                            title={data.reservedSlot}
                            show={this.state.showReservedSlot}
                            hide={handleCloseReservedSlot}
                            cancelBtn={data.cancelReservation}
                            cancelFun={handleCancelReservedSlot}
                            saveBtn={data.reschedule}
                            saveFun={handleShowReschedule}
                            content={
                                this.state.slot &&
                                <div>
                                    <Row className="border-bottom pt-2 pb-2 m-0">
                                        <Col className="text-left bold text-dark" xs={6}>{data.date}</Col> <Col xs={6}>{this.state.slot.date}</Col>
                                    </Row>
                                    <Row className="border-bottom pt-2 pb-2 m-0">
                                        <Col className="text-left bold text-dark" xs={6}>{data.time}</Col> <Col xs={6}>{this.state.slot.time}</Col>
                                    </Row>
                                    <Row className="border-bottom pt-2 pb-2 m-0">
                                        <Col className="text-left bold text-dark" xs={6}>{data.duration}</Col> <Col xs={6}>{this.state.slot.rate_minutes_type.name}</Col>
                                    </Row>
                                    <Row className="pt-2 pb-2 m-0">
                                        <Col className="text-left bold text-dark" xs={6}>{data.CourseType}</Col> <Col xs={6}>{this.state.slot.course_type.name}</Col>
                                    </Row>
                                </div>
                            }
                        />
                    </div>
                </div>
            )
        }
    }
}

export default withRouter(Index)