import React , {useState} from 'react';
import { withRouter } from 'react-router';
import {Link} from 'react-router-dom';
import $ from 'jquery';
import Loader from 'react-loader-spinner';
import CreditCardInput from 'react-credit-card-input';

import PopUp from '../../../../pop-up/pop-up.component';

import { checkName , checkCardnumber , checkValidateForm} from '../../../../../validations/validation';
import {SESSION_BOOK} from '../../../apis/apis';
import {Url} from '../../../../../url';
import Summary from './summary.component';


import {Row , Col , Form} from 'react-bootstrap';
import visa from '../../../../../images/icons/visa.svg';
import mastercard from '../../../../../images/icons/mastercard.svg';
import paypal from '../../../../../images/icons/paypal.png';
import email from '../../../../../images/icons/email.svg';

import {FormHelperText} from '@material-ui/core';


import Text from '../../../../../languages/english/translate.json';

function CheckOut(props) {
    let pageText = Text.student.instractorProfile.checkOut;
    const [state , setState] = useState({
        loading : false ,
        errors : {
            cardNumber : '',
            holderName : '',
            cvv : ''
        }
    });
    const [showPopup , setShowPopup ] = useState(false)

    const handleSumbit = (e) => {
       e.preventDefault();
       const token = (localStorage.getItem('QA_jwt')) ? JSON.parse(localStorage.getItem('QA_jwt')).BearerTokenResponse?.access_token : JSON.parse(localStorage.getItem('QA_jwt')).access_token ;
       if (!token) {
           props.history.push({
               pathname: '/login'
           })
       }
       else{
           setState({...state , loading : true});
           let sessionDetails = JSON.parse((localStorage.getItem('arryOfReservedSession')));;
           let arr = [] ,  newObj = {};
           sessionDetails.map(session => {
                newObj = {}
                newObj.id = session.id
                newObj.note = session.note
                arr.push(newObj);
           });
           $.ajax({
               url: Url,
               method: 'post',
               headers: {
                   authorization: `Bearer ${token}`
               },
               data: {
                   query: SESSION_BOOK,
                   variables: {
                       sessions :  arr,
                       name : state.holderName ,
                       number : state.cardNumber.replace(/\s/g,''),
                       date : state.expiry.replace(/\s/g,''),
                       cvv : state.cvv ,
                   }
               },
               dataType: 'json',
               success: res => {
                    setState({...state , loading : false})
                    console.log(res);
                   if(res.data.sessionsBook.Status.check === true){
                        setShowPopup(true);
                   }
         
               },
               cache: err => console.log(err)
            });
        }
    }
    const handleChange = props => event => {
        let value = event.target.value
        let errors = state.errors;

        if(props === 'cvv'){
            if(value > 0 && value.length <= 3){
                setState({...state , [props] : value });
            }
        }
        else{
            setState({...state , [props] : value });
        }
        switch (props) {
            case 'cardNumber':
                errors.cardNumber = checkCardnumber(value);
                break;
            case 'holderName':
                errors.holderName = checkName(value);
                break;
            default:
                break;
        }
    }
    const hidePopup = () => {
        setShowPopup(false);
        props.history.push({
            pathname : '/profile'
        });
        localStorage.setItem('arryOfReservedSession' , "")
    }
    return(
        <form onSubmit={handleSumbit}>
            <div className='bg-lightGreen'>
                <nav aria-label="breadcrumb " className='container'>
                    <ol className="breadcrumb bg-transparent">
                        <li  className="breadcrumb-item "><Link to='/instractorProfile'>Instructor profile</Link></li>
                        <li className="breadcrumb-item "><Link onClick={e=> window.history.back()}  aria-current="page">Reserve session</Link></li>
                        <li className="breadcrumb-item active" aria-current="page">Checkout</li>
                    </ol>
                </nav>
            </div>
            <main className="container mt-5 mb-5 p-0">
            <div>
                <Loader
                    className="loading"
                    visible={state.loading}
                    type="TailSpin"
                    color="white"
                    height={50}
                    width={50}
                />
            </div>
            <Row className="m-0">
                <Col md={7} className="">
                    <Row  className="m-0 mb-2">
                        <Col className="pl-0" md={6} xs={12}>
                            <h6 style={{fontSize : '20px'}}>{pageText.title}</h6>
                            <p style={{fontSize : '14px'}} className="text-grey">{pageText.description}</p>
                        </Col>
                        <Col className="pr-1 pl-0" md={2} xs={4}>
                            <figure style={{height : '48px'}}   className="border m-0 d-flex justify-content-center align-items-center">
                                <img src={paypal} alt="" style={{maxHeight : '18px'}} className="img-fluid"/>
                            </figure>
                        </Col>
                        <Col className="pr-1 pl-1" md={2} xs={4}>
                            <figure style={{height : '48px' }}  className="border m-0 d-flex justify-content-center align-items-center">
                                <img src={visa} style={{maxHeight : '16px'}} alt="" className="img-fluid"/>
                            </figure>
                        </Col>
                        <Col className="pr-1 pl-1" md={2} xs={4}>
                            <figure style={{height : '48px'}}   className="border m-0 d-flex justify-content-center align-items-center">
                                <img src={mastercard} style={{maxHeight : '25px'}} alt="" className="img-fluid"/>
                            </figure>
                        </Col>
                    </Row>
                    <div  className="bg-light p-3 mb-3">
                        <Form.Group >
                            {/* <Form.Label required style={{fontSize : '14px'}} className="bold">{pageText.cardHolderNameInput}</Form.Label> */}
                            <Form.Control placeholder={pageText.cardHolderNameInput} value={state.holderName} onChange={handleChange('holderName')} required type="text"  />
                            <FormHelperText className="text-danger">{state.errors.holderName}</FormHelperText>
                        </Form.Group>
                        <CreditCardInput
                            cardNumberInputProps={{ value: state.cardNumber, onChange: handleChange('cardNumber') }}
                            cardExpiryInputProps={{ value: state.expiry, onChange: handleChange('expiry')}}
                            cardCVCInputProps={{ value: state.cvv, onChange: handleChange('cvv')}}
                            fieldClassName="form-control input w-100"
                            containerClassName = "form-group w-100 "
                            customTextLabels={{
                                invalidCvc: 'invalid Cvv',
                                cvcPlaceholder: 'Cvv'}}
                            />
                    </div> 
                </Col>
                <Col md={5} className="">
                   <Summary data={props.location.state}/>
                </Col>
            </Row>
        </main>
        <PopUp 
            show={showPopup}
            hide={hidePopup}
            content={
                <article className="text-center">
                    <picture className='mb-1'><img height='46px' className="img-fluid" src={email} alt="email"/></picture>
                    <h6 style={{fontSize : '21px'}}>Reservation complete</h6>
                    <p style={{fontSize : '14px'}}>Your reservation has been successfully reserved and now you can see it in your upcoming sessions</p>
                </article>
            }/>
        </form>
    )
}
export default withRouter(CheckOut);