import React , { useRef }from 'react';
import {Link} from 'react-router-dom';
import ScrollIntoView from 'react-scroll-into-view'

// styles and images
import { Navbar, Container, Nav } from 'react-bootstrap';
import logoWeb from '../../images/logo/logo-1.svg'
import logoMob from '../../images/logo/logo.svg'

// language for test
import text from '../../languages/english/translate.json';

export default function Header() {
    let data = text?.home.header;
    const handleClick = () => {
        window.scrollTo({top: "0" , behavior : "smooth"})
    }
    return (
        <Navbar collapseOnSelect fixed="top" expand="lg" bg="white" className="shadow" variant="light">
                {/* for web */}
                <Navbar.Brand className="d-web">
                    <img src={logoWeb} className="img-fluid" alt="logo" />
                </Navbar.Brand>

                {/*  for mob */}
                <Navbar.Brand className="d-mob  mr-0">
                    <img src={logoMob} className="w-50" alt="logo" />
                </Navbar.Brand>
                <Nav className="d-mob">
                    <Link className="btn-green-outline  mr-1 " to='/confirmation'>Register</Link>
                    <Link className="btn-green" to='/login'>Sign In</Link>
                </Nav>

                {/*  5s mob or similer*/}
                <Navbar.Brand className="d-sm-mob mr-0">
                    <img src={logoMob} className="w-50" alt="logo" />
                </Navbar.Brand>
                <Nav className="d-sm-mob">
                    <Link  className="btn-green-outline  mr-1 pr-1 pl-1" to='/confirmation'>Register</Link>
                    <Link className="btn-green pr-1 pl-1" to='/login'>Sign In</Link>
                </Nav>

                <Navbar.Toggle aria-controls="responsive-navbar-nav" />

                <Navbar.Collapse id="responsive-navbar-nav">
                    <Nav className="m-auto">
                        <ScrollIntoView selector={data.home.url}>
                            <Nav.Link
                                className="mr-2 ml-2"
                            >
                                {data.home.text}
                            </Nav.Link>
                        </ScrollIntoView>
                        <ScrollIntoView selector={data.about.url}>
                            <Nav.Link
                                className="mr-2 ml-2"
                            >
                                {data.about.text}
                            </Nav.Link>
                        </ScrollIntoView>
                        <ScrollIntoView selector={data.services.url}>
                            <Nav.Link
                                className="mr-2 ml-2"
                            >
                                {data.services.text}
                            </Nav.Link>
                        </ScrollIntoView>
                        <ScrollIntoView selector={data.difference.url}>
                            <Nav.Link
                                className="mr-2 ml-2"
                            >
                                {data.difference.text}
                            </Nav.Link>
                        </ScrollIntoView>
                        <ScrollIntoView selector={data.contactUs.url}>
                            <Nav.Link
                                className="mr-2 ml-2"
                            >
                                {data.contactUs.text}
                            </Nav.Link>
                        </ScrollIntoView>
                    </Nav>
                </Navbar.Collapse>
                {/* buttons for web */}
                <Nav className="inline-block d-web">
                    <Link 
                        className="btn-green-outline mr-2 ml-2"  
                        onClick={handleClick} 
                        to={{pathname : '/registration',
                            state : "Register"
                        }}>
                            Register</Link>
                    <Link 
                        className="btn-green mr-2 ml-2" 
                        onClick={handleClick} 
                        to='/login'>Sign In</Link>
                </Nav>
        </Navbar>
    )
}