export const USER_TYPES = `
    query{
        meStudents{
        id
        email
        first_name
        last_name
        gender
        need_password
        date_of_birth
        profile_image{url}
        }
    }
`;
export const FINDE_INSTRCTOR = `
query($id : Int !){
    InstructorFind(id: $id){
      id
      email
      first_name
      last_name
      gender
      date_of_birth
      years_of_experience
      description
      is_profile_public
      is_Approval
      rate_15_min
      rate_30_min
      rate_60_min
      public_rate_minutes
      public_rate_minutes_type_id
      currency_type{code}
      total_students_counter
      total_earning_counter
      total_profile_views_counter
      currency_type{id code}
      courses_type{id name}
      student_gender_types{id name}
      student_age_types{id name}
      profile_vidoe{id url name}
      profile_image{id url}
      country{id name code flag}
      certificates{id file{name url}}
      Languages{id name }
    }
  }`
export const GET_INSTRACTOR_SESSIONS = `
query($id : Int! $afterDate : Date){
  InstructorFind(id: $id){
    sessionsByUnits(afterDate:$afterDate, firstUnits: 7,hasInUnits: dayly ){
    sessions{
      id
      course_type{id name}
      date
      time
      to_time
      rate_minutes_type{id name}
      student {id}
      rate_salary
      currency_type{symbol}
      }
    }
  }
}
`
export const GET_INSTRACTOR_SESSIONS_PER_MONTH = `
query($id : Int! $afterDate : Date){
  InstructorFind(id: $id){
    sessionsByUnits(afterDate:$afterDate, firstUnits: 30,hasInUnits: dayly ){
    sessions{
      id
      course_type{id name}
      date
      time
      to_time
      rate_minutes_type{id name}
      student {id}
      rate_salary
      currency_type{symbol}
      }
    }
  }
}
`
export const GET_INFO = `
query{
    meStudents{
      id
      email
      first_name
      last_name
      gender
      date_of_birth
      need_password
      profile_image{id url}
      country{id code}
      notifications{
        data{
          id
          message
          kind
          kind_number
          click_action
          created_at
          student{id profile_image{url}}
          instructor{id profile_image{url}}
          session{id}
        }
      }
    }
  }`
export const  USER_SESSIONS = `
query($orderBy : SessionsSortOrder $rate_minutes_type_id : Int $course_type_id  : Int  $rate_salary : Int $currency_type_id : Int){
  meStudents{
    sessionsGet(Inputs:{orderBy : $orderBy} first: 1000){
      data{
         id
        instructor{
          id 
          first_name 
          last_name 
          email 
          courses_type{name} 
          profile_image{url}
          sessionsByUnits(filter:{
            rate_minutes_type_id : $rate_minutes_type_id
           course_type_id : $course_type_id 
           rate_salary : $rate_salary
           currency_type_id : $currency_type_id}  firstUnits : 30)
            {
            sessions {id rate_minutes_type{name} date  time  course_type{id name}}
            }
        }
        date
        time 
       count_materials
        materials{url}
        course_type{id name}
        note
        can_start
        rate_minutes_type{name}
      }
    }
  }
}
`
export const SESSION_RESCEDUAL = `
mutation($id_old : Int ! $id_new : Int !){
  sessionsChange(data: {
    id_old : $id_old
    id_new : $id_new
  }){
    Status{
      check 
    }
    session{
      id instructor{id}
    }
  }
}
`
export const DELETE_SESSION = `mutation( $id : Int!){
  sessionsDelete(data: {
    id : $id
  }){
    Status{
      check
    }
  }
}`
export const EDIT_PROFILE = `
mutation($first_name : Name $last_name : Name){
    PersonEdit(data: {
    first_name : $first_name
    last_name : $last_name
    }){
    Student{
        email
        first_name
        last_name
        email
        profile_image{url}
    }
    Instructor{id}
    SubInstructor{id}
    Status{code message}
    }
}`

export const UPLOAD_FILE = gql`
mutation($profile_image : Upload){
    PersonEdit(data: {
      profile_image : $profile_image
    }){
      Student{
        email
        first_name
        last_name
        email
        profile_image{url}
      }
      Instructor{id}
      SubInstructor{id}
      Status{code message}
    }
  }
`;

export const CHANGE_NAME = gql`
mutation($first_name : Name $last_name : Name){
    PersonEdit(data: {
      first_name : $first_name
      last_name : $last_name
    }){
      Student{
        email
        first_name
        last_name
        email
        profile_image{url}
      }
      Instructor{id}
      SubInstructor{id}
      Status{code message}
    }
  }
`;
export const SESSION_BOOK =  `
mutation($sessions : [sessionsArrayInputs!]! $name : Name ! $number : CardNumber! $date : CreditCardExpirationDate! $cvv: Int!){
  sessionsBook(data: {
    sessions : $sessions
    Card: {
      name : $name
      number : $number
      date : $date
      cvv : $cvv
    }
  }){
    Status{ check}
  }
}`
export const SESSION_CANCEL =   `
mutation( $id : Int !){
  sessionsCancel(data: {
    id : $id
  }){
    Status{
      check
      message
    }
    session{
      id
    }
  }
}`