import React, { useState, useEffect } from 'react';
import { withRouter } from 'react-router';
import $ from 'jquery';

import SearchCard from './search-card.component';
import Filters from './filters.component';
import RoundedCard from '../../loaders/rounded.card.component';
import { url, SearchQuery } from '../apis/apis';
import NoData from '../../no-data/no-data.component';

import { Skeleton } from '@material-ui/lab';
import './search.style.css';

function Search(props) {
    const [state, setState] = useState({
        gender : '',
        country : '',
        language : '',
        course : '' ,
        duration : '',
        price : [0,1000],
        priceFrom : '',
        priceTo : '',
        loading: false
    });
    const counter = 9; 
    const handleChangeFilters = props => event =>  {
        if(event.target.value === 'true'){
            setState({ ...state , [props] : true});
        }
        else if(event.target.value === 'false'){
            setState({ ...state , [props] : false});
        }
        else{
            setState({ ...state , [props] : parseInt(event.target.value)});
        }

    };
    const handleChangePrice = props =>{
       setState({ ...state , price : props });
    }
    const InstractorSearch = ($name, $gender, $country_id,
        $Spoken_Language_User, $courses_types_id, $price_from,
        $price_to, $rate_minutes_type_id) => {

        setState({...state , loading: true });
        // var formData = new FormData();
        // let variables = {
        //     name: $name && $name,
        //     gender: $gender && $gender,
        //     country_id: $country_id && $country_id,
        //     Spoken_Language_User: $Spoken_Language_User && $Spoken_Language_User,
        //     courses_types_id: $courses_types_id && $courses_types_id,
        //     price_from: $price_from && $price_from,
        //     price_to: $price_to && $price_to,
        //     rate_minutes_type_id: $rate_minutes_type_id && $rate_minutes_type_id,
        // }
        // formData.append('operations' , 
        // JSON.stringify({ 'query': SearchQuery ,'variables' : variables }));
        // formData.append( 'map', JSON.stringify( [ ] ) ) ;
        // $.ajax({
        //     url      : url,
        //     data : formData,
        //     processData: false,
        //     success : ( data) => {
        //         console.log( data ) ;
        //     }
        // });
        $.ajax({
            url: url,
            method: 'post',
            data: {
                query: SearchQuery,
                variables: {
                    name: $name && $name,
                    
                    country_id: $country_id && $country_id,
                    Spoken_Language_User: $Spoken_Language_User && $Spoken_Language_User,
                    courses_types_id: $courses_types_id && $courses_types_id,
                    price_from: $price_from && $price_from,
                    price_to: $price_to && $price_to,
                    rate_minutes_type_id: $rate_minutes_type_id && $rate_minutes_type_id,
                }
            },
            dataType: 'json',
            success: res => {
                if (res) {
                    console.log(res);
                     setState({ ...state , data: res.data.InstructorSearch.data, loading: false })
                }
            },
            cache: err => { console.log(err) }
        });
    }
    const handleApplyFilter = () => {
        InstractorSearch(props.location?.state && props.location?.state, state.gender, state.country , state.language , state.course , state.price[0] , state.price[1] , state.duration)
    }
    const handleReset = () => {
        setState({...state , loading: true });
        $.ajax({
            url: url,
            method: 'post',
            data: {
                query: SearchQuery,
                variables: {
                    name: "",
                    gender:"",
                    country_id: "",
                    Spoken_Language_User: "",
                    courses_types_id: "",
                    price_from: "",
                    price_to: "",
                    rate_minutes_type_id: "",
                }
            },
            dataType: 'json',
            success: res => {
                if (res) {
                    setState({ ...state , data: res.data.InstructorSearch.data,
                        gender : '',
                        country : '',
                        language : '',
                        course : '' ,
                        price : [0  , 1000],
                        duration : '',loading: false });
                        resetSearchInput();

                }
            },
            cache: err => { console.log(err) }
        });
    }
    const resetSearchInput = () =>{
        let searchInput =  document.querySelector('#searchInput');
        searchInput.value = '';
        props.history.push({search : ''})
    }
    useEffect(() => {
        if(props.location.state) {

            let serachKey = props.location.state;
            InstractorSearch(serachKey);

        }
        else if(props.location.search)
        {
            let serachKey = props.location.search.replace("?" , "");
            InstractorSearch(serachKey);

            let searchInput =  document.querySelector('#searchInput');
            searchInput.value = serachKey;
        }
        else{
            InstractorSearch()
        }
    }, [props]);
    return (
        <main className="container mt-5 mb-5">
            <section className="row align-items-start">
                <aside className="col-md-3">
                    <Filters defaultValue={state} applyFilter={handleApplyFilter} reset={handleReset} handleChange={handleChangeFilters} handleChangePrice={handleChangePrice}/>
                </aside>
                <article className="col-md-9 row m-0 border border-rounded pt-3 pb-1">
                    {
                        state.loading ?
                            <>
                                <div className="col-md-12">
                                    <Skeleton className="mb-2" animation="wave" height={30} width={170} />
                                </div>
                                {[...Array(counter)].map((e, i) => <RoundedCard key={i} />)}
                            </>
                            :
                            <>
                                {(state.data && state.data.length) > 0 ?
                                    <>
                                        <p className="text-grey col-md-12 ">
                                            <span className="pr-1">Showing </span>
                                            {state.data?.length}
                                            {(state.data?.length <= 1) ? ' result ' : ' results '}
                                        </p>
                                        {state.data?.map(instractor =>
                                            <SearchCard data={instractor} />
                                        )}
                                    </>
                                    : <NoData title="Sorry! No result found" description="Were sorry what you were looking for isnt found. Please try another way"/>
                                }
                            </>
                    }
                </article>
            </section>
        </main>
    )
}
export default withRouter(Search)