import React, { Component } from 'react';
import { Link } from 'react-router-dom';
import ReactCodeInput from 'react-verification-code-input';
import { withRouter } from 'react-router';
import $ from 'jquery';
import { CometChat } from "@cometchat-pro/chat"

import Main from './main.component';
import { Url } from '../../url';
import { VERFY_PIN_CODE } from './apis';

// styles images and icons
import { Row, Col } from 'react-bootstrap';
import { Button } from '@material-ui/core';
import mail from '../../images/login/icon.svg'

// language for test
import text from '../../languages/english/translate.json';


class PinCode extends Component {
    constructor(props) {
        super(props);
        this.state = {
            email: '',
            codeFor: ''
        }
    }
    componentDidMount() {
        if (this.props.location?.state) {

            let data = this.props.location.state;
            let codeFor = "";
            if (data.codeFor === 'passwordresets') {
                codeFor = 'reset';
                this.setState({ ...this.state, email: data.email, codeFor: codeFor })
            }
            else if(data.state?.codeFor === 'Register') {
                codeFor = 'registration';
                this.setState({
                    ...this.state, email: data.email, codeFor: codeFor,
                    codeForServer: data.state.codeFor, firstName: data.state.firstName, lastName: data.state.lastName,
                    password: data.state.password, dateOfBirth: data.state.dateOfBirth, gender: data.state.gender, userType: data.state.userType
                })
            }
        }
    }
    render() {
        // language variable
        let data = text.pinCode;
        const registerWithEmail = (code) => {
            // register as student
            let userType = '';
            let to = '';

            if (this.state.userType === 'Student') {
                userType = 'studentsInput';
                to = '/dashboard'
            }
            // regiter as an instructor
            else {
                userType = 'subInstructorsInput';
                to = '/basicInformation'
            }
            $.ajax({
                url: Url,
                method: 'post',
                data: {
                    query: `
                        mutation($${userType} : ${userType}  $email : Email!
                            $pin_code :String!){
                            globalRegister(data: {
                                ${userType} : $${userType} ,
                                verifyPinCodeInput : {
                                email : $email
                                pin_code : $pin_code
                                }
                            }){
                                
                                BearerTokenResponse{
                                access_token
                                refresh_token
                                expires_in
                                }
                                SubInstructor{
                                    id
                                    email
                                    first_name
                                    last_name
                                    gender
                                    date_of_birth
                                }
                                Student{
                                    id
                                    first_name
                                }
                                Status{
                                code
                                message
                                check
                                }
                            }
                            }
                            `,
                    variables: JSON.stringify({
                        [userType]:
                        {
                            "first_name": this.state.firstName,
                            "last_name": this.state.lastName,
                            "password": this.state.password,
                            "date_of_birth": this.state.dateOfBirth,
                            "gender" : this.state.gender
                        },
                        "pin_code": code,
                        "email": this.state.email
                    })
                },
                dataType: 'json',
                success: res => {
                    if (res.data?.globalRegister?.Status.code === 200) {
                        localStorage.setItem('QA_jwt', JSON.stringify(res.data.globalRegister));

                        this.props.history.push({
                            pathname: to,
                            state : res.data?.globalRegister
                        });
                    if( this.state.userType === 'Student') {
                        
                            let authKey = "9d358e07010f7a0c1bf278f80645c1649d394937";
                             var uid =  "STUDENT"+res.data.globalRegister.Student.id.toString();
                            // let uid = "SUPERHERO1";
                            var name =  res.data.globalRegister.Student.first_name;
                            var user = new CometChat.User(uid);
                            user.setName(name);
                            CometChat.createUser(user, authKey).then(
                                user => {
                                    console.log("user created", user);
                                    // localStorage.setItem('QA_COMETCHAT_USER', JSON.stringify(user));

                                    const appID = "33784fe3bc0b0c2";
                                    const region = "us";
                                    const appSetting = new CometChat.AppSettingsBuilder().subscribePresenceForAllUsers().setRegion(region).build();
                                    CometChat.init(appID, appSetting).then(
                                    () => {
                                        console.log("Initialization completed successfully");

                                        //call login function.
                                        CometChat.login(uid, authKey).then(
                                            user => {
                                                console.log("Login Successful:", { user });    
                                            },
                                            error => {
                                                console.log("Login failed with exception:", { error });    
                                            }
                                            );
                                    },
                                    error => {
                                        console.log("Initialization failed with error:", error);
                                        // Check the reason for error and take appropriate action.
                                    }
                                    );

                                },error => {
                                    console.log("error", error);
                                }
                            );
                        }
                    }
                },
                cache: err => {
                    let errors = this.state.errors;
                    errors.form = text.validation.form;
                    this.setState({ ...this.state, errors: errors })
                }
            });
        }
        const handleVerify = () => {

            let inputs = document.querySelectorAll('input');
            let code = "";
            for (var x = 0; x < inputs.length; x++) {
                code = code + inputs[x].value
            }
            if (code.length === 5) {
                $.ajax({
                    url: Url,
                    method: 'post',
                    data: {
                        query: VERFY_PIN_CODE,
                        variables: {
                            "email": this.state.email,
                            "pin_code": code
                        }
                    },
                    dataType: 'json',
                    success: res => {
                        if (res.data) {
                            if (res.data.globalverifyPinCode.Status.code === 200) {
                                if (this.state.codeFor === 'registration') {
                                    // register with email
                                    registerWithEmail(code);
                                } 
                                else {
                                    // go to reset password
                                    this.props.history.push({
                                        pathname: this.state.codeFor,
                                        state: {
                                            email: this.state.email,
                                            code: code
                                        }
                                    });
                                }
                            } else {
                                this.setState({ ...this.state, error: res.data.globalverifyPinCode.Status.message })
                            }
                        }
                    },
                    cache: err =>  this.setState({ ...this.state, error:  text.validation.form })
                });
            }
        }
        return (
            <Main
                content={
                    <Row className="alig-items-center justify-content-center mt-5 mb-3">
                        <Col md={4} className="bg-white  shadow text-center pt-5 pb-5 pr-4 pl-4">
                            <div className="mb-2">
                                <img src={mail} alt="mail" className="img-flid" />
                            </div>
                            <div>
                                <h4>{data.title}</h4>
                                <p>{data.description}
                                    {this.state.email && this.state.email}</p>
                            </div>
                            <div className="mb-4">
                                <ReactCodeInput
                                    fields={5}
                                    fieldHeight={45}
                                    fieldWidth={45}
                                    className="w-auto"
                                    onComplete={handleVerify}
                                />
                                <div className="text-danger text-center"><p className="mb-1 mt-1">{this.state.error}</p></div>
                            </div>
                            <div className="">
                                <Button
                                    fullWidth
                                    className="btn-green"
                                    onClick={handleVerify}>
                                    {data.verify}
                                </Button>
                            </div>
                            <div>
                                <div className="text-center mt-3">
                                    {this.state.codeFor}
                                    <Link className="text-green" to={{
                                        pathname: '/confirmation',
                                        state: this.state.codeForServer
                                    }}> {data.resend} </Link>
                                </div>
                            </div>
                        </Col>
                    </Row>
                } />
        )
    }
}

export default withRouter(PinCode)