import React from 'react';
import { withRouter } from 'react-router';

import Container from '../../instractor/view-profile/container.component';

import {Row , Col} from 'react-bootstrap';
import {Button} from '@material-ui/core'
import pp from '../../../images/intstractor-profile/pp.png'
import DateRangeIcon from '@material-ui/icons/DateRange';

import text from '../../../languages/english/translate.json';


function  Info(props) {
    
    let pageText = text['instractor-profile'].view;
    let userData = props.data;
    
    return(
        <Container >
            <Row className="align-items-center mb-4">
                <Col md={2} xs={12} className="text-center-responsive mb-2">
                    <img height="90px" width="90px" className=" rounded-circle" src={userData.profile_image? userData.profile_image.url :pp} alt="profile"/>
                </Col>
                <Col md={5} xs={12} className="text-center-responsive mb-2">
                    <h5 className="mb-1 text-capitalize">{userData.first_name +'  '+ userData.last_name}</h5>
                    {userData.country &&
                        <p style={{ fontSize : '14px'}} className="text-grey mb-0">
                            <img src={userData.country.flag} className="pr-2" alt="flag" height="12px"/>
                            {userData.country.name}</p>
                    }
                </Col>
                <Col md={5} xs={12} className="text-center-responsive mb-2">
                    <Button 
                        size="small"
                        className="border-0 btn-green rounded pr-4 pl-4" 
                        onClick={e=>{
                            props.history.push({
                                pathname : '/reserveSession',
                                search: userData.id.toString()
                            })
                        }}
                        >Reserve Session</Button>
                </Col>
            </Row>
            <Row className="mb-3">
                <Col md={3} xs={6}  className="">
                    <article>
                        <h5 style={{fontSize : '16px'}} className="text-dark">{pageText.info.rate}</h5>
                        <p style={{fontSize : '14px'}} className="text-grey d-inline-block">
                            {userData.public_rate_minutes +' '+ userData.currency_type.code +' Per ' }  
                        </p>
                        <p style={{fontSize : '14px'}} className='text-grey d-inline-block pl-1'>{userData.public_rate_minutes_type_id === 1 && '15 min'}</p>
                        <p style={{fontSize : '14px'}} className='text-grey d-inline-block pl-1'>{userData.public_rate_minutes_type_id === 2 && '30 min'}</p>
                        <p style={{fontSize : '14px'}} className='text-grey d-inline-block pl-1'>{userData.public_rate_minutes_type_id === 3 && '60 min'}</p>

                    </article>
                </Col>
                <Col md={4} xs={6} className=" border-left border-right">
                    <article>
                        <h5 style={{fontSize : '16'}} className="text-dark">{pageText.info.courses}</h5>
                        <p style={{fontSize : '14px'}} className="text-grey">{userData.courses_type.name}</p>
                    </article>
                </Col>
                <Col md={4} xs={12} className="">
                    <article>
                        <h5 style={{fontSize : '16px'}} className="text-dark">{pageText.info.yearsOfExperience}</h5>
                        <p style={{fontSize : '14px'}} className="text-grey">{userData.years_of_experience}</p>
                    </article>
                </Col>
            </Row>
        </Container>
    )
}

export default withRouter(Info);