import React, { useState, useEffect } from 'react';
import { InMemoryCache } from 'apollo-cache-inmemory';
import { createUploadLink } from 'apollo-upload-client';
import { ApolloClient } from "apollo-client";
import $ from 'jquery';
import Loader from 'react-loader-spinner';


import { Url } from '../../../url';
import { GET_DATA, EDIT_PROFILE } from './api';

import Container from './container.component';
import text from '../../../languages/english/translate.json';
import {
    TextField, InputLabel, FormControl, Select, FormLabel, RadioGroup, Radio,
    Button, MenuItem, Input, Checkbox, ListItemText, FormControlLabel
} from '@material-ui/core';

import { Row, Col } from 'react-bootstrap';

export default function Basic(props) {
    const [state, setState] = useState({
        languageSpoken: [],
        preferedGender: "Male &amp; Female",
        preferedAge: "Adults &amp; Children",
        loading: false,
        errors: {
            rate15: '',
            rate30: '',
            rate60: ''
        }
    });
    const [selectedValue, setSelectedValue] = React.useState('1');
    const fetchData = () => {
        $.ajax({
            url: Url,
            method: 'post',
            data: {
                query: GET_DATA
            },
            dataType: 'json',
            success: res => {
                if (res.data) {
                    if (res.data.languageTypes) {
                        let language = [];
                        console.log(props);
                        props.data.Languages.map(lang => language.push(lang.name));
                        setState({
                            ...state, languageTypes: res.data.languageTypes, currencyTypes: res.data.currencyTypes
                            , courseType: res.data.courseType, studentAgeTypes: res.data.studentAgeTypes, studentGenderTypes: res.data.studentGenderTypes
                            , languageSpoken: language, preferedGender: props.data.student_gender_types?.name, courses: props.data.courses_type?.id
                            , preferedAge: props.data.student_age_types?.name, currency: props.data.currency_type?.id.toString()
                            , rate15: props.data.rate_15_min, rate30: props.data.rate_30_min, rate60: props.data.rate_60_min, loading: false
                        });
                        setSelectedValue(props.data.public_rate_minutes_type_id?.toString());
                    }
                }
            },
            cache: err => console.log(err)
        });
    }
    useEffect(() => {
        fetchData();
    }, [props])
    // activate uploading link on the oppollo 
    const apolloCache = new InMemoryCache()
    const token = (localStorage.getItem('QA_jwt')) ? JSON.parse(localStorage.getItem('QA_jwt')).access_token : '';

    const uploadLink = createUploadLink({
        uri: 'https://quranacademy.black-squares.com/graphql',
        headers: {
            authorization: token ? `Bearer ${token}` : ""
        }
    })
    const client = new ApolloClient({
        cache: apolloCache,
        link: uploadLink
    })
    const handleChange = props => event => {
        const value = event.target.value;
        if (props === 'languageSpoken') {
            let languages = [], id = [];
            for (let i = 0; i < value.length; i++) {
                languages.push(state.languageTypes.filter(language => language.name === value[i])[0])
            }
            id = languages.map(lang => lang.id);
            console.log(id);
            setState({ ...state, [props]: event.target.value, languageSpokenId: id });
        }
        else {
            setState({ ...state, [props]: event.target.value });
        }

        let errors = state.errors;
        errors.rate15 = ""; errors.rate30 = ""; errors.rate60 = ""
        switch (props) {
            case 'rate15':
                errors.rate15 =
                    value > 0 || value === ""
                        ? ''
                        : text.validation.positive;
                break;
            case 'rate30':
                errors.rate30 =
                    value > 0 || value === ""
                        ? ''
                        : text.validation.positive;
                break;
            case 'rate60':
                errors.rate60 =
                    value > 0 || value === ""
                        ? ''
                        : text.validation.positive;
                break;
            default:
                break;
        }
    }
    const handleChangeSelected = (event) => {
        setSelectedValue(event.target.value);
    };
    const validateForm = (errors) => {
        let valid = true;
        Object.values(errors).forEach(
            // if we have an error string set valid to false
            (val) => val.length > 0 && (valid = false)
        );
        return valid;
    }
    const checkRate = () => {
        if (state.rate15 || state.rate30 || state.rate60) {
            return true;
        }
        else {
            let errors = state.errors;
            errors.rate60 = text.validation.onlyOneRequired;
            setState({ ...state, errors: errors })
            return false;
        }
    }
    const handleSubmit = (event) => {
        event.preventDefault();
        setState({ ...state, loading: true });
        if (validateForm(state.errors) && checkRate()) {
            if (localStorage.QA_jwt) {
                const token = JSON.parse(localStorage.getItem('QA_jwt')).BearerTokenResponse.access_token;
                let gender = state.studentGenderTypes.filter(item => item.name == state.preferedGender);
                let age = state.studentAgeTypes.filter(item => item.name == state.preferedAge);
                $.ajax({
                    url: Url,
                    method: 'post',
                    headers: {
                        authorization: `Bearer ${token}`
                    },
                    data: {
                        query: EDIT_PROFILE,
                        variables: {
                            currency_type_id: state.currency,
                            courses_types_id: state.courses,
                            student_gender_types_id: gender[0].id,
                            student_age_types_id: age[0].id,
                            Spoken_Language_User: state.languageSpokenId,
                            rate_15_min: state.rate15,
                            rate_30_min: state.rate30,
                            rate_60_min: state.rate60,
                            public_rate_minutes_type_id: selectedValue,
                        }
                    },
                    dataType: 'json',
                    success: res => {
                        if (res.data.PersonEdit.Status.check) {
                            setState({ ...state, loading: false });
                            props.refetch();
                        }
                    },
                    cache: err => console.log(err)
                });
            }
        }
    }
    console.log(selectedValue);
    let pageText = text["instractor-profile"].create.requirements;
    if (!state.studentAgeTypes || !state.languageTypes || !state.studentGenderTypes) {
        return (<div>Loading...</div>)
    }
    else {
        return (
            <Container
                title={pageText.title}
                content={
                    <div className="mt-3 pb-3 text-lef">
                        <div>
                            <Loader
                                className="loading"
                                visible={state.loading}
                                type="TailSpin"
                                color="white"
                                height={50}
                                width={50}
                            />
                        </div>
                        <form
                            onSubmit={handleSubmit}>
                            {/* Language spoken */}
                            <Row className="m-0 mb-3 ">
                                <Col className="p-0">
                                    <InputLabel required>
                                        {pageText.languageSpoken}
                                    </InputLabel>
                                    <FormControl size="small" fullWidth>
                                        <Select
                                            id="demo-mutiple-checkbox"
                                            required
                                            multiple
                                            variant="outlined"
                                            value={state.languageSpoken}
                                            onChange={handleChange('languageSpoken')}
                                            renderValue={(selected) => selected.join(',')}
                                        >
                                            {state.languageTypes?.map((name) => (
                                                <MenuItem key={name.name} value={name.name}>
                                                    <Checkbox color="primary" checked={state.languageSpoken.indexOf(name.name) > -1} />
                                                    <ListItemText primary={name.name} />
                                                </MenuItem>
                                            ))}
                                        </Select>
                                    </FormControl>
                                </Col>
                            </Row>
                            {/* Preferred students */}
                            <Row className="m-0 mb-3">
                                {/* Preferred gender */}
                                <Col md={12} className="mb-2 p-0">
                                    <FormControl component="fieldset">
                                        <FormLabel required className="mb-1" component="legend">{pageText.preferedStudents}</FormLabel>
                                        <RadioGroup
                                            row
                                            required
                                            onChange={handleChange('preferedGender')}
                                            value={state.preferedGender}
                                            aria-label="preferedStudents"
                                            name="preferedStudents">
                                            {state.studentGenderTypes?.map(type =>
                                                <FormControlLabel
                                                    name="prefereGender"
                                                    value={type.name}
                                                    control={<Radio color="primary" />}
                                                    label={type.name}
                                                    labelPlacement="end"
                                                />
                                            )}
                                        </RadioGroup>
                                    </FormControl>
                                </Col>
                                {/* Preferred  age */}
                                <Col md="12" className="p-0">
                                    <FormControl component="fieldset">
                                        <FormLabel required className="mb-1" component="legend">{pageText.preferedAge}</FormLabel>
                                        <RadioGroup
                                            row

                                            value={state.preferedAge}
                                            onChange={handleChange('preferedAge')}
                                            aria-label="preferedAge"
                                            name="preferedAge">
                                            {state.studentAgeTypes?.map(type =>
                                                <FormControlLabel
                                                    name="preferedAge"
                                                    value={type.name}
                                                    control={<Radio color="primary" />}
                                                    label={type.name}
                                                    labelPlacement="end"
                                                />
                                            )}
                                        </RadioGroup>
                                    </FormControl>
                                </Col>
                            </Row>
                            {/* Courses */}
                            <Row className="m-0 mb-3">
                                <Col md={12} className="p-0">
                                    <InputLabel required id="courses" >
                                        {pageText.courses}
                                    </InputLabel>
                                    <FormControl size="small" fullWidth>
                                        <Select
                                            variant="outlined"
                                            native
                                            required
                                            name={pageText.courses}
                                            value={state.courses}
                                            onChange={handleChange('courses')}
                                        >
                                            <option value=""></option>
                                            {state.courseType?.map(course =>
                                                <option value={course.id}>{course.name}</option>
                                            )}
                                        </Select>
                                    </FormControl>
                                </Col>
                            </Row>
                            {/* curency */}
                            <Row className="m-0 mb-3">
                                <Col md={12} className="p-0">
                                    <InputLabel required id="courses" >
                                        {pageText.currency}
                                    </InputLabel>
                                    <FormControl size="small" fullWidth>
                                        <Select
                                            variant="outlined"
                                            native
                                            required
                                            name={pageText.courses}
                                            value={state.currency}
                                            onChange={handleChange('currency')}
                                        >
                                            <option value=""></option>
                                            {state.currencyTypes?.map(currency =>
                                                <option value={currency.id}>{currency.code}</option>
                                            )}
                                        </Select>
                                    </FormControl>
                                </Col>
                            </Row>
                            {/* Rate per minutes */}
                            <Row className="m-0 mb-3">
                                <Col md={12} className="p-0">
                                    <InputLabel required className="text-capitalize" id="courses" >
                                        {pageText.rate}
                                    </InputLabel>
                                    <p style={{ fontSize: '14px' }}>{pageText["rate-description"]}</p>
                                </Col>
                                <Col md={1} className="text-center p-0" >
                                    <Radio
                                        checked={selectedValue === '1'}
                                        onChange={handleChangeSelected}
                                        value="1"
                                        color="primary"
                                        name="radio-button-demo"
                                        inputProps={{ 'aria-label': 'rate15' }}
                                    />
                                </Col>
                                <Col md={5} className="pr-1 pl-0 ">
                                    <TextField
                                        disabled
                                        value="15 MIN"
                                        fullWidth
                                        className="bg-muted"
                                        size="small"
                                        variant="outlined"
                                    />
                                </Col>
                                <Col md={6} className="pr-0 pl-1 ">
                                    <TextField
                                        inputProps={{
                                            min: 1
                                        }}
                                        fullWidth
                                        className=""
                                        size="small"
                                        type="number"
                                        variant="outlined"
                                        value={state.rate15}
                                        onChange={handleChange('rate15')}
                                    />
                                    <div className="text-danger"><p className="mb-1">{state.errors.rate15}</p></div>
                                </Col>
                            </Row>
                            <Row className="m-0 mb-3">
                                <Col md={1} className="text-center p-0" >
                                    <Radio
                                        checked={selectedValue === '2'}
                                        onChange={handleChangeSelected}
                                        value="2"
                                        color="primary"
                                        name="radio-button-demo"
                                        inputProps={{ 'aria-label': 'rate30' }}
                                    />
                                </Col>
                                <Col md={5} className="pr-1 pl-0 ">
                                    <TextField
                                        disabled
                                        value="30 MIN"
                                        fullWidth
                                        className="bg-muted"
                                        size="small"
                                        variant="outlined"
                                    />
                                </Col>
                                <Col md={6} className="pr-0 pl-1 ">
                                    <TextField
                                        fullWidth
                                        type="number"
                                        inputProps={{
                                            min: 1
                                        }}
                                        className=""
                                        size="small"
                                        value={state.rate30}
                                        variant="outlined"
                                        onChange={handleChange('rate30')}
                                    />
                                    <div className="text-danger"><p className="mb-1">{state.errors.rate30}</p></div>
                                </Col>
                            </Row>
                            <Row className="m-0 mb-3">
                                <Col md={1} className="text-center p-0">
                                    <Radio
                                        checked={selectedValue === '3'}
                                        onChange={handleChangeSelected}
                                        value="3"
                                        color="primary"
                                        name="radio-button-demo"
                                        inputProps={{ 'aria-label': 'rate60' }}
                                    />
                                </Col>
                                <Col md={5} className="pr-1 pl-0 ">
                                    <TextField
                                        disabled
                                        value="60 MIN"
                                        fullWidth
                                        className="bg-muted"
                                        size="small"
                                        variant="outlined"
                                    />
                                </Col>
                                <Col md={6} className="pr-0 pl-1 ">
                                    <TextField
                                        fullWidth
                                        inputProps={{
                                            min: 1
                                        }}
                                        type="number"
                                        className=""
                                        size="small"
                                        value={state.rate60}
                                        variant="outlined"
                                        onChange={handleChange('rate60')}
                                    />
                                    <div className="text-danger"><p className="mb-1">{state.errors.rate60}</p></div>
                                </Col>
                            </Row>
                            {/* form errors */}
                            <div className="text-danger"><p className="mb-1">{state.errors.form}</p></div>
                            {/* back and save buttons */}
                            <Row className="m-0 align-items-center w-100 border-top pt-3">
                                <Col className="text-right p-0">
                                    <Button
                                        type="submit"
                                        className="btn-green pl-4 pr-4">
                                        {pageText.saveChanges}
                                    </Button>
                                </Col>
                            </Row>

                        </form>
                    </div>
                }
            />
        )
    }
}