import React from 'react';
import {Link} from 'react-router-dom';

import Container from './container.component';
import {Row , Col} from 'react-bootstrap';
import {Button} from '@material-ui/core';
import pp from '../../../images/intstractor-profile/pp.png'
import DateRangeIcon from '@material-ui/icons/DateRange';

import text from '../../../languages/english/translate.json';


export default function     Info(props) {
    
    let pageText = text['instractor-profile'].view;
    let userData = props.data;
    
    return(
        <Container >
            <Row className="align-items-center mb-4">
                <Col md={2} xs={12} className="text-center-responsive mb-2">
                    <img height="90px" width="90px" className=" rounded-circle" src={userData.profile_image? userData.profile_image.url :pp} alt="profile"/>
                </Col>
                <Col md={5} xs={12} className="text-center-responsive mb-2">
                    <h5 className="mb-1 text-capitalize">{userData.first_name +'  '+ userData.last_name}</h5>
                    <p style={{ fontSize : '14px'}} className="text-grey mb-0">{userData.email}</p>
                    {userData.date_of_birth &&
                        <p style={{ fontSize : '14px'}} className="text-grey mb-0">
                            <DateRangeIcon style={{height : '17px'}}/>
                            {userData.date_of_birth}
                        </p>
                    }
                </Col>
                <Col md={5} xs={12} className="text-center-responsive mb-2">
                    <Link className="border-0 btn-green rounded pr-4 pl-4" to='/editProfile'>Edit Profile</Link>
                </Col>
            </Row>
            <Row className="mb-3">
                <Col md={3} xs={6}  className="">
                    <article>
                        <h5 className="text-dark">{pageText.info.rate}</h5>
                        <p className="text-grey d-inline-block">
                            {userData.public_rate_minutes +' '+ userData.currency_type.code +' Per ' }  
                        </p>
                        <p className='text-grey d-inline-block pl-1'>{userData.public_rate_minutes_type_id === 1 && '15 min'}</p>
                        <p className='text-grey d-inline-block pl-1'>{userData.public_rate_minutes_type_id === 2 && '30 min'}</p>
                        <p className='text-grey d-inline-block pl-1'>{userData.public_rate_minutes_type_id === 3 && '60 min'}</p>

                    </article>
                </Col>
                <Col md={3} xs={6} className=" border-left border-right">
                    <article>
                        <h5 className="text-dark">{pageText.info.courses}</h5>
                        <p className="text-grey">{userData.courses_type.name}</p>
                    </article>
                </Col>
                <Col md={4} xs={12} className="">
                    <article>
                        <h5 className="text-dark">{pageText.info.yearsOfExperience}</h5>
                        <p className="text-grey">{userData.years_of_experience}</p>
                    </article>
                </Col>
            </Row>
        </Container>
    )
}