import React , {useState} from 'react';
import $ from 'jquery';
import Loader from 'react-loader-spinner';


import {DELETE_SESSION} from './api';
import {Url} from '../../../url'

import Pp from '../../../images/intstractor-profile/pp.png';
import DeleteOutlineIcon from '@material-ui/icons/DeleteOutline';
import link from '../../../images/icons/link.svg'



export default function SessionsCardPast(props){
    const [state , setState] = useState({
        loading : false
    });
    let data = props.data;
    
    const handleDeleteSession = () => {
        setState({...state , loading : true})
        if (localStorage.QA_jwt) {

            const token = JSON.parse(localStorage.getItem('QA_jwt')).BearerTokenResponse.access_token;
            let id = data.id
            $.ajax({
                url: Url,
                method: 'post',
                headers: {
                    authorization: `Bearer ${token}`
                },
                data: {
                    query: DELETE_SESSION,
                    variables : {id : id}
                },
                dataType: 'json',
                success: res => {
                    console.log(res);
                    if (res.data.sessionsDelete.Status.check === true) {
                        props.getPastSessions();
                        setState({...state , loading : false})

                    }
                },
                cache: err => console.log(err)
            });
        }
    }
    return(
    <section className="col-md-3 col-6 mb-3">
        <div>
            <Loader
                className="loading"
                visible={state.loading}
                type="TailSpin"
                color="white"
                height={50}
                width={50}
            />
        </div>
        <figure className="mb-1">
            <img src={data.instructor.profile_image? data.instructor.profile_image.url : Pp} style={{height : '206px' }} alt="instractor" className="rounded img-fluid"/>
        </figure>
        <article className="row m-0 align-items-center">
            <div className="col-10 pl-0">
                <p className="mb-0">{data.instructor.first_name} {data.instructor.last_name}</p>
                <p style={{fontSize : '14px'}} className="mb-0 text-green d-inline-block pr-3">{data.instructor.courses_type.name}</p>
                {data.count_materials > 0 &&
                    <p className="text-green mb-0   d-inline-block">
                       <img src={link} alt="clock"/><span>{data.count_materials}</span>
                    </p>
                }
            </div>
            <button onClick={handleDeleteSession} className="col-2 text-right p-0 border-0 bg-transparent"><DeleteOutlineIcon className="text-danger"/></button>
        </article>
    </section>
    )
}