import React from 'react';
import {Link} from 'react-router-dom';
import {Rating} from '@material-ui/lab';

// styles
import flag from '../../../images/search/egypt.svg';
import user from '../../../images/search/user.png';

export default function SearchCard(props){
    let userdata = props.data;
    console.log(userdata.id);
    return(
        <Link className="col-md-4 pb-4" to={{
            pathname : '/instractorProfile',
            state : userdata.id
        }}>
            <section className="border rounded p-2">
                <figure className="text-center mb-1">
                    <img width="130" height="130" className="rounded-circle" src={userdata.profile_image? userdata.profile_image.url : user} alt="instractor"/>
                </figure>
                <article className="text-center">
                    <h6 style={{fontSize : '16px'}}>{userdata.first_name} {userdata.last_name}</h6>
                    <p style={{fontSize : '14px'}} className="text-green mb-0">{userdata.courses_type?.name}</p>
                    {userdata.country ?               
                    <figure className="mb-0">
        
                        <img className="img-fluid pr-2" width="24px" alt="flag" src={userdata.country?.flag}/>
                        <p style={{fontSize : '14px'}} className="d-inline text-grey ">{userdata.country?.name}</p>
                    </figure>
                    : <div className="mb-4"></div>
                    }
                    <Rating size="small"  defaultValue={2} />
                </article>
            </section>
        </Link>
    )
}