import React , {useState , useEffect} from 'react';
import $ from 'jquery';

import {FINDE_INSTRCTOR} from '../apis/apis';
import {Url} from '../../../url';
import Summary from '../../instractor/view-profile/summary.component';
import Certificates from '../../instractor/view-profile/certificates.component';
import Info from './info.component';
import TodaySession from '../../session-of-today/today-session.component';
import RecomendedArticles from '../../recomended-articles';
import {Row , Col } from 'react-bootstrap';
import Skeleton from '@material-ui/lab/Skeleton';

import text from '../../../languages/english/translate.json';



export default function InstractorProfile(props){

    const [userData , setUserData] = useState();
    let pageText = text.instractorHome.dashboard;
    let id = props.location?.state;
    console.log(id);

    useEffect(()=>{
        console.log(id);
        if(id){
            console.log("in")
            localStorage.setItem('QA-id' , JSON.stringify(id))
            geStudentData(id)
        }
        else{
            console.log("else")
            let data = JSON.parse(localStorage.getItem('QA-id'))
            geStudentData(data)
        }
    }, [id]);
    const geStudentData = (id) => {
        if (localStorage.QA_jwt) {
            const token = JSON.parse(localStorage.getItem('QA_jwt')).BearerTokenResponse.access_token;
            //get user data
            $.ajax({
                url: Url,
                method: 'post',
                headers: {
                    authorization: `Bearer ${token}`
                },
                data: {
                    query: FINDE_INSTRCTOR,
                    variables: { id : id}
                 },
                dataType: 'json',
                success: res => {
                    console.log(res);
                    if (res.data.InstructorFind) {
                        setUserData(res.data.InstructorFind)
                    }
                },
                cache: err => console.log(err)
            });
        }
    }
    console.log(userData);
    if(!userData){
        return(
            <main className="container mt-4 mb-4">
                <Row >
                    <Col className="mt-3" md={8}>
                        <Skeleton variant="rect" height={530} />
                    </Col>
                    <Col className="mt-3" md={4}>
                        <Skeleton variant="rect" height={230} />
                    </Col>
                </Row>
            </main>
        )
    }
    else{
        return(
            <main className="container mt-4 mb-4">
                <Row>
                    <Col className="mt-3" md={8}>
                        <Info data={userData}/>
                        <div className="mt-4"></div>
                        <Summary data={userData}/>
                        <div data={userData} className="mt-4"></div>
                        <Certificates data={userData} />
                    </Col>
                    <Col md={4}>
                        <TodaySession />
                        <RecomendedArticles />
                    </Col>
                </Row>
            </main>
        )
    }
}