import React from 'react';

// styles
import { Container, Row, Col, ProgressBar } from 'react-bootstrap';


export default function ProfileStrength(props) {
    return (
        <>
            <Container fluid className="profileStrength  mb-3 p-0">
                <div className="rounded border">
                    <Row className="m-0 p-3">
                        <Col xs={12} className="p-0 mb-4">
                            <h6 className="bold capitablize">{props.title}</h6>
                            <p style={{fontSize : "12px"}}>{props.description && props.description}</p>
                        </Col>
                        <Col xs={12} className="p-0 mb-2">
                            <ProgressBar style={{borderRadius : "10px"}} color="primary" now={props.percentage} />
                        </Col>
                        <Col md={8} className="text-left p-0">
                            <p style={{fontSize : "12px"}} className="mb-0 mt-1">{props.strength} <span className="text-orange">{props.percentage} %</span></p>
                        </Col>
                        <Col md={4} className="text-right p-0">
                            <p style={{fontSize : "12px"}} className="mb-0 mt-1 text-green">{props.complete}</p>
                        </Col>
                    </Row>
                </div>
            </Container>
        </>
    )
}