export const GET_STUDENT_SESSIONS =   `
query($orderBy : SessionsSortOrder){
  meInstructors{
    id
sessionsGet(Inputs : {
  orderBy : $orderBy
}){
  data{
    id
    instructor{
      courses_type {id name}
    }
    student{
      id 
      first_name
      last_name
      profile_image {url}
    }
    course_type{id name }
    rate_minutes_type{id name}
    date
    time
    to_time
    DateTime
    can_start
  }
}
}
  rateMinutesTypes{
     id
    name
  }
  courseType{
    id name
  }
}
`
export const DELETE_SESSION = `mutation( $id : Int!){
  sessionsDelete(data: {
    id : $id
  }){
    Status{
      check
    }
  }
}`
export const SESSION_CANCEL =   `
mutation( $id : Int !){
  sessionsCancel(data: {
    id : $id
  }){
    Status{
      check
      message
    }
    session{
      id
    }
  }
}`

export const SESSION_RESCEDUAL = `
mutation($id : Int!  $course_type_id : Int $date : Date $time : Time ){
  sessionsEdit(data: {
    id : $id
    course_type_id : $course_type_id
    date : $date
     time : $time
  }){
    Status{
      check
    }
    session{
      id
      date
      time
    }   
  }
}`