import React ,  {useEffect , useState} from 'react';
import $ from 'jquery';

import BalanceActivity from './balance-activity.component';
import TotalEarnings  from './total-earnings.component';
import {EARNINGS} from './api';
import {Url} from '../../../url';


export default function Index() {
    const [earnings , setEarnings] = useState();
    const [data , setData] = useState();
    const [payouts , setPayouts] = useState();

    const geStudentData = () => {
        if (localStorage.QA_jwt) {
            const token = JSON.parse(localStorage.getItem('QA_jwt')).BearerTokenResponse.access_token;
            //get user data
            $.ajax({
                url: Url,
                method: 'post',
                headers: {
                    authorization: `Bearer ${token}`
                },
                data: {
                    query: EARNINGS
                },
                dataType: 'json',
                success: res => {
                    if(res.data.meInstructors.sessionsCanEarning.data){
                        setEarnings(res.data.meInstructors.sessionsCanEarning.data);
                        setData(res.data.meInstructors);
                        setPayouts(res.data.InstructorGetTransfers?.data);
                    }
                },
                cache: err => console.log(err)
            });
        }
    }
    useEffect(() => {
        geStudentData();
    },[]);
    return (
        <main className="container mt-5 ">
            <div className="row">
                <div className="col-md-7">
                    {earnings ?
                        <BalanceActivity data={ earnings}/>
                        : "Loading..."
                    }
                </div>
                <div className="col-md-5">
                    {data &&
                        <TotalEarnings 
                        totalEarnings={ data.total_earning_counter} 
                        currentBalance={data.current_balance}
                        currency={data.currency_type?.symbol}
                        payoutList = {payouts && payouts}
                        />
                    }
                </div>
            </div>
        </main>
    )
}
