import React, { Component } from 'react';
import $ from 'jquery';
import { withRouter } from 'react-router';

import {GET_INSTRACTOR_SESSIONS , GET_INSTRACTOR_SESSIONS_PER_MONTH} from '../../../apis/apis'; 
import {Url} from '../../../../../url';
import PopUp from '../../../../pop-up/pop-up.component'
// styles 
import { Row, Col , Table } from 'react-bootstrap';
import { Button, TextField } from '@material-ui/core';
import Loader from 'react-loader-spinner';
import NavigateNextIcon from '@material-ui/icons/NavigateNext';
import NavigateBeforeIcon from '@material-ui/icons/NavigateBefore';
import EventNoteIcon from '@material-ui/icons/EventNote';
import './calendar.style.css';

// language for test
import text from '../../../../../languages/english/translate.json';
import { CodeSharp, CompassCalibrationOutlined } from '@material-ui/icons';

class Calendar extends Component {
    constructor(props) {
        super(props);
        let month = new Date().getMonth();
        let year = new Date().getFullYear();
        let day = new Date().getDate();
        this.state = {
            alertMessage : false,
            selectedRow : 1,
            loading : false,
            week: 1,
            year: year,
            month: month,
            day : day ,
            date : year+'-'+(month+1),
            notAvailableSession : [],
            availableSlot : []
        }
    }
    componentDidMount() {
        this.showCalendar(this.state.year, this.state.month);
        this.selectToday();
        this.handleRowSelect();
        this.handelCellSelect(); 
    }

    times = ["8AM", "9AM", "10AM", "11AM", "12PM", "1PM","2PM", "3PM", "4PM", "5PM", "6PM", "7PM",
            "8PM", "9PM", "10PM","11PM"];
    months = ["Jan", "Feb", "Mar", "Apr", "May", "Jun", "Jul", "Aug", "Sep", "Oct", "Nov", "Dec"];
    // calendar functions
    // get days of a month
    daysInMonth(iYear, iMonth) {
        return 32 - new Date(iYear, iMonth, 32).getDate();
    }
    getFirstDayOfWeek(row){
        let day = 0;
        row = row + 2;
        day = document.querySelector('.month-table tr:nth-child('+row+') td').innerHTML
        if(day){
            return day;
        }
        else{
            return 1;
        }
    }
    selectToday(){
        let cells , that = this;
        let rows = document.querySelectorAll('.month-table tr');
        rows && rows.forEach(row=>{
            cells = row.querySelectorAll('td')
            cells.forEach(cell=>{
                if(parseInt(cell.innerHTML) === this.state.day){
                    cell.classList.add('bg-lightGreen');
                    that.setState({...this.state , selectedRow : row.rowIndex});
                    that.getCalendarData(row.rowIndex);

                }
            })
        })

    }
    handleNext() { 
        let month = this.state.month, year = this.state.year;
        let currentYear = (month === 11) ? year + 1 : year;
        let currentMonth = (month + 1) % 12;
        let date = currentMonth.toString().length===1 && parseInt(currentMonth) !== 9 ? currentYear+"-"+0+(currentMonth+1) : currentYear+"-"+(parseInt(currentMonth)+1);
        this.setState({...this.state , year : currentYear , month : currentMonth , date : date})
        this.showCalendar(currentYear, currentMonth);
        this.showClendarBerWeek(1);
        this.handleRowSelect();
        this.handelCellSelect();  

    }
    handlePrevious() {
        let month = this.state.month, year = this.state.year;
        let currentYear = (month === 0) ? year - 1 : year;
        let currentMonth = (month === 0) ? 11 : month - 1;
        let date = currentMonth.toString().length===1 && parseInt(currentMonth) !== 9 ? currentYear+"-"+0+(currentMonth+1) : currentYear+"-"+(parseInt(currentMonth)+1);
        this.setState({...this.state , year : currentYear , month : currentMonth ,  date : date})
        this.showCalendar(currentYear, currentMonth);
        this.showClendarBerWeek(1);
        this.handleRowSelect();
        this.handelCellSelect(); 
    }
    handleChangeDate(e){
        let year = e.target.value.substring(0,4);
        let month = parseInt(e.target.value.substring(5,7));
        month = month[0] === 0 ? month[1]-1 : month-1;
        this.setState({...this.state , year : year , month : month , date : e.target.value});
        this.showClendarBerWeek(1);
    }
    convertTimeFrom12To24(time) {
        if (time?.includes('AM')) {
            // from 1 to 9 AM
            if (parseInt(time.substring(0, 2)) <= 9) {
                return '0' + time.replace('AM', ':00:00')
            }
            // 12 AM
            else if (parseInt(time.substring(0, 2)) === 12) {
                return '00:00:00';
            }// 10 and 11 AM 
            else {
                return time.replace('AM', ':00:00')
            }
        }
        else if(time?.includes('PM')) {
            // 12 PM
            if (parseInt(time.substring(0, 2)) === 12) {
                let parseTime = parseInt(time.substring(0, 2));
                return parseTime + ':00:00';
            }
            // from 1 to 11 PM
            else {
                let parseTime = parseInt(time.substring(0, 2)) + 12;
                return parseTime + ':00:00';
            }
        }
    }
    convertTimeFrom24To12(time){
        let convertedTime = time.substring(0,2);

        if(convertedTime > 12 )
            convertedTime = (convertedTime-12)+'PM' 

        else if(parseInt(convertedTime) === 12 )
            convertedTime = convertedTime+'PM' 

        else convertedTime =parseInt(convertedTime[0]) === 0? convertedTime[1]+'AM' :  convertedTime+'AM';

        return convertedTime;
    }// end of calendar functions
    //show calendar of a month
    showCalendar(year, month) {
        // clear week table
        if (document.querySelector('.month-table tr')) {
            document.querySelectorAll('.month-table tr').forEach(tr => {if(tr.rowIndex > 0)tr.remove()});
        }
        let firstDay = new Date(year, month).getDay();

        let date = 1;
        // i represents a counter for 5 weeks in the month
        for (let i = 0; i < 5; i++) {
            let row = document.createElement("tr"), cell, cellText;
            // j represents a counter for 7 days in the week
            for (let j = 0; j < 7; j++) {
                if (i === 0 && j < firstDay) {
                    cell = document.createElement("td");
                    cellText = document.createTextNode("");
                    cell.appendChild(cellText);
                    row.appendChild(cell);
                }
                else if (date > this.daysInMonth(year, month)) {
                    break;
                }
                else {
                    cell = document.createElement("td");
                    cellText = document.createTextNode(date);
                    cell.appendChild(cellText);
                    row.appendChild(cell);
                    date++;
                }
            }
            document.querySelector('.month-table').append(row);
        }
        this.handleCellSelectFromLocalStorage();
        this.disaplePastDays(month , year);
        this.getAvailableDays(month , year);
    }
    // make past days disappled 
    disaplePastDays = (selectedMonth , selectedYear) => {
        let this_month = new Date().getMonth();
        let this_year = new Date().getFullYear();
        let today = new Date().getDate();
        
        document.querySelectorAll('.month-table tr').forEach(tr => {
            tr.querySelectorAll('td').forEach(td=>{
                let dayInCalendar = parseInt(td.innerHTML);
                // in case the two dates in the same year
                if( selectedYear == this_year){
                    // in case same month
                    if( selectedMonth == this_month && dayInCalendar < today){
                        td.classList.add('text-muted');
                    }else if (selectedMonth < this_month){
                        td.classList.add('text-muted');
                    }
                }
                else if(selectedYear < this_year){
                    td.classList.add('text-muted');
                }
            })
        });

    }
    getAvailableDays = (month , year , row = this.state.selectedRow) =>{
        let that = this;
        let firstDay = that.getFirstDayOfWeek(row);
        let id =  this.props.location.search.replace("?" , "");
        if(firstDay){
            let startingDate = year+'-'+(month+1)+'-'+firstDay;
            const token = (localStorage.getItem('QA_jwt')) ? JSON.parse(localStorage.getItem('QA_jwt')).BearerTokenResponse?.access_token : JSON.parse(localStorage.getItem('QA_jwt')).access_token ;
            if (!token) {
                that.props.history.push({
                    pathname: '/login'
                })
            }
            else{
                // that.setState({...this.state , loading : true})
                $.ajax({
                    url: Url,
                    method: 'post',
                    headers: {
                        authorization: `Bearer ${token}`
                    },
                    data: {
                        query: GET_INSTRACTOR_SESSIONS_PER_MONTH,
                        variables: {
                            id : id ,
                            afterDate : startingDate
                        }
                    },
                    dataType: 'json',
                    success: res => {
                            // that.setState({...this.state , loading : false})
                            if(res && res.data?.InstructorFind)
                            {
                                if(res.data.InstructorFind?.sessionsByUnits){
                                     that.filterDays(res.data.InstructorFind.sessionsByUnits , row);
                                }
                            }
                            else{
                                // if this token belongs to subinstractor
                                // if the user clicks on skip for now
                                // that.setState({...that.state , showAlert : true , alertMessage : "You Have to complete your profile first" })
                            }
                    },
                    cache: err => console.log(err)
                });
            }
        }
    }
    // show calendar ber week
    showClendarBerWeek(week, times = this.times ) {
        if(week !== 0){
            // clear week table
            if (document.querySelector('.week-table tr')) {
                document.querySelectorAll('.week-table tr').forEach(tr => tr.remove());
            }

            let i = 2 + week;
            let cell, cellText;
            cell = document.createElement("td");
            cellText = document.createTextNode("");
            cell.appendChild(cellText);

            let row = document.querySelector('.month-table  tr:nth-child(' + i + ')');
            let rowCopy = row.cloneNode(true);
            rowCopy.prepend(cell);
            let arr = [];
            rowCopy.querySelectorAll('td').forEach(td=>{
                    arr.push(td.innerHTML)
            })
               console.log(arr)
            // document.querySelector('.week-table').append(rowCopy);
            let counter = 0;
            document.querySelectorAll('.week-table thead td').forEach(td=>{

                td.querySelector('.day').innerHTML = arr[counter] ? arr[counter] : ''
                counter++;
            })

            for (let i = 0; i < times.length; i++) {
                let row = document.createElement("tr");
                for (let j = 0; j < 8; j++) {
                    if (j === 0) {
                        cell = document.createElement("td");
                        cellText = document.createTextNode(times[i]);
                        cell.append(cellText);
                        row.append(cell);
                    }
                    else 
                    {
                        cell = document.createElement("td");
                        cellText = document.createTextNode("");
                        cell.append(cellText);
                        row.append(cell);
                    }

                }
                 document.querySelector('.week-table').append(row);

            }
        }
    }
    // when user select a week from the months calendar
    handleRowSelect() {
        let that = this;
        const row = document.querySelectorAll(".month-table tr");
        for (let i = 0; i < row.length; i++) {
            row[i].addEventListener("click", function () {
                let copyRow = i;
                that.setState({...that.state , selectedRow : copyRow})
                that.getCalendarData();
            });
        }

    }
    // when user select a day and time from weeks calendar 
    handelCellSelect() {
        let that = this;
        const cell = document.querySelectorAll(".week-table td");

        for (let i = 0; i < cell.length; i++) {
            cell[i].addEventListener("click", function () {
                //check if there is a reserved session or not
                if(cell[i].getAttribute('data-id') && cell[i].classList.contains('bg-white')){
                    // handle show data of available slots
                    let id = cell[i].getAttribute('data-id');
                    let session = that.state.availableSlot.filter(slot => parseInt(slot.id) === parseInt(id))[0];
                    that.handleReserveSession(session)
                }
                // handle show  reserved session
                else if(cell[i].getAttribute('data-id') && cell[i].classList.contains('bg-green')){
                       // handle show data of reserved session
                       let id = cell[i].getAttribute('data-id');
                       let slot = that.state.availableSlot.filter(slot => parseInt(slot.id) === parseInt(id))[0];
                       that.handleshowAvailableSlotDetails(slot);
                }
                else{   
                    console.log("not available right Now");
                }
            })
        }
    }
    // select session from local storage
    handleCellSelectFromLocalStorage=(sessions)=>{
          const rows = document.querySelectorAll(".week-table tr");
            rows.forEach(row=>{    
                row.querySelectorAll('td').forEach(cell => {
                   if(cell.getAttribute('data-id') ){
                    let sessionId = cell.getAttribute('data-id');

                    let reservedSessionFromLocalStorage = JSON.parse((localStorage.getItem('arryOfReservedSession')));
                    reservedSessionFromLocalStorage && reservedSessionFromLocalStorage.map(ele=>{
                        if(parseInt(sessionId) === parseInt(ele.id)){
                            cell.classList.add('bg-green');
                            cell.classList.remove('bg-white');
                        }
                    })
                   }   
                })
            });
            // let x = $('.month-table td[data-id=825]');
            // console.log(x);
            // x.classList && x.classList.add('bg-green')
            const monthRows = document.querySelectorAll(".month-table tr");
            // monthRows.forEach(row=>{    
            //     row.querySelectorAll('td').forEach(cell => {
            //        if(cell.getAttribute('data-id') ){
            //         let sessionId = cell.getAttribute('data-id');

            //         let reservedSessionFromLocalStorage = JSON.parse((localStorage.getItem('arryOfReservedSession')));
            //         console.log(reservedSessionFromLocalStorage)
            //         reservedSessionFromLocalStorage && reservedSessionFromLocalStorage.map(ele=>{
            //             console.log(ele)
            //             if(parseInt(sessionId) === parseInt(ele.id)){
            //                 cell.classList.add('bg-green');
            //                 // cell.classList.add('text-white');
            //                 cell.classList.remove('bg-white');
            //             }
            //         })
            //        }   
            //     })
            // });

            clearTimeout();
    }
    // reserve a slot popUp
    handleReserveSession(session){
        if(session){
            // let date =  this.state.year +"-"+ (this.state.month + 1)+"-"+ day;
            this.props.reserveSession(session)
        }
    }
    // get all reserved  sessions 
    getCalendarData (row = this.state.selectedRow) {
        let that = this;
        let firstDay = that.getFirstDayOfWeek(row);
        let id =  this.props.location.search.replace("?" , "");
        if(firstDay){
            let startingDate = that.state.year+'-'+(that.state.month+1)+'-'+firstDay;
            const token = (localStorage.getItem('QA_jwt')) ? JSON.parse(localStorage.getItem('QA_jwt')).BearerTokenResponse?.access_token : JSON.parse(localStorage.getItem('QA_jwt')).access_token ;
            if (!token) {
                that.props.history.push({
                    pathname: '/login'
                })
            }
            else{
                that.setState({...this.state , loading : true})
                $.ajax({
                    url: Url,
                    method: 'post',
                    headers: {
                        authorization: `Bearer ${token}`
                    },
                    data: {
                        query: GET_INSTRACTOR_SESSIONS,
                        variables: {
                            id : id ,
                            afterDate : startingDate
                        }
                    },
                    dataType: 'json',
                    success: res => {
                            that.setState({...this.state , loading : false})
                            if(res && res.data?.InstructorFind)
                            {
                                if(res.data.InstructorFind?.sessionsByUnits){
                                    that.filterCalendarData(res.data.InstructorFind.sessionsByUnits , row);
                                    that.handleCellSelectFromLocalStorage();
                                }
                            }else{
                                // if this token belongs to subinstractor
                                // if the user clicks on skip for now
                                // that.setState({...that.state , showAlert : true , alertMessage : "You Have to complete your profile first" })
                            }
                    },
                    cache: err => console.log(err)
                });
            }
        }
    }
    //(done) split data into available  and not available session
    filterCalendarData(data, row) {
        let that = this, availableSlot = [], notAvailableSession = [];
       data && data.filter(reserved => {
            if (reserved.sessions.length > 0) {
                if (reserved.sessions.student) {
                        reserved.sessions.forEach(session => notAvailableSession.push(session))
                }
                else {
                    reserved.sessions.forEach(session => availableSlot.push(session))
                }
            }
        })
        this.setState({...this.state , notAvailableSession :  notAvailableSession, availableSlot :  availableSlot});
        that.showClendarBerWeek(row, that.times)
        that.handelCellSelect();
        this.handleAvailableSlot();
    }
    // filter availeble  and not available days
    filterDays(data, row) {
        let that = this, availableDays = [];
       data && data.filter(reserved => {
            if (reserved.sessions.length > 0) {
                if (!reserved.sessions.student) {
                    reserved.sessions.forEach(session => availableDays.push(session))
                }
            }
        })
        this.showAvailableDays(availableDays);
        // this.setState({...this.state , notAvailableDays :  notAvailableDays, availableDays :  availableDays});
  
    }
    showAvailableDays(availableSessions){
        let selectedMonth = ((this.state.month+1) <= 9)? "0"+(this.state.month+1) : (this.state.month+1)

        availableSessions.length > 0 && 
            availableSessions.forEach(session=>{
                let monthOfAvSession = session.date.substring(5,7)
                // make sure that the action will fired in same selected month on month calendar
                if(monthOfAvSession == selectedMonth){
                    let dayOfAvSession = session.date.substring(8,10);
                    document.querySelectorAll('.month-table tr').forEach(tr=>{
                        tr.querySelectorAll('td').forEach(td=>{
                            let tdInnerHtml = parseInt(td.innerHTML) <= 9 ?  "0"+td.innerHTML : td.innerHTML
                            if(tdInnerHtml == dayOfAvSession){
                                td.classList.add('bg-white');
                                td.setAttribute('data-id',session.id);
                            }
                        })
                    })
                }
            })
    }
    // handle show reserved slots and sessions on week calendar
    getCellByDayAndTime(day,time){
        let rows = document.querySelectorAll('.week-table tr');
        let rowIndex ,  columnIndex , cell ;

        document.querySelectorAll('.week-table thead td').forEach(td=> {
            if( !isNaN(parseInt(td.querySelector('.day').innerHTML))  && parseInt(td.querySelector('.day').innerHTML) === parseInt(day)){
                columnIndex = $(td).index();
            }
        });
         rows.forEach(row => {
            if(row.querySelector('td').innerHTML === time){
                rowIndex = row.rowIndex;
            }
        })
        rowIndex = rowIndex + 3;
        columnIndex = columnIndex + 1;
        if(rowIndex && columnIndex){
            cell = document.querySelector('.week-table tr:nth-child('+rowIndex+') td:nth-child('+columnIndex+') ');
        }
        return  cell;

    }
    // handle data before showing reserved slots and sessions on week calendar
    handleAvailableSlot(){
        let that = this;
        let notAvailableSession = this.state.notAvailableSession;
        let availableSlot = this.state.availableSlot;
        availableSlot && availableSlot.map(slot=>{
            let day , time , cell;

            time = that.convertTimeFrom24To12(slot.time);
            day = slot.date.substring(8,10) <= 9 ? slot.date.substring(9,10) : slot.date.substring(8,10);
            console.log(day , time);
            cell = that.getCellByDayAndTime(day,time);
            if(cell){
                cell.setAttribute('data-id',slot.id);
                cell.classList.add('bg-white');
            }
        })
        notAvailableSession && notAvailableSession.map(slot=>{
            let day , time , cell;

            time = that.convertTimeFrom24To12(slot.time);
            day = slot.date.substring(8,10) <= 9 ? slot.date.substring(9,10) : slot.date.substring(8,10);

            cell = that.getCellByDayAndTime(day,time);
            if(cell){
                cell.setAttribute('data-id',slot.id);
                cell.classList.add('bg-light');
            }
        })
    }
    // show slot details popUp
    handleshowAvailableSlotDetails(session){
        if(session){
            this.props.showSessionDetails(session)
        }
    }
    hendleCloseAlert = () => this.setState({...this.state , showAlert : false});
    handleCompleteProfile = () => console.log("save");
    handleCloseMonthTable = () =>  this.setState({ ...this.state, showMonthTable : false })
    render() {
        let data = text.instractorHome.dashboard;
        let pageText = text.student.instractorProfile.reserveSession;
        return (
            <div className="mt-3 calendar calendar-student">
                <div>
                    <Loader
                        className="loading"
                        visible={this.state.loading}
                        type="TailSpin"
                        color="white"
                        height={50}
                        width={50}
                    />
                </div>
                <Row className=" m-0 ">
                    <Col lg={4}  className="">
                        <div className=" p-0 bg-transperant">
                            <Row className="m-0 mb-2 d-none">
                                <Col className="p-0">
                                    <TextField  value={this.state.date}  fullWidth onChange={e=>this.handleChangeDate(e)} type="month"/>
                                </Col>
                            </Row>
                            <Row className="m-0 bg-grey align-items-center  text-center  bold  mb-0  pb-2">
                                <Col xs={2} className=" text-left p-0">
                                    <button className="border-0  bg-light rounded w-100" onClick={e=>this.handlePrevious()}><NavigateBeforeIcon /></button>
                                </Col>
                                <Col xs={8} className="p-0"><h5 style={{fontSize : '14px'}} className="mb-0 month-table-header"> {this.months[this.state.month]} {this.state.year} </h5></Col>
                                <Col xs={2} className="text-right p-0">
                                    <button className="border-0  bg-light rounded w-100" onClick={e=>this.handleNext()}><NavigateNextIcon /></button>
                                </Col>
                            </Row>
                            <Table responsive striped bordered 
                                style={{fontSize : '12px'}}
                                className="text-center month-table mb-0" size="md">
                                <thead className="bg-light bold">
                                    <tr>
                                        <td>Su</td>
                                        <td>Mo</td>
                                        <td>Tu</td>
                                        <td>We</td>
                                        <td>Th</td>
                                        <td>Fr</td>
                                        <td>Sa</td>
                                    </tr>
                                </thead>
                                <tbody className="">
                                </tbody>
                            </Table>
                            <div className="w-100 mt-3">
                                <Button 
                                    className="btn-green w-100 mb-2"
                                    disabled={this.props.reservedSession.length > 0 ?false:true}
                                    onClick={e=>{
                                        window.scrollTo({ top: 0, behavior: 'smooth' });
                                        this.props.history.push({
                                            pathname : '/checkOut',
                                            state : this.props.reservedSession
                                        })
                                    }}>{pageText.completeBtn}</Button>
                                <p style={{fontSize : '12px'}} className="text-center text-grey">{pageText.completeDescription}</p>
                            </div>
                        </div>
                    </Col>
                    <Col lg={8} className="">
                        <div 
                            className="border-top border-bottom " 
                            style={{ maxHeight: '18.5rem', overflow: 'auto', scrollBehavior: 'smooth' }}
                        >
                            <Table  responsive className="text-center week-table border-0 p-0 m-0" bordered size="md">
                                <thead  style={{fontSize : '14px'}}  className="bg-light text-grey bold">
                                    <td><span className="day pr-1"></span>Time</td>
                                    <td><span className="day pr-1"></span>Sun</td>
                                    <td><span className="day pr-1"></span>Mon</td>
                                    <td><span className="day pr-1"></span>Tus</td>
                                    <td><span className="day pr-1"></span>Wed</td>
                                    <td><span className="day pr-1"></span>Thu</td>
                                    <td><span className="day pr-1"></span>Fri</td>
                                    <td><span className="day pr-1"></span>Sat</td>
                                </thead>
                                <tbody  className="text-grey bg-light">
                                </tbody>
                            </Table>
                        </div>
                        <Row className="row m-0 mt-2 mb-3 align-items-center">
                            <Col lg={4} className="d-flex pl-0 align-items-center">
                                <div 
                                    className="bg-white border mr-2 d-inline-block text-center" 
                                    style={{height : '23px' , width : '25px'}}
                                ></div>
                                <p className="mb-0 d-inline-block">{pageText.availableSession}</p>
                            </Col>
                            <Col lg={4} className="d-flex pl-0 align-items-center">
                                <div 
                                    className=" border mr-2 d-inline-block text-center" 
                                    style={{height : '23px' , width : '25px' , backgroundColor : '#F5F6FA'}}
                                ></div>
                                <p className="mb-0 d-inline-block">{pageText.reservedSession}</p>
                            </Col>
                            <Col lg={4} className="d-flex pl-0 align-items-center">
                                <div 
                                    className="bg-green border mr-2 d-inline-block text-center" 
                                    style={{height : '23px' , width : '25px'}}
                                ></div>
                                <p className="mb-0 d-inline-block">{pageText.yourReservations}</p>
                            </Col>
                        </Row>
                        {this.props.reservedSession.length > 0 &&
                            <Row className="row m-0 mt-2 pt-3 pb-3  bg-shineGreen align-items-center">
                                <Col lg={12} className="d-flex pl-0 align-items-center">
                                    <EventNoteIcon className="text-green ml-2"/>
                                    <p style={{fontSize : '12px'}} className="mb-0 d-inline-block pl-2 bold text-green">{pageText.daysSelected}</p>
                                </Col>
                                {this.props.reservedSession.map(ele=>
                                    { return(
                                              <>
                                                <Col sm={4} className="text-green ml-4">
                                                    <p className="mb-0" style={{fontSize : '14px'}}>{ele.date}</p>
                                                </Col>
                                                <Col sm={7} className="text-green ml-4">
                                                    <p className="mb-0" style={{fontSize : '14px'}}>
                                                        <span className="pr-2">From</span>
                                                        {ele.time}
                                                        <span className="pl-2 pr-2">To</span>{ele.to_time}</p>
                                                </Col>
                                            </>
                                        )
                                    }
                                )}
                            </Row>
                        }
                    </Col>
                </Row>
            </div>
        )
    }
}
export default (withRouter(Calendar));