import React from 'react';

import icon from '../../../images/student/icon.svg';

export default function Analysis() {
    return (
        <>
            <section className="shadow rounded m-3 mb-5 p-2 pt-3 pb-3">
                <article className="row m-0 mb-3">
                    <figure className="col-md-1 col-4">
                        <img   className="" src={icon} alt="logo"/>
                    </figure>
                    <article className="col-md-10 col-12 p-0">
                            <h3  className="d-inline">Weekly Total analysis</h3>
                            <p  className="mb-0">Quick overview of your statistics within this week.</p>
                    </article>
                </article>
                <section className="row m-0">
                    <article className="col col-md-4 col-12 border-right">
                        <h6 className="text-green">120h</h6>
                        <p className="text-grey mb-0">Total no. of hours spent learning</p>
                        <small className="text-danger">Total no. of hours spent learning</small>
                    </article>
                    <article className="col col-md-4 col-12 border-right">
                        <h6 className="text-green">120h</h6>
                        <p className="text-grey mb-0">Total no. of hours spent learning</p>
                        <small className="text-danger">Total no. of hours spent learning</small>
                    </article>
                    <article className="col col-md-4 col-12">
                        <h6 className="text-green">120h</h6>
                        <p className="text-grey mb-0">Total no. of hours spent learning</p>
                        <small className="text-danger">Total no. of hours spent learning</small>
                    </article>
                </section>
            </section>
        </>
    )
}