import { gql } from 'apollo-boost';

export const GET_INFO =  `
query{
  meInstructors{
    id
    email
    first_name
    last_name
    gender
    date_of_birth
    years_of_experience
    description
    is_profile_public
    is_Approval
    need_password
    rate_15_min
    rate_30_min
    rate_60_min
    public_rate_minutes
    public_rate_minutes_type_id
    currency_type{code}
    total_students_counter
    total_earning_counter
    total_profile_views_counter
    currency_type{id code}
    courses_type{id name}
    student_gender_types{id name}
    student_age_types{id name}
    profile_vidoe{id url name}
    profile_image{id url}
    country{id code}
    certificates{id file{name url}}
    Languages{id name }
    notifications{
      data{
        id
        message
        click_action
        created_at
        kind 
        kind_number
        session{id}
        student{id profile_image{url}}
        instructor{id profile_image{url}}
      }
    }
  }
  rateMinutesTypes{
    id
    context
    name
    created_at
    updated_at
  }
}`

export const GET_DATA =  
 `query{
    languageTypes{
    id
    context
    name
    created_at
    updated_at
    },
    currencyTypes{
      id
      code
      symbol
      symbol_native
      created_at
      updated_at
      },
    courseType{
      id
      context
      name
      created_at
      updated_at
      },
      studentGenderTypes{
        id
        context
        name
        created_at
        updated_at
      },
      studentAgeTypes{
        id
        context
        name
        created_at
        updated_at
      }
}`
export const EDIT_PROFILE =  `
mutation( $first_name : Name $last_name : Name $gender : Boolean $date_of_birth : Date  $years_of_experience : Int 
$profile_image :Upload  $description  : Text $is_profile_public : Boolean  $rate_15_min :Int $rate_30_min :Int $rate_60_min :Int $currency_type_id : Int $courses_types_id  : Int $student_gender_types_id : Int  $student_age_types_id : Int $country_id : Int $public_rate_minutes_type_id : Int $profile_vidoe : Upload $certificates : [Upload!] $Spoken_Language_User : [Int !] $delete_certificates_ids : [Int!]){
  PersonEdit(data: {
    first_name : $first_name,
    last_name : $last_name
    gender : $gender
    date_of_birth : $date_of_birth
    years_of_experience : $years_of_experience
    description : $description 
    is_profile_public : $is_profile_public
    rate_15_min : $rate_15_min
    rate_30_min : $rate_30_min
    rate_60_min : $rate_60_min
    currency_type_id : $currency_type_id
    courses_types_id : $courses_types_id 
    student_gender_types_id : $student_gender_types_id 
    student_age_types_id : $student_age_types_id
    country_id : $country_id
    public_rate_minutes_type_id : $public_rate_minutes_type_id
    profile_vidoe : $profile_vidoe
    profile_image : $profile_image
    certificates : $certificates
    Spoken_Language_User : $Spoken_Language_User
    delete_certificates_ids  : $delete_certificates_ids
    
  }){
    Student{id}
    Instructor{id is_profile_public}
    SubInstructor{id}
    Status{check}
  }
}`
