import React from 'react';
import { Link } from 'react-router-dom';
// styles images and icons 
import { Container, Row, Col, Form } from 'react-bootstrap';
import { TextField, Button } from "@material-ui/core";
import placeholder from '../../images/home/map-placeholder.jpg';
import location from '../../images/home/Icon material-location-on.svg';
import phone from '../../images/home/Icon ionic-ios-call.svg';
import email from '../../images/home/email.svg';
import fb from '../../images/home/Icon awesome-facebook-f.svg';
import linkedIn from '../../images/home/Icon awesome-linkedin-in.svg';
import twitter from '../../images/home/Icon awesome-twitter.svg';
import rightShape from '../../images/home/shape-2.svg';
import path from '../../images/home/path.svg';

// language for test
import text from '../../languages/english/translate.json';

export default function GetInTouch() {
    let data = text?.home.getInTouch;
    return (
        <div className="bg-lightGreen mt-5 pt-5 pb-4 p-relative" id="getInTouch">
            <div className="p-absolute" style={{ top: '-118px', left: '-98px' }}>
                <img src={rightShape} alt="shape" className="w-75" />
            </div>
            <Container className="pt-5">
                <Row className="align-items-end m-0 p-relative">
                    <Col md={5} sm={10} className="">
                        <div className="mb-2">
                            <small className="d-block text-grey">{data.smallTitle}</small>
                            <h2 className="text-dark pr-2 pl-2 title-line d-inline-block">
                                <span className="text-green">
                                    {data.title.partOne}
                                </span>
                                {data.title.partTwo}
                            </h2>
                            <div className="pt-3">
                                <p className=" green-line">
                                    {data.description}
                                </p>
                            </div>
                            <Form>
                                <div className=" mb-3">
                                    <TextField
                                        fullWidth
                                        label={data.userName}
                                        variant="outlined"
                                    />
                                </div>
                                <div className=" mb-3">
                                    <TextField
                                        type="email"
                                        fullWidth
                                        label={data.userEmail}
                                        variant="outlined"
                                    />
                                </div>
                                <div className=" mb-3">
                                    <TextField
                                        multiline
                                        fullWidth
                                        rows={4}
                                        label={data.userMessage}
                                        variant="outlined"
                                    />
                                </div>
                                <div>
                                    <Button type="submit" fullWidth className="btn-green">{data.submit}</Button>
                                </div>
                            </Form>
                        </div>
                    </Col>
                    <Col md={7} sm={2} className=" text-right text-center-responsive p-relative">
                        <div className="w-100 mt-3 mb-3">
                            <img src={placeholder} width="417" className="img-fluid " alt="map" />
                        </div>
                        {/* green card for mob */}
                        <div className="bg-green text-white text-left d-mob ">
                            <div className="p-relative  p-3 pb-5">
                                <div className="p-absolute text-right" style={{ bottom: '0', right: '0px' }}>
                                    <img src={path} alt="path" className="w-75" />
                                </div>
                                <h5 className="mb-2">{data.welcome}</h5>
                                <div className="pt-3 pb-1">
                                    <p className="mb-1"><img src={location} width="18" className="pr-2" alt="address" />{data.companyAddress}</p>
                                    <p className="mb-1"><img src={phone} width="20" className="pr-2" alt="phone" />{data.companyPhone}</p>
                                    <p className="mb-1"><img src={email} width="20" className="pr-2" alt="email" />{data.companyEmail}</p>
                                </div>
                                <hr />
                                <div>
                                    <div className="mr-3 d-inline-block">
                                        <Link to="/"><img src={fb} width="10px" alt="facebook" /></Link>
                                    </div>
                                    <div className="mr-3 d-inline-block">
                                        <Link to="/"><img src={linkedIn} width="17" alt="linkedIn" /></Link>
                                    </div>
                                    <div className="d-inline-block">
                                        <Link to="/"><img src={twitter} width="17" alt="twitter" /></Link>
                                    </div>
                                </div>
                            </div>
                        </div>
                        {/* end of green card mob */}
                        {/* green card small mob */}
                        <div className="bg-green text-white text-left d-sm-mob ">
                            <div className="p-relative  p-3 pb-5">
                                <div className="p-absolute text-right" style={{ bottom: '0', right: '0px' }}>
                                    <img src={path} alt="path" className="w-75" />
                                </div>
                                <h5 className="mb-2">{data.welcome}</h5>
                                <div className="pt-3 pb-1">
                                    <p className="mb-1"><img src={location} width="18" className="pr-2" alt="address" />{data.companyAddress}</p>
                                    <p className="mb-1"><img src={phone} width="20" className="pr-2" alt="phone" />{data.companyPhone}</p>
                                    <p className="mb-1"><img src={email} width="20" className="pr-2" alt="email" />{data.companyEmail}</p>
                                </div>
                                <hr />
                                <div>
                                    <div className="mr-3 d-inline-block">
                                        <Link to="/"><img src={fb} width="10px" alt="facebook" /></Link>
                                    </div>
                                    <div className="mr-3 d-inline-block">
                                        <Link to="/"><img src={linkedIn} width="17" alt="linkedIn" /></Link>
                                    </div>
                                    <div className="d-inline-block">
                                        <Link to="/"><img src={twitter} width="17" alt="twitter" /></Link>
                                    </div>
                                </div>
                            </div>
                        </div>
                        {/* end green card small mob */}
                    </Col>
                    {/* green card for web */}
                    <div className="bg-green text-white text-left p-absolute d-web"
                        style={{ maxWidth: '321px', top: "0", left: "43%", zIndex: '1' }}
                    >
                        <div className="p-relative  p-3 pb-5">
                            <div className="p-absolute text-right" style={{ bottom: '0', right: '0px' }}>
                                <img src={path} alt="path" className="w-75" />
                            </div>
                            <h5 className="mb-2">{data.welcome}</h5>
                            <div className="pt-3 pb-1">
                                <p className="mb-1"><img src={location} width="18" className="pr-2" alt="address" />{data.companyAddress}</p>
                                <p className="mb-1"><img src={phone} width="20" className="pr-2" alt="phone" />{data.companyPhone}</p>
                                <p className="mb-1"><img src={email} width="20" className="pr-2" alt="email" />{data.companyEmail}</p>
                            </div>
                            <hr />
                            <div>
                                <div className="mr-3 d-inline-block">
                                    <Link to="/"><img src={fb} width="10px" alt="facebook" /></Link>
                                </div>
                                <div className="mr-3 d-inline-block">
                                    <Link to="/"><img src={linkedIn} width="17" alt="linkedIn" /></Link>
                                </div>
                                <div className="d-inline-block">
                                    <Link to="/"><img src={twitter} width="17" alt="twitter" /></Link>
                                </div>

                            </div>
                        </div>
                    </div>
                    {/* end of green card web */}
                </Row>
            </Container>
        </div>
    )
}