import React from 'react';

// styles
import { Modal, Row, Col } from 'react-bootstrap';
import { Button } from '@material-ui/core';


export default function PopUp(props) {
   let start_session_disabled =  props.saveDisabled === undefined ? false : (props.saveDisabled ? false : true);
    return (
        <div>
            <Modal
                size="md"
                centered
                rounded
                className="row m-0  pr-0"
                show={props.show}
                onHide={e => props.hide()}>
                {props.title &&
                <Modal.Header closeButton className="pr-3 pl-3 pt-2 pb-2 bg-light">
                    <Modal.Title className='h5'>{props.title}</Modal.Title>
                </Modal.Header>
                }
                <Modal.Body>
                    {props.content}
                </Modal.Body>
                {(props.cancelBtn || props.saveBtn) &&
                <Modal.Footer>
                    <Row className="w-100">
                        {props.cancelBtn &&
                            <Col md={props.editBtn?5:6} className="text-left p-0">
                                <Button
                                    onClick={e => props.cancelFun()}
                                    variant="outlined"
                                    className=" pr-4 pl-4">{props.cancelBtn}</Button>
                            </Col>
                        }
                        {props.editBtn &&
                        <Col md={5} className="text-right pl-0">
                            <Button
                                type="submit"
                                variant="outlined"
                                onClick={e => props.editFun()}
                                className="pr-4 pl-4">{props.editBtn}</Button>
                        </Col>
                        }
                        {props.saveBtn &&
                        <Col md={props.editBtn?2:6} className="text-right p-0">
                            <Button
                                disabled={start_session_disabled}
                                type="submit"
                                variant="outlined"  
                                onClick={e => props.saveFun()}
                                className="btn-green pr-4 pl-4 defined">{props.saveBtn}</Button>
                        </Col>
                        }
                    </Row>
                </Modal.Footer>
                }
            </Modal>
                
        </div>
    )
}

