import React, { Component } from 'react';
import { withRouter } from 'react-router';

import {checkName  ,  checkImage , isFutureDate, checkValidateForm} from '../../../validations/validation';


// styles images and icons
import { Row, Col } from 'react-bootstrap';
import {
    TextField, InputLabel, FormControl, Select,
    Button
} from '@material-ui/core';
import pp from '../../../images/intstractor-profile/pp.png'

// language for test
import text from '../../../languages/english/translate.json';

class basicInformationForm extends Component {
    constructor(props) {
        super(props);
        this.state = {
            errors: {
                first_name: '',
                last_name: '',
                profile : ''
            }
        }
    }
    componentDidMount() {
        if (this.props.data) {
            console.log(this.props.data)
            localStorage.setItem('baicInfo', JSON.stringify(this.props.data));
            this.setState({
                first_name: this.props.data.first_name, last_name: this.props.data.last_name,
                gender: this.props.data.gender, date_of_birth: this.props.data.date_of_birth, email: this.props.data.email
            })
        }
        let data = JSON.parse(localStorage.getItem('baicInfo'));
        if (data) {
            this.setState({
                first_name: data.first_name, last_name: data.last_name,
                gender: data.gender, date_of_birth: data.date_of_birth, email: data.email
            })
        }
    }
    render() {
        let data = text["instractor-profile"].create.basicInfo;
        let dataForm = text.registration

        const handleChange = props => event => {
            const value = event.target.value;
            let errors = this.state.errors;
            let gender = this.state.gender;

            if (props === 'profile' && event.target.files[0]) {
                // make sure that the user attache image
                if (!checkImage(event.target.files[0])) {
                    this.setState({ ...this.state, [props]: event.target.files[0] })
                }
                else {
                    this.setState({ ...this.state, [props]: '' })
                }
            }
            else if (value || value === '') {
                this.setState({ ...this.state, [props]: event.target.value.trim() });
            }

            errors.form = ''
            errors.profile = ''
            switch (props) {
                case 'first_name':
                    errors.first_name = checkName(value)
                    break;
                case 'last_name':
                    errors.last_name = checkName(value)
                    break;
                case 'profile':
                    errors.profile = checkImage(event.target.files[0])
                    break;
                case 'date_of_birth':
                    errors.date = isFutureDate(value)
                    break;
                case 'gender':
                    gender =
                        value === "true"
                            ? true
                            : false;
                    this.setState({ ...this.state, gender: gender })
                    break;
                default:
                    break;
            }
        }
        const handleSubmit = (event) => {
            event.preventDefault();
            fileRequired()
            if (checkValidateForm(this.state.errors) && this.state.profile) {

                localStorage.setItem('baicInfo', JSON.stringify(this.state));
                window.scrollTo({ top: 0, behavior: 'smooth' })
                this.props.history.push({
                    pathname: "/workExperience",
                    state: this.state
                })
            }
        }
        const fileRequired = () =>{
            let file = this.state.profile;
            let errors = this.state.errors;
            errors.profile = file === undefined ? text.validation.required : ''
            this.setState({...this.state , errors : errors});
        }
        return (
            <div className="shadow border">
                <div className="border-bottom p-4 text-dark">
                    <Row className="">
                        <Col>
                            <h4>{data.title}</h4>
                            <p>{data.description}</p>
                        </Col>
                    </Row>
                </div>
                <div className="p-4">
                    <Row className="align-items-center">
                        <Col md={3} className="">
                            <div className="text-center">
                                <img
                                    width="150px"
                                    height="150px"
                                    src={this.state.profile && this.state.profile.type ? URL.createObjectURL(this.state.profile) : pp}
                                    alt="profile" className="rounded-circle" />
                            </div>
                            <div className="text-danger text-center"><p className="mb-1">{this.state.errors.profile}</p></div>
                        </Col>
                        <Col md={9}>
                            <div>
                                <h5 className="mb-0">{data.profileTitle}</h5>
                                <p className="text-muted">{data.profileDesc}</p>
                            </div>
                            <div className="mt-4">
                                <label
                                    required
                                    className="mb-0 pt-1 pb-1 pr-3 pl-3 btn-green"
                                    style={{ cursor: 'pointer' }}
                                    for="uploadProfile">
                                    {data.uploadBtn}
                                </label>
                                <input
                                    required
                                    onChange={handleChange('profile')}
                                    type="file"
                                    accept='image/*'
                                    className="d-none"
                                    id="uploadProfile"
                                />
                            </div>
                        </Col>
                    </Row>
                    <div className="mt-4 pb-3 text-lef">
                        <form onSubmit={handleSubmit}>
                            {/* name */}
                            <Row className="m-0 mb-2 w-100">
                                {/* first name */}
                                <Col className="pr-1 pl-0 t">
                                    <InputLabel required>
                                        {dataForm.firstName}
                                    </InputLabel>
                                    <TextField
                                        fullWidth
                                        required
                                        className=""
                                        size="small"
                                        variant="outlined"
                                        value={this.state.first_name && this.state.first_name}
                                        onChange={handleChange('first_name')}
                                    />
                                    <div className="text-danger"><p className="mb-1">{this.state.errors.first_name}</p></div>
                                </Col>
                                {/* last name */}
                                <Col className="pr-0 pl-1 ">
                                    <InputLabel required>
                                        {dataForm.lastName}
                                    </InputLabel>
                                    <TextField
                                        fullWidth

                                        className=""
                                        size="small"
                                        variant="outlined"
                                        value={this.state.last_name && this.state.last_name}
                                        onChange={handleChange('last_name')}
                                    />
                                    <div className="text-danger"><p className="mb-1">{this.state.errors.last_name}</p></div>
                                </Col>
                            </Row>
                            {/* email */}
                            <Row className="m-0 mb-2">
                                <Col className="pr-0 pl-0">
                                    <InputLabel required>
                                        {dataForm.email}
                                    </InputLabel>
                                    <TextField
                                        size="small"
                                        fullWidth
                                        type="email"
                                        disabled
                                        inputProps={{
                                            value: this.state.email
                                        }}
                                        variant="outlined"
                                    />
                                    <div className="text-danger"><p className="mb-1">{this.state.errors.email}</p></div>
                                </Col>
                            </Row>
                            {/* gender and data of birth */}
                            <Row className="m-0 mb-2 w-100">
                                {/* data of birth */}
                                <Col className="pr-1 pl-0">
                                    <div className="">
                                        <InputLabel 
                                            required
                                            className="">{dataForm.dateOfBirth}</InputLabel>
                                        <TextField
                                            required
                                            fullWidth
                                            size="small"
                                            type="date"
                                            inputProps={{value : this.state.date_of_birth && this.state.date_of_birth}}
                                            variant="outlined"
                                            onChange={handleChange('date_of_birth')}
                                        />
                                    </div>
                                    <div className="text-danger"><p className="mb-1">{this.state.errors.date}</p></div>
                                </Col>
                                {/* gender */}
                                <Col className="pr-0 pl-1">
                                    <InputLabel required>
                                        {dataForm.gender}
                                    </InputLabel>
                                    <FormControl
                                        required
                                        className="mb-3"
                                        variant="outlined" fullWidth size="small">
                                        <Select
                                            native
                                            name={dataForm.gender}
                                            onChange={handleChange('gender')}
                                            value={this.state.gender}
                                            inputProps={{
                                                name: dataForm.gender,
                                                id: 'outlined-age-native-simple',
                                            }}
                                        >
                                            <option value=""></option>
                                            <option value={true}>Male</option>
                                            <option value={false}>Female</option>
                                        </Select>
                                    </FormControl>
                                </Col>
                            </Row>
                            {/* form errors */}
                            <div className="text-danger"><p className="mb-1">{this.state.errors.form}</p></div>
                            {/* continue button */}
                            <div className="m-0 align-items-center w-100 border-top pt-3">
                                <div className="text-right ">
                                    <Button
                                        type="submit"
                                        className="btn-green pl-3 pr-3">
                                        {data.continueBtn}
                                    </Button>
                                </div>
                            </div>
                        </form>
                    </div>
                </div>
            </div>
        )
    }
}

export default withRouter(basicInformationForm)
