import React, { useEffect, useState } from 'react';
import $ from 'jquery'; 

import {Url} from '../../../url';
import { GET_INFO} from '../apis/apis';

import Welcome from './welcome.component';
import TodaySessions from '../../session-of-today/today-session.component';
import RecomendedArticles from '../../recomended-articles';
import TotalAnalysis from './total-analysis.component';
import Calendar from '../calendar/index.component';

// styles 
import {Row, Col } from 'react-bootstrap';
import Skeleton from '@material-ui/lab/Skeleton';

export default function Dashboard(props) {
    const [userData, setUserData] = useState()
    const geStudentData = () => {
        if (localStorage.QA_jwt) {
            const token = JSON.parse(localStorage.getItem('QA_jwt')).BearerTokenResponse.access_token;
            //get user data
            $.ajax({
                url: Url,
                method: 'post',
                headers: {
                    authorization: `Bearer ${token}`
                },
                data: {
                    query: GET_INFO
                },
                dataType: 'json',
                success: res => {
                    if (res.data.meStudents) {
                        setUserData(res.data.meStudents)
                    }
                },
                cache: err => console.log(err)
            });
        }
    }
    useEffect(() => {
        geStudentData();
    },[props]);
    console.log(userData)
    if (!userData) {
        return (
            <section className="custom-container mt-5">
         
            <div className="row m-0 mt-5">

                <Col md={9}>
                    <Skeleton className="mt-5" variant="text" width={200} />
                    <Skeleton className="mb-2" variant="text" width={300} />

                    <Skeleton  className="mb-4" variant="rect"  height={200} />
                    <Skeleton width={200} className="mt-5 mb-2" variant="text" />
                    <Skeleton   variant="rect"  height={380} />

                </Col>
                <Col md={3}>
                    <Skeleton  variant="rect"  height={500} />
                </Col>
            </div>

        </section>
        )
    }
    else {
        return (
            <>
                <section className="custom-container">
                    <Row>
                        <Col md={9}>
                            <Welcome data={userData} />
                            <TotalAnalysis />
                            <Calendar />
                        </Col>
                        <Col md={3}>
                            <TodaySessions />
                            <RecomendedArticles />
                        </Col>
                    </Row>
                </section>
            </>
        )
    }
}