import React, { Component } from 'react';
import { withRouter } from 'react-router';

// styles icons 
import {Row, Col } from 'react-bootstrap';
import search from '../../../images/instractor-home/searching.svg';
import student from '../../../images/instractor-home/network.svg';
import coins from '../../../images/instractor-home/coins.svg';

// language for test
import text from '../../../languages/english/translate.json';

class Welcome extends Component {
    componentWillMount() {
    }
    render() {
        let data = text.instractorHome.dashboard;
        return (
            <div >
                <Row className="m-0">
                    <Col>
                        <p className="mb-0">{data.hi}</p>
                        <h3 className="text-green bold">{data.welcome}</h3>
                    </Col>
                </Row>
                <Row className="mt-4 m-0">
                    <Col className="" lg={3} md={4}>
                        <div className="shadow mt-2  p-3 row m-0 mb-2">
                            <Col xs={6} className="p-0 text-left"><h3 className="bold">230K </h3></Col>
                            <Col xs={6} className="p-0 text-right"><img height="42px" src={search} className="img-fluid text-right" alt="search icon"/></Col>
                            <p className="mb-0">{data.viewNumber}</p>
                        </div>
                    </Col>
                    <Col lg={3} md={4} className="">
                        <div className="shadow mt-2  p-3 row m-0 mb-2">
                            <Col xs={6} className="p-0 text-left"><h3 className="bold">230K </h3></Col>
                            <Col xs={6} className="p-0 text-right"><img height="42px" src={student} className="img-fluid text-right" alt="search icon"/></Col>
                            <p className="mb-0">{data.studentNumer}</p>
                        </div>
                    </Col>
                    <Col lg={3} md={4} className="">
                        <div className="shadow mt-2  p-3 row m-0 mb-2">
                            <Col xs={6} className="p-0 text-left"><h3 className="bold">230K </h3></Col>
                            <Col xs={6} className="p-0 text-right"><img height="42px" src={coins} className="img-fluid text-right" alt="search icon"/></Col>
                            <p className="mb-0">{data.earnings}</p>
                        </div>
                    </Col>
                </Row>
            </div>
        )
    }
}

export default withRouter(Welcome)