// external imports
import React from 'react';
// styles images and icons
import { Container, Row, Col } from 'react-bootstrap';
import logo from '../../images/logo/logo-1.svg';

// language for test
import text from '../../languages/english/translate.json';

export default function Main(props) {
    return (
        <div className="bg-main">
            <Container >
                <Row className="pt-2 ">
                    <Col xs={12} className="">
                        <img src={logo} alt="logo" className="img-fluid" />
                    </Col>
                </Row>
                <div className="mb-1">
                    {props.content}
                </div>
            </Container>
            <Row className="bg-light text-center pt-2 sticky-bottom ">
                <Col><small>{text.home.getInTouch.copyRight}</small></Col>
            </Row>
        </div>
    )
}
