import React , {useEffect , useState} from 'react';
import $ from 'jquery';

import SessionCard from './session-card-upcoming.componant';
import SessionCardPast from './session-card-past.componant';
import {Url} from '../../../url';
import {GET_STUDENT_SESSIONS} from './api';
import empty from '../../../images/empty.svg'
import CardSkeleton from '../../loaders/card.component';


import Skeleton from '@material-ui/lab/Skeleton';
import {Tab, Nav} from 'react-bootstrap';

export default function Sessions(){
    const [upcomingSessions, setUpcomingSessions] = useState();
    const [pastSessions, setPastSessions] = useState();
    const [duration, setDuration] = useState();
    const [courseType, setcourseType] = useState();



    const getUpcomingSessions = () => {
        if (localStorage.QA_jwt) {
            const token = JSON.parse(localStorage.getItem('QA_jwt')).BearerTokenResponse.access_token;
            //get user data
            $.ajax({
                url: Url,
                method: 'post',
                headers: {
                    authorization: `Bearer ${token}`
                },
                data: {
                    query: GET_STUDENT_SESSIONS,
                    variables : {orderBy : "ASC"}
                },
                dataType: 'json',
                success: res => {
                    if (res.data.meInstructors) {
                        setUpcomingSessions(res.data.meInstructors.sessionsGet.data);
                        setDuration(res.data.rateMinutesTypes);
                        setcourseType(res.data.courseType);
                    }
                },
                cache: err => console.log(err)
            });
        }
    }
    const getPastSessions = () => {
        if (localStorage.QA_jwt) {
            const token = JSON.parse(localStorage.getItem('QA_jwt')).BearerTokenResponse.access_token;
            //get user data
            $.ajax({
                url: Url,
                method: 'post',
                headers: {
                    authorization: `Bearer ${token}`
                },
                data: {
                    query: GET_STUDENT_SESSIONS,
                    variables : {orderBy : "DESC"}
                },
                dataType: 'json',
                success: res => {
                    if (res.data.meInstructors) {
                        setPastSessions(res.data.meInstructors.sessionsGet.data);
                    }
                },
                cache: err => console.log(err)
            });
        }
    }
    useEffect(() => {
        getUpcomingSessions();
        getPastSessions();
    },[]);
    if ( !upcomingSessions && !pastSessions) {
        return (
            <section className="mt-5 container">
                 <main className="row align-items-center justify-content-center">
                    <CardSkeleton />
                    <CardSkeleton />
                    <CardSkeleton />
                    <CardSkeleton />
                </main>
            </section>
        )
    }
    else {
    return (
        <section className="custom-tab rounded mb-3">
            <Tab.Container  className="p-1" defaultActiveKey="first">
                <header className="row m-0  pt-3 shadow">
                    <Nav variant="pills" className="text-grey container pr-3 pl-3">
                        <Nav.Item>
                            <Nav.Link eventKey="first" className="mr-3 bg-transparent text-center p-0" style={{ outline: 'none' }}>
                                <h6 className="mb-2 font-weight-normal">Upcoming Sessions
                                {/* <span className="bg-light  p-1 text-dark small">1</span> */}
                                </h6>
                            </Nav.Link>
                        </Nav.Item>
                        <Nav.Item >
                            <Nav.Link eventKey="second" className=" bg-transparent text-center p-0" style={{ outline: 'none' }}>
                                <h6 className="mb-2 font-weight-normal">Past Sessions
                                {/* <span className="bg-light p-1 ml-1 text-dark small">1</span> */}
                                </h6>
                            </Nav.Link>
                        </Nav.Item>
                    </Nav>
                </header>
                <main>
                    <Tab.Content className="text-grey container p-0 mt-4">
                        <Tab.Pane eventKey="first" className="">
                            <main className="row m-0">
                                {upcomingSessions &&
                                        upcomingSessions?.length > 0 ?
                                            upcomingSessions.map(session=>
                                                <SessionCard courseType={courseType} duration={duration} getUpcomingSessions={getUpcomingSessions} data={session}/>

                                            ):
                                        <div className="w-100 text-center mt-5">
                                            <p style={{fontSize : '18px'}} className="text-dark  text-center">There's no sessions available</p>
                                            <img src={empty} alt="no data" className="img-fluid"/>
                                        </div>
                                    }
                            </main>
                        </Tab.Pane>
                        <Tab.Pane eventKey="second" className="p-1">
                            <main className="row m-0">
                                    {pastSessions &&
                                    pastSessions?.length > 0 ?
                                     pastSessions.map(session=>
                                        <SessionCardPast getPastSessions={getPastSessions} data={session}/>
                                    ) : 
                                    <div className="w-100 text-center mt-5">
                                        <p style={{fontSize : '18px'}} className="text-dark  text-center">There's no sessions available</p>
                                        <img src={empty} alt="no data" className="img-fluid"/>
                                    </div>
                                    }
                            </main>                               
                        </Tab.Pane>
                    </Tab.Content>
                </main>
            </Tab.Container>
        </section>
    )
  }
}