import React,{useState} from 'react';
import $ from 'jquery';
import Loader from 'react-loader-spinner';


import {EDIT_PROFILE} from '../edit-profile/api';
import {Url} from '../../../url';

import FormControlLabel from '@material-ui/core/FormControlLabel';
import Switch from '@material-ui/core/Switch';

import text from '../../../languages/english/translate.json';

export default function MakeProfilePublic(props) {
    console.log(props.privacy);
    const [state, setState] = useState({
        check: props.privacy,
        loading : false ,
      });
    //   JSON.stringify()
    let pageText = text['instractor-profile'].create.makeProfilePublic
    const handleChange = (event) => {
        // setState({ ...state, [event.target.name]: event.target.checked });
        changePrivacy(event.target.checked)
      };
      const changePrivacy = (check) => {
        setState({ ...state, loading : true});

        console.log(check);
        let token = (localStorage.getItem('QA_jwt')) ? JSON.parse(localStorage.getItem('QA_jwt')).BearerTokenResponse.access_token : '';
        $.ajax({
            url: Url,
            method: 'post',
            headers: {
                authorization: `Bearer ${token}`
            },
            data: {
                query: EDIT_PROFILE,
                variables: JSON.stringify({
                    is_profile_public : check
                })
            },
             dataType : "json",
            success: res => {
                if (res.data.PersonEdit.Status.check) {
                    let privacy = res.data.PersonEdit.Instructor.is_profile_public;
                    setState({ ...state, check: privacy , loading : false});
                }
            },
            cache: err => console.log(err)
        });
      }
      console.log(state.check)
    return(
        <div className="border bg-light p-3 ">
            {state.loading && 
                    <Loader
                        className="loading"
                        type="TailSpin"
                        color="white"
                        height={50}
                        width={50}
                    />
                }
            <div className="row align-items-center m-0">
                <div className="col-8 p-0">
                    <h6 style={{fontSize : '14px'}} className="bold">{pageText.title}</h6>
                </div>
                <div className="col-4 text-right p-0">
                    <FormControlLabel className="mr-0"
                        control={<Switch color="primary" checked={state.check} onChange={handleChange} name="check" />}
                    />
                </div>
            </div>
            <p style={{fontSize : '12px'}} className="text-grey mb-0">{pageText.description}</p>
        </div>
    )
}