import React, { Component } from 'react';


import Welcome from './welcome.component';
import ProfileStringth from '../../profile-strength/profile-strength.component';
import TodaySessions from '../../session-of-today/today-session.component';
import Calendar from './../calendar/index.component';
// styles
import { Row, Col, Container } from 'react-bootstrap';
import $ from 'jquery';
// language for test
import text from '../../../languages/english/translate.json';

export default class Home extends Component {
    constructor(props) {
        super(props);

        this.state = {
        }
    }
    componentWillMount() {
        $.ajax({
            url: 'https://quranacademy.black-squares.com/graphql',
            method: 'post',
            data: {
                query: `  
            query{
                courseType{
                id
                context
                name
                created_at
                updated_at
                }
            }`
            },
            dataType: 'json',
            success: res => {
                if (res.data) {
                    this.setState({ ...this.state, courseType: res.data.courseType })
                }
            },
            cache: err => console.log(err)
        });
        $.ajax({
            url: 'https://quranacademy.black-squares.com/graphql',
            method: 'post',
            data: {
                query: `          
                query{
                    rateMinutesTypes{
                    id
                    context
                    name
                    created_at
                    updated_at
                    }
                }`
            },
            dataType: 'json',
            success: res => {
                if (res.data) {
                    this.setState({ ...this.state, durations: res.data.rateMinutesTypes })
                }
            },
            cache: err => console.log(err)
        });
    }
    render() {
        let data = text.instractorHome.dashboard;

        return (
            <div className="">
                <div className="mt-4 mb-4 mr-3 ml-3">
                    <div className=" m-auto ">
                        <Row className="m-0 align-items-top">
                            <Col className="p-0" md={8}>
                                <div>
                                    <Welcome />
                                </div>
                                <div>
                                    <Calendar
                                        data={this.state} />
                                </div>
                            </Col>
                            <Col md={4}>
                                <ProfileStringth
                                    title={data.improveProfile}
                                    percentage="63"
                                    complete={data.completeProfile}
                                    strength={data.profileStrength} />
                                <TodaySessions />
                            </Col>
                            <Col md={12}>

                            </Col>
                        </Row>
                    </div>
                </div>
            </div>
        )
    }
}