import React from 'react';
import { withRouter } from 'react-router';


import text from '../../../languages/english/translate.json';
import info from '../../../images/icons/info.svg';
import check from '../../../images/icons/Icon feather-check-circle.svg';


function TotalEarnings(props) {
    let pageText = text.instractor.earnings;
    return (
       <aside>
           <div>
               <h4 className="d-inline-block">{pageText.total}</h4> 
               <img src={info} alt="info" className="ml-2" width="24px" height="24px"/>
               <p>{pageText.titalDescription}</p>
           </div>
           <div class="shadow-none p-3 m-0 mb-3 bg-light rounded row ">
               <div className="text-center col-6 p-0 border-right">
                   <h4 className="text-green">{props.currency}{props.currentBalance}</h4>
                   <p className="text-grey">{pageText.currentBalance}</p>
                </div>
                <div className="text-center col-6 p-0">
                    <h4 className="text-green">{props.currency}{props.totalEarnings}</h4>
                   <p className="text-grey">{pageText.totalPaid}</p>
                </div>
           </div>
           {parseInt(props.currentBalance) !== 0?
            <form className="mb-3">
                <button onClick={e=> props.history.push({pathname : '/addPayment' ,  state : props.currentBalance})} className="btn-green w-100">{pageText.payoutRequest}</button>
            </form>
              : ''
            }
            <div>
               <h4 className="mb-3">{pageText.payouts}</h4>
               {/* payout list */}
               {props.payoutList && 
                props.payoutList.map(payout=>
                    <div className="row m-0">
                        <div className="col-8 text-left p-0">
                            <b className="text-dark">{payout.amount}</b>
                            <p className="text-green">{payout.accountType}</p>
                        </div>
                        <div className="col-4 text-right p-0">
                            <p className="d-inline-block" style={{fontSize : '16px'}}>
                                <img width="16px" height="16px" src={check} alt="check" className="mr-2"/>
                              {payout.step} on {payout.created_at}</p>
                        </div>
                    </div>
                    )
               }
                <form className="mb-3">
                    <button className="btn-white-outline w-100" style={{padding : '0.5rem 0.75rem'}}>{pageText.viewAllBtn}</button>
                </form>
            </div>
       </aside>
    )
}
export default withRouter(TotalEarnings);
