import React from 'react';

import FilterByGender from './filter-by-gendar.component';
import FilterByNationality from './filter-by-nationality.component';
import FilterByLanguage from './filter-by-language.component';
import FilterByCourseType from './filter-by-courseType.component';
import FilterByPrice from './filter-by-price.component';
import FilterByDuration from './filter-by-duration.component';

import {Button} from '@material-ui/core';

export default function filters(props) {

    return (
        <section className="border rounded">
            <header className="bg-lightgray pt-2 pb-2  pl-3 border-bottom">
                <h6>Filters</h6>
            </header>
            <form id="searchForm" className="pt-3 pb-2 student-filter">
                <FilterByGender defaultValue={props.defaultValue.gender} handleChange={props.handleChange}/>
                <FilterByNationality defaultValue={props.defaultValue.country} handleChange={props.handleChange}/>
                <FilterByLanguage defaultValue={props.defaultValue.language} handleChange={props.handleChange}/>
                <FilterByCourseType defaultValue={props.defaultValue.course} handleChange={props.handleChange}/>
                <FilterByPrice defaultValue={props.defaultValue.price} handleChange={props.handleChangePrice}/>
                <FilterByDuration  defaultValue={props.defaultValue.duration}  handleChange={props.handleChange}/>
                <article className="pr-3 pl-3">
                    <Button  onClick={props.applyFilter} className="bg-green text-white w-100">Apply Filter</Button>
                    <Button onClick={props.reset} className="text-green w-100 bg-transparent border-0">Reset Filters</Button>
                </article>
            </form>
        </section>
    )
}