import React , {useState} from 'react';
import { withRouter } from 'react-router';
import Loader from 'react-loader-spinner';
import { CometChat } from "@cometchat-pro/chat";
import $ from 'jquery';

import PopUp from '../../pop-up/pop-up.component';
import text from '../../../languages/english/translate.json';
import {Url} from '../../../url';
import {SESSION_CANCEL , SESSION_RESCEDUAL} from './api';
import email from '../../../images/icons/email.svg';


import {Row , Col} from 'react-bootstrap';
import { InputLabel,  FormControl , Select , MenuItem, TextField} from '@material-ui/core';


import Pp from '../../../images/intstractor-profile/pp.png';
import clock from '../../../images/icons/clock.svg';
import { StopTwoTone } from '@material-ui/icons';


function SessionsCard(props){
    const [state , setState] = useState({
        showStartSession : false , 
        showRescedual : false ,
        loading : false,
        showRequestSent : false
    });
    let data = props.data;
    let duration = props.duration;
    let courseType = props.courseType;
    let availableCourseType = [] 
   
    if( courseType &&  data.instructor.courses_type.id === 3){
        availableCourseType = courseType.filter(course => course.id == 1 || course.id == 2)
    }
    else{
        availableCourseType.push(data.instructor.courses_type)
    }
    let textPage = text.student.instractorProfile.reserveSession.calendar;
    const onlyUnique =(value, index, self)=> {
        return self.indexOf(value) === index;
      }
    const handleCloseStartSession = () => setState({...state , showStartSession : false});
    const handleCancelReservedSession = () => {
        setState({...state , loading : true});
        if (localStorage.QA_jwt) {
            const token = JSON.parse(localStorage.getItem('QA_jwt')).BearerTokenResponse.access_token;
            //get user data
            $.ajax({
                url: Url,
                method: 'post',
                headers: {
                    authorization: `Bearer ${token}`
                },
                data: {
                    query: SESSION_CANCEL,
                    variables : {id : data.id}
                },
                dataType: 'json',
                success: res => {
                    setState({...state , loading : false});
                    if(res.data.sessionsCancel.Status.check ==  true){
                        props.getUpcomingSessions();
                        setState({...state , showStartSession : false })
                    }
                },
                cache: err => console.log(err)
            });
        }
    }
    const handleStartSession = () => {
        props.history.push({
            pathname : '/session',
            state : data.id
        })
        // setState({...state , showStartSession : false});
        // let sessionID = data.id;
        // console.log(sessionID)

        // let audioOnly = false;
        // let deafaultLayout = true;

        // let callSettings = new CometChat.CallSettingsBuilder()
        //                     .enableDefaultLayout(deafaultLayout)
        //                     .setSessionID(sessionID)
        //                     .setIsAudioOnlyCall(audioOnly)
        //                     .build();

        // CometChat.startCall(
        // callSettings,
        // document.getElementById("callScreen"),
        // new CometChat.OngoingCallListener({
        //         onUserListUpdated: userList => {
        //             console.log("user list:", userList);
        //     },
        //     onCallEnded: call => {
        //     console.log("Call ended:", call);
        //     },
        //     onError: error => {
        //     console.log("Error :", error);
        //     },
        //     onMediaDeviceListUpdated: deviceList => {
        //     console.log("Device List:", deviceList);
        //     },
        // })
        // );
    }
    const handleRescedual = () => {
        setState({...state , showStartSession : false , showRescedual : true});
    }
    const handleCloseRescedual = () => {
        setState({...state , showRescedual : false});
    }
    const handleOnClickReschedual = () => {
        if (localStorage.QA_jwt) {
            const token = JSON.parse(localStorage.getItem('QA_jwt')).BearerTokenResponse.access_token;
            setState({...state , loading : true })
             $.ajax({
                url: Url,
                method: 'post',
                headers: {
                    authorization: `Bearer ${token}`
                },
                data: {
                    query: SESSION_RESCEDUAL,
                    variables : { 
                        id : data.id ,
                        course_type_id : state.courseType ? state.courseType : data.course_type?.id  ,
                        date : state.date ? state.date : data.date ,
                        time : state.time ? state.time : data.time
                    }
                },
                dataType: 'json',
                success: res => {
                    if(res.data.sessionsEdit.Status.check === true){
                       props.getUpcomingSessions();
                       setState({...state , showRescedual : false , loading : false , showRequestSent : true  })
                    }else{
                       setState({...state , loading : false , error : 'Try another date.'  });

                    }
                },
                cache: err => console.log(err)
            });
        }
    }   
    const handleChange = props => event => {
            if(props === 'time'){
                setState({...state,[props]: event.target.value+":00" , error : ''});
            }
            else
            {
                setState({...state,[props]: event.target.value ,  error : ''});
            }
      };
    const hidewRequestSent = () => setState({...state , showRequestSent : false , error : ''})
    if(data){
    return(
    <section className="col-md-3 mb-3 col-6">
        <div>
            <Loader
                className="loading"
                visible={state.loading}
                type="TailSpin"
                color="white"
                height={50}
                width={50}
            />
        </div>
       <button className="border-0 bg-transparent"  onClick={e=>setState({...state , showStartSession : true})}>
            <figure className="mb-1 text-left">
                <img style={{height : '207px'}} src={data.student.profile_image? data.student.profile_image.url : Pp}  alt="instractor" className="rounded img-fluid"/>
            </figure>
            <article className="row m-0 align-items-center">
                <div className="col-12 pl-0 text-left">
                    <p className="mb-0">{data.student.first_name} {data.student.last_name}</p>
                    <p style={{fontSize : '14px'}} className="mb-0 text-green d-inline-block pr-3">{data.course_type?.name} </p>
                    <p className="text-green mb-0 d-inline-block">
                        <img  src={clock} alt="clock"/>{data.time}
                    </p>
                </div>
            </article>
       </button>
       <PopUp
            title={<Row className="m-0 align-items-center">
                <Col xs={3} className="p-0">
                    <img height="64px" width="64px" className=" rounded-circle" src={data.student.profile_image?.url} alt="instractor"/>
                </Col>
                <Col xs={9} className="pr-0">
                    <p className="mb-0 text-capitalize text-dark " style={{fontSize : '16px' ,  fontWeight : 'normal'}}>{data.student.first_name} {data.student.last_name}</p>
                    <p className="text-grey mb-0" style={{fontSize : '11px' , fontWeight : 'normal'}}>{data.student.email}</p>

                </Col>
            </Row>}
            show={state.showStartSession}
            hide={handleCloseStartSession}
            cancelBtn={textPage.cancelReservationBtn}
            cancelFun={handleCancelReservedSession}
            saveBtn={textPage.startBtn}
            saveFun={handleStartSession}
            // saveDisabled={data.can_start}
            editBtn={textPage.rescedual}
            editFun={handleRescedual}
            content={
                <div>
                    <Row className="border-bottom pt-2 pb-2 m-0">
                        <Col className="text-left bold text-dark" xs={6}>{textPage.date}</Col> <Col xs={6}>{data.date}</Col>
                    </Row>
                    <Row className="border-bottom pt-2 pb-2 m-0">
                        <Col className="text-left bold text-dark" xs={6}>{textPage.time}</Col> <Col xs={6}>{data.time}</Col>
                    </Row>
                    <Row className="border-bottom pt-2 pb-2 m-0">
                        <Col className="text-left bold text-dark" xs={6}>{textPage.duration}</Col> <Col xs={6}>{data.rate_minutes_type.name}</Col>
                    </Row>
                    <Row className="pt-2 pb-2 m-0">
                        <Col className="text-left bold text-dark" xs={6}>{textPage.CourseType}</Col> <Col xs={6}>{data.course_type.name}</Col>
                    </Row>
                    {data.note && 
                        <Row className="pt-2 pb-2 m-0 bg-lightGreen">
                            <Col className="text-left text-green bold text-dark" xs={12}>{textPage.notes}</Col> 
                            <Col xs={12}>{data.note}</Col>
                        </Row>}
                    
                </div>
            }
        />
        <PopUp 
            title={textPage.newReservation}
            show={state.showRescedual}
            hide={handleCloseRescedual}
            cancelBtn={textPage.cancelBtn}
            cancelFun={handleCloseRescedual}
            saveBtn={textPage.rescedual}
            saveFun={handleOnClickReschedual}
            content={
                <div>
                <Row className=" pt-2 pb-2 m-0">
                    <Col md={12} className="p-0">
                        <InputLabel required id="courses" >
                            {textPage.reservationType}
                        </InputLabel>
                        <FormControl size="small" fullWidth>
                            <Select
                                variant="outlined"
                                native
                                required
                                name={textPage.reservationType}
                                defaultValue={data.course_type?.id}
                                onChange={handleChange('courseType')} 
                                
                            >{availableCourseType && availableCourseType.map(ele=>
                                <option value={ele.id}>{ele.name}</option>
                            )}
                            </Select>
                        </FormControl>
                    </Col>
                </Row>
                <Row className=" pt-2 pb-2 m-0">
                    <Col className="text-left  p-0 pr-1 text-dark" xs={6}>
                        <InputLabel required id="courses" >
                            {textPage.date}
                        </InputLabel>
                        <TextField 
                            fullWidth
                            variant="outlined" 
                            onChange={handleChange('date')} 
                            type="date" 
                            defaultValue={data.date}
                            size="small"/>
                    </Col> 
                    <Col className="p-0 pl-1" xs={6}>
                        <InputLabel required id="courses" >
                            {textPage.time}
                        </InputLabel>
                        <TextField 
                            fullWidth
                            variant="outlined" 
                            onChange={handleChange('time')} 
                            type="time" 
                            defaultValue={data.time}
                            size="small"/>
                    </Col>
                </Row>
                <Row className=" pt-2 pb-2 m-0">
                    <Col className="text-left p-0 text-dark" xs={12}>
                        <InputLabel required id="courses" >
                            {textPage.duration}
                        </InputLabel>
                        <FormControl size="small" fullWidth>
                            <Select
                                variant="outlined"
                                native
                                required
                                name={textPage.duration}
                                defaultValue={data.rate_minutes_type.id}
                                disabled
                            >
                                <option value=""></option>
                                {duration?.map(ele =>
                                    <option value={ele.id}>{ele.name}</option>
                                )}
                            </Select>
                        </FormControl>
                    </Col>
                    <Col md={12} className=" p-0"><p className="text-danger">{state.error}</p></Col>
                </Row>
            </div>
            }
        />
        <PopUp 
            show={state.showRequestSent}
            hide={hidewRequestSent}
            content={
                <article className="text-center p-2">
                    <figure className='mb-3'><img height='46px' className="img-fluid" src={email} alt="email"/></figure>
                    <h6 style={{fontSize : '21px'}}>Your request s been sent</h6>
                    <p style={{fontSize : '14px'}}>Your request will be confirmed. Please check your email to see your request statues.If the student rejected your proposal you'll be refunded.</p>
                </article>
            }/>
    </section>
    )
}
}
export default withRouter(SessionsCard);