import React from 'react';
import { Link } from 'react-router-dom';
// styles images and icons 
import { Container , Row , Col} from 'react-bootstrap';
import ArrowForwardIcon from '@material-ui/icons/ArrowForward';
import slide from '../../images/home/images@2x.png';
import leftShape from '../../images/home/shape-1.svg';

// language for test
import text from '../../languages/english/translate.json';

export default function Services() {
    let data = text?.home.difference;
    return (
        <div   className="max-h-100-responsive bg-white pb-5 pt-5 p-relative" id="difference">
            <div className="p-absolute" style={{top : '55px' , right : '-75px'}}>
                <img src={leftShape} alt="shape" className="w-75"/>
            </div>
            <Container className="pt-5">
                <Row   className="align-items-center m-0">
                    <Col md={9} sm={10} className="">
                        <div className="mb-1">
                            <small className="d-block text-grey">{data.smallTitle}</small>
                            <h2 className="text-dark pr-2 pl-2 title-line d-inline-block">
                                {data.title.partOne}
                                <span className="text-green">
                                    {data.title.partTwo}
                                </span> 
                            </h2>
                            <div className="pt-2">
                                <p className=" green-line">
                                    {data.description}
                                </p>
                            </div>
                        </div>
                    </Col>
                    <Col xs={12}>
                        <div className="text-center  mb-1">
                            <img src={slide} alt="slide" className="img-fluid"/>
                        </div>
                        <div className="text-right text-dark">
                            <Link to="/">
                                {data.link}
                                <ArrowForwardIcon />
                            </Link>
                        </div>
                    </Col>
                </Row>
            </Container>
        </div>
    )
}