import React from 'react';



export default function Container(props) {
    return(
        <section className="border-t-green shadow">
            <header className="p-3 pt-2 pb-2 border-bottom">
               <h6 style={{fontSize : '18px'}} className="text-capitalize text-dark"> {props.title} </h6>
            </header>
            <main className="p-3 ">
                {props.content}
            </main>
        </section>
    )
}