import React , {useState , useEffect} from 'react';
import {Form} from 'react-bootstrap';

import Pagination from '../../pagination/pagination.component';
import text from '../../../languages/english/translate.json';
import TableList from './table-list.component';

export default function BalanceActivity(props) {
    const [state , setState] = useState({
        data : ''
    });

    useEffect(()=>{
        if(props.data){
            let temp =  props.data.sort(function(a, b) {
                var dateA = new Date(a.date), dateB = new Date(b.date);
                return dateA - dateB;
            });
            temp = temp.reverse();
            setState({...state , data : temp});
        }
    },[]);
    let pageText = text.instractor.earnings;

    const handleChange = (e) =>{
        let temp ;
        if(e.target.value === 'recent'){
                     temp =  state.data.sort(function(a, b) {
                            var dateA = new Date(a.date), dateB = new Date(b.date);
                            return dateA - dateB;
                        });
                        temp = temp.reverse();
        }
        else{
            temp = state.data.reverse();
        }
        if(temp){
            setState({...state , data : temp});
        }
    }
        return(
            <>
            {state.data ?
                <section>
                    {/* title */}
                    <div>
                        <h4>{pageText.title}</h4>
                        <div className="row">
                            <div className="col-2 pr-0 pt-2">
                                <p className="mb-0">Sort By :</p> 
                            </div>
                            <Form.Control onChange={handleChange} className="border-0 col-2 p-0" size="" as="select">
                                <option value="recent">Recent</option>
                                <option  value="oldest">oldest</option>
                            </Form.Control>
                        </div>
                   
                    </div>
                    {/* table */}
                    <Pagination card={state.data}>
                        <TableList />
                    </Pagination>
                    
                </section>
            :
            <section>
                <h4 className="text-grey w-100 text-center">You Have No Earnings Yet !</h4>    
            </section> 
            }
            </>
        )

}
