import React from 'react';
import { Link } from 'react-router-dom';
// styles images and icons 
import { Container , Row , Col , Button} from 'react-bootstrap';
import slide from '../../images/home/side.png'
// language for test
import text from '../../languages/english/translate.json';

export default function About() {
    let data = text?.home.about;
    return (
        <div className="bg-lightGreen max-h-100-responsive pt-5  pb-5 p-relative" id="about">
            <Container  className="pt-5">
                <Row className="align-items-center max-h-100-responsive m-0">
                    <Col md={7} sm={10} className="">
                        <div className="mb-2">
                            <small className="d-block text-grey">{data.smallTitle}</small>
                            <h2 className="text-dark pr-2 pl-2 title-line d-inline-block">
                                <span className="text-green">
                                    {data.title.partOne}  
                                </span> {data.title.partTwo}
                            </h2>
                            <div className="pt-3">
                                <p className=" green-line">
                                    {data.description}
                                </p>
                            </div>
                        </div>
                        <div className="mb-2 text-center-responsive">
                            <div className="mb-3">
                                <b className="text-dark"> {data.otherVersion} </b>
                            </div>
                            <div className="d-inline-block m-1">
                                <button className="btn-green pr-5 pl-5">
                                    {data.joinBtn}
                                </button>
                            </div>
                            <div className="d-inline-block m-1">
                                <button className="btn-green-outline pr-4 pl-4">
                                    {data.haveAccountBtn}
                                </button>
                            </div>
                        </div>
                    </Col>
                    {/* bicture for web */}
                    <Col  md={5} className=" text-right d-web">
                        <div className="w-100">
                            <img src={slide} className="img-fluid " alt="slide"/>
                        </div>
                    </Col>
                     {/* bicture for mob */}
                    <Col className="p-absolute  d-mob "  
                        style={{top : '-20px' , right : '37px'}}  xs={5}>
                        <div className="">
                            <img src={slide} className="img-fluid " alt="slide"/>
                        </div>
                    </Col>
                </Row>
            </Container>
        </div>
    )
}