import React, { useState } from 'react';
import { withRouter } from 'react-router';
import { graphql } from 'react-apollo';
import * as compose from 'lodash.flowright';
import { Mutation } from 'react-apollo';
import { createUploadLink } from 'apollo-upload-client';
import { ApolloClient } from "apollo-client";
import { ApolloProvider } from "react-apollo";
import { InMemoryCache } from 'apollo-cache-inmemory';
import $ from 'jquery';
import Loader from 'react-loader-spinner';

// internal 
import PopUp from '../../pop-up/pop-up.component';
import { checkImage} from '../../../validations/validation';
import {Url} from '../../../url';
import {EDIT_PROFILE ,  UPLOAD_FILE ,  CHANGE_NAME} from '../apis/apis';
// styles
import { Button } from '@material-ui/core';
import Pp from '../../../images/intstractor-profile/pp.png';
import date from '../../../images/student/date.svg';
import { InputLabel, TextField } from '@material-ui/core';
import { FileCopy } from '@material-ui/icons';

// language for test
import text from '../../../languages/english/translate.json';

function Profile(props) {
    let data = text.registration;
    if(props.data){
        localStorage.setItem('QA_userData' , JSON.stringify(props.data))
    }
    const [state, setState] = React.useState({
        profileImg: props.data?.profile_image?.url || JSON.parse(localStorage.getItem('QA_userData')).profile_image?.url,
        firstName: props.data?.first_name || JSON.parse(localStorage.getItem('QA_userData')).first_name,
        lastName: props.data?.last_name || JSON.parse(localStorage.getItem('QA_userData')).last_name,
        newFirstName: props.data?.first_name || JSON.parse(localStorage.getItem('QA_userData')).first_name,
        newLastName: props.data?.last_name || JSON.parse(localStorage.getItem('QA_userData')).last_name,
        email: props.data?.email || JSON.parse(localStorage.getItem('QA_userData')).email,
        dateOfBirth: props.data?.date_of_birth || JSON.parse(localStorage.getItem('QA_userData')).date_of_birth,
        showEdit: false,
        loading : false ,
        errors: {
            firstName: '',
            lastName: '',
            profileImg: ''
        }
    })
    // activate uploading link on the oppollo 
    const apolloCache = new InMemoryCache()
    const token = (localStorage.getItem('QA_jwt')) ? JSON.parse(localStorage.getItem('QA_jwt')).BearerTokenResponse.access_token : '';
    const uploadLink = createUploadLink({
        uri: Url,
        headers: {
            authorization: token ? `Bearer ${token}` : ""
        }
    })
    const client = new ApolloClient({
        cache: apolloCache,
        link: uploadLink
    })

    const handleShowEdit = () => setState({ ...state, showEdit: true });
    const handleCloseEdit = () => {
        let errors = state.errors;
        errors.firstName = '';
        errors.lastName = '';
        setState({ ...state, showEdit: false , errors : errors , newFirstName : state.firstName , newLastName : state.lastName})
    };
    const handleSubmit = () => {
        setState({ ...state, loading: true });
        if (localStorage.QA_jwt) {
            const token = JSON.parse(localStorage.getItem('QA_jwt')).BearerTokenResponse.access_token;
            if (validateForm(state.errors)) {
                $.ajax({
                    url: Url,
                    method: 'post',
                    headers: {
                        authorization: `Bearer ${token}`
                    },
                    data: {
                        query:EDIT_PROFILE,
                        variables: {
                            first_name: state.newFirstName,
                            last_name: state.newLastName
                        }
                    },
                    dataType: 'json',
                    success: res => {
                        if (res.data.PersonEdit.Status.code === 200) {
                            setState({
                                ...state, profileImg: res.data.PersonEdit.Student.profile_image?.url,
                                firstName: res.data.PersonEdit.Student.first_name,
                                lastName: res.data.PersonEdit.Student.last_name, loading: false
                            });
                            props.getUserData();
                            handleCloseEdit();
                        }
                    },
                    cache: (err) => console.log(err)
                });
            }
        }
    }
    const handleChange = props => event => {
        let value = event.target.value.trim();
        setState({ ...state, [props]: value });
        let nameRegEx = RegExp(/[a-z][A-Za-z ]{2,}/);;
        let errors = state.errors;

        switch (props) {
            case 'newFirstName':
                errors.firstName =
                nameRegEx.test(value)
                        ? ''
                        : text.validation.name;
                break;
            case 'newLastName':
                errors.lastName =
                nameRegEx.test(value)
                        ? ''
                        : text.validation.name;
                break;
            default:
                break;
        }
    }
    const validateForm = (errors) => {
        let valid = true;
        Object.values(errors).forEach(
            // if we have an error string set valid to false
            (val) => val.length > 0 && (valid = false)
        );
        return valid;
    }
    const UploadFile = () => {
        return (
            <Mutation mutation={UPLOAD_FILE}>
                {uploadFile => (
                    <form className="col-md-9 mb-4">
                        <label
                            htmlFor="file-input"
                            id='TextEditorBtn'
                            style={{ fontSize: '', cursor: "pointer" }}
                            className="btn-green d-inline-block font-weight-normal p-1 pr-3 pl-3 mb-0 mr-2"
                        >
                            Upload New
                            </label>
                        <input
                            id="file-input"
                            type="file"
                            className="d-none MuiButtonBase-root MuiButton-root"
                            accept='image/*'
                            onChange={({ target: { validity, files: [file] } }) => {
                                let imgError = checkImage(file);
                                if (!imgError) {
                                    validity.valid &&
                                        setState({ ...state, loading: true });
                                    uploadFile({ variables: { profile_image: file } })
                                        .then((res, loading) => {
                                            if (res.data.PersonEdit.Status.code === 200) {
                                                let errors = state.errors;
                                                errors.profileImg = '';
                                                setState({ ...state, loading: false, profileImg: res.data.PersonEdit.Student.profile_image?.url , errors :errors })
                                            }
                                        });
                                }
                                else {
                                    let errors = state.errors;
                                    errors.profileImg = imgError;
                                    setState({ ...state, loading: false, errors: errors})
                                }
                            }
                            }
                        />
                        {/* <Button 
                                className="btn-green-outline d-inline-block font-weight-normal p-1 pr-3 pl-3"
                            >Delete</Button> */}
                        <small className="text-grey d-block mt-2">You can upload a .jpg, .png, or .jpeg photo with max size of 5MB</small>
                        <p className="text-danger">{state.errors.profileImg}</p>
                    </form>
                )}
            </Mutation>
        );
    };
    return (
        <ApolloProvider client={client}>
            <div>
                <Loader
                    className="loading"
                    visible={state.loading}
                    type="TailSpin"
                    color="white"
                    height={50}
                    width={50}
                />
            </div>
            <section className="mt-5 container">
                <main className="row align-items-center justify-content-center">
                    <figure className="col-md-2 col-12 text-center">
                        <img className="img-fluid  rounded-circle" src={state?.profileImg ? state.profileImg : Pp} alt="profile" />
                    </figure>
                    <article className="col-md-10 col-12 text-center-responsive">
                        <h3 className="text-capitalize">{state.firstName + " " + state.lastName}</h3>
                        <p className="text-grey d-inline">{state.email}</p>
                        <figure className="d-inline"><img className="img-fluid mr-2 ml-2" src={date} alt="date icon" /></figure>
                        <p className="d-inline mb-0 text-grey">{state?.dateOfBirth}</p>
                        <div className="w-100">
                            <Button onClick={handleShowEdit} className="btn-green mt-2 pr-4 pl-4 d-block  text-center-responsive">Edit Profile</Button>
                        </div>
                    </article>
                </main>
                <PopUp
                    title="Edit Basic information"
                    hide={handleCloseEdit}
                    show={state.showEdit}
                    cancelBtn="cancel"
                    cancelFun={handleCloseEdit}
                    saveBtn="Save Changes"
                    saveFun={handleSubmit}
                    content={
                        <main className="row align-items-top justify-content-center">
                            <figure className="col-md-3 mb-4 text-center">
                                <img className="img-fluid  rounded-circle" src={state.profileImg ? state.profileImg : Pp} alt="profile" />
                            </figure>
                            <UploadFile />
                            <div className="col-md-6 mb-2">
                                <InputLabel
                                    required
                                    className="">First Name</InputLabel>
                                <TextField
                                    required
                                    fullWidth
                                    className="bg-light border-0"
                                    size="small"
                                    variant="outlined"
                                    inputProps={{ value : state.newFirstName ? state.newFirstName : ""}}
                                    onChange={handleChange('newFirstName')}
                                />
                                <div className="col-md-12 mb-2 text-danger p-0">
                                    <p className="mb-0">{state.errors.firstName}</p>
                                </div>
                            </div>
                            <div className="col-md-6 mb-2">
                                <InputLabel
                                    required
                                    className="">Last Name</InputLabel>
                                <TextField
                                    required
                                    fullWidth
                                    size="small"
                                    variant="outlined"
                                    inputProps={{ value : state.newLastName ? state.newLastName : ""}}
                                    onChange={handleChange('newLastName')}
                                />
                                <div className="col-md-12 mb-2 text-danger p-0">
                                    <p className="mb-0">{state.errors.lastName}</p>
                                </div>
                            </div>
                            <div className="col-md-12 mb-2">
                                <InputLabel
                                    required
                                    className="">Email Address</InputLabel>
                                <TextField
                                    required
                                    type="email"
                                    fullWidth
                                    size="small"
                                    disabled
                                    variant="outlined"
                                    defaultValue={state.email}
                                />
                            </div>
                            <div className="col-md-12 mb-2">
                                <InputLabel
                                    required
                                    className="">Date Of Birth</InputLabel>
                                <TextField
                                    type="date"
                                    required
                                    fullWidth
                                    size="small"
                                    disabled
                                    variant="outlined"
                                    defaultValue={state.dateOfBirth}
                                />
                            </div>
                        </main>
                    }
                />
            </section>
        </ApolloProvider>
    )
}

export default compose(
    graphql(CHANGE_NAME, {
        name: "change_name"
    })
)(withRouter(Profile));
