import React , {Component} from 'react';
import { withRouter } from 'react-router';

import Mian from './main.component';
import WorkExperienceForm from './work-experience-form.component';

// styles  icons and images
import { Row, Col } from 'react-bootstrap';
import Slide from '../../../images/intstractor-profile/slide-2.svg';

// language for test
import text from '../../../languages/english/translate.json';

function WorkExperience(props){
    let data = text["instractor-profile"].create.workExperience;
        console.log(props)
        return(
            <Mian 
                value={data.progressValue}
                step = {data.stepNumber}
                content={
                    <Row className="mt-5 mb-5">
                        <Col md={5} className="text-center mt-2">
                            <div className="p-5">
                                <img src={Slide} alt="slide" className="img-fluid p-3"/>
                            </div>
                        </Col>
                        <Col  md={6} className="text-align-center">
                            <WorkExperienceForm data={props.location?.state && props.location.state}/>
                        </Col>
                    </Row>
                }
            />
        )
}

export default withRouter(WorkExperience)