// external imports
import React, { Component } from 'react';
import { Link } from 'react-router-dom';
import { withRouter } from 'react-router';
import GoogleLogin from 'react-google-login';
import $ from 'jquery';
import { CometChat } from "@cometchat-pro/chat";
// internal imports
import Main from './main.component';
import { checkEmail, checkPassword, checkValidateForm } from '../../validations/validation';
import { Url } from '../../url';
import { LOGIN, LOGIN_GOOGLE } from './apis';
// styles images and icons
import { Row, Col } from 'react-bootstrap';
import {
    TextField, Checkbox, FormControlLabel, OutlinedInput,
    Button, FormControl, InputLabel, InputAdornment, IconButton
} from '@material-ui/core';
import Visibility from '@material-ui/icons/Visibility';
import VisibilityOff from '@material-ui/icons/VisibilityOff';
import shape from '../../images/login/OBJECTS.svg';

// language for test
import text from '../../languages/english/translate.json';


class Login extends Component {
    constructor(props) {
        super(props);
        this.state = {
            rememberMe: true,
            errors: {
                email: '',
                password: '',
                form: ''
            }
        }
    }
    render() {
        // language variable 
        let data = text.login;

        const handleClickShowPassword = () => {
            this.setState({ ...this.state, showPassword: !this.state.showPassword });
        };
        const handleMouseDownPassword = event => {
            event.preventDefault();
        };
        const handleChange = props => event => {
            const value = event.target.value;

            if (props === 'rememberMe') {
                this.setState({ ...this.state, [props]: event.target.checked });
            } else {
                this.setState({ ...this.state, [props]: event.target.value });
            }

            let errors = this.state.errors;
            errors.form = ''
            errors.loginSucc = ''
            switch (props) {
                case 'email':
                    errors.email = checkEmail(value);
                    break;
                case 'password':
                    errors.password = checkPassword(value);
                    break;
                default:
                    break;
            }
        }
        const handleLogin = (event) => {
            event.preventDefault();
            if (checkValidateForm(this.state.errors)) {
                $.ajax({
                    url: Url,
                    method: 'post',
                    'Access-Control-Allow-Origin' : 'true',
                    data: {
                        query: LOGIN,
                        variables: {
                            "email": this.state.email,
                            "password": this.state.password
                        }
                    },
                    dataType: 'json',
                    success: res => {
                        if (res.data.globalLogin.Status.code === 200) {
                            // intioalize cometchat
                            const appID = "33784fe3bc0b0c2";
                            const region = "us";
                            const appSetting = new CometChat.AppSettingsBuilder().subscribePresenceForAllUsers().setRegion(region).build();
                            CometChat.init(appID, appSetting).then(
                            () => {
                                console.log("Initialization completed successfully");
                                
                                //login function
                                                        
                                const authKey = "9d358e07010f7a0c1bf278f80645c1649d394937";
                                const uid = res.data.globalLogin.Student ?  "STUDENT"+res.data.globalLogin.Student.id : "INSTRUCTOR"+res.data.globalLogin.Instructor.id ;
                                // const uid = "SUPERHERO1";
                                CometChat.login(uid, authKey).then(
                                user => {
                                    console.log("Login Successful:", { user });    
                                },
                                error => {
                                    console.log("Login failed with exception:", { error });
                    
                                }
                                );
                            },
                            error => {
                                console.log("Initialization failed with error:", error);
                                // Check the reason for error and take appropriate action.
                            }
                            );
                            // login successfully
                            let errors = this.state.errors;
                            errors.loginSucc = res.data.globalLogin.Status.message
                            this.setState({ ...this.state, errors: errors })


                            // if remember me checked
                            if (this.state.rememberMe) {
                                
                                localStorage.setItem('QA_jwt', JSON.stringify(res.data.globalLogin));
                                this.props.setUserAuth(res.data.globalLogin);
                                // if user type instractor 
                                if (res.data.globalLogin.Student === null) {
                                    this.props.history.push({
                                        pathname: '/dashboard',
                                    });
                                }
                                // if user type student 
                                else {
                                    this.props.history.push({
                                        pathname: '/dashboard',
                                    });
                                }
                            }
                            else {
                                this.props.setUserAuth(res.data.globalLogin);
                                // if user type instractor 
                                if (res.data.globalLogin.Student === null) {
                                    this.props.history.push({
                                        pathname: '/dashboard',
                                    });
                                }
                                // if user type student 
                                else {
                                    this.props.history.push({
                                        pathname: '/dashboard',
                                    });
                                }
                            }
                        }
                        else {
                            let errors = this.state.errors;
                            errors.form = res.data.globalLogin.Status.message
                            this.setState({ ...this.state, errors: errors })
                        }
                    },
                    cache: err => {
                        let errors = this.state.errors;
                        errors.form = text.validation.form;
                        this.setState({ ...this.state, errors: errors })
                    }
                });
            }
            else {
                console.log("Form not valid!")
            }
        }
        const responseGoogle = (response) => {
            if (response.accessToken) {
                this.setState({ ...this.state, accessToken: response.accessToken });
                $.ajax({
                    url: Url,
                    method: 'post',
                    data: {
                        query: LOGIN_GOOGLE,
                        variables: {
                            "token": response.accessToken,
                            "provider": "google"
                        }
                    },
                    dataType: 'json',
                    success: res => {
                        // if not register  before 
                        if (res.data.globalLoginSocialite.BearerTokenResponse === null) {
                            this.props.history.push({
                                pathname: '/registration',
                                state: response
                            });
                        }
                        // if regitsered before
                        else {
                            localStorage.setItem('QA_jwt', JSON.stringify(res.data.globalLoginSocialite));
                            this.props.setUserAuth(res.data.globalLoginSocialite);

                            if (res.data.globalLoginSocialite.Student === null) {
                                // if the user type instractor 
                                this.props.history.push({
                                    pathname: '/dashboard',
                                });
                            }
                            else {
                                // if the user type student 
                                this.props.history.push({
                                    pathname: '/dashboard',
                                });
                            }
                        }
                    },
                    cache: err => {
                        let errors = this.state.errors;
                        errors.form = text.validation.form;
                        this.setState({ ...this.state, errors: errors })
                    }
                });
            }
        }
        return (
            <Main
                content={
                    <Row className="justify-content-center align-items-center">
                        <Col md={6}>
                            <div>
                                <img src={shape} alt="shape" className="img-fluid" />
                            </div>
                        </Col>
                        <Col className="ml-auto " md={6}>
                            <div className="shadow bg-white p-4">
                                <form onSubmit={handleLogin}>
                                    <div>
                                        <h4>{data.welcome}</h4>
                                        <p>{data.description}</p>
                                    </div>
                                    <div className="w-100 text-center mt-1 mb-2">
                                        <GoogleLogin
                                            className="border-0 shadow rounded"
                                            clientId="468100560729-5fbgm4t3v87f0v15kppflb2ct7qa143m.apps.googleusercontent.com"
                                            buttonText="Login with Google"
                                            onSuccess={responseGoogle}
                                            onFailure={responseGoogle}
                                            cookiePolicy={'single_host_origin'}
                                        />
                                    </div>
                                    <p className="text-center text-grey">{data.or}</p>
                                    <div>
                                        <div className="mb-3">
                                            <TextField
                                                size="small"
                                                fullWidth
                                                type="email"
                                                required
                                                onChange={handleChange('email')}
                                                label={data.userEmail}
                                                variant="outlined"
                                            />
                                            <div className="text-danger"><p className="mb-1">{this.state.errors.email}</p></div>
                                        </div>
                                        <div className="mb-3">
                                            <FormControl
                                                size="small"
                                                required
                                                fullWidth
                                                variant="outlined">
                                                <InputLabel htmlFor="outlined-adornment-password">Password</InputLabel>
                                                <OutlinedInput
                                                    id="outlined-adornment-password"
                                                    type={this.state.showPassword ? 'text' : 'password'}
                                                    value={this.state.password}
                                                    onChange={handleChange('password')}
                                                    endAdornment={
                                                        <InputAdornment position="end">
                                                            <IconButton
                                                                aria-label="toggle password visibility"
                                                                onClick={handleClickShowPassword}
                                                                onMouseDown={handleMouseDownPassword}
                                                                edge="end"
                                                            >
                                                                {this.state.showPassword ? <Visibility /> : <VisibilityOff />}
                                                            </IconButton>
                                                        </InputAdornment>
                                                    }
                                                    labelWidth={70}
                                                />
                                            </FormControl>
                                            <div className="text-danger"><p className="mb-1">{this.state.errors.password}</p></div>
                                        </div>
                                    </div>
                                    <div className="text-danger">
                                        <p className="mb-1">{this.state.errors.form}</p>
                                    </div>
                                    <div className="text-green">
                                        <p className="mb-1">{this.state.errors.loginSucc}</p>
                                    </div>
                                    <Row className="m-0 align-items-center">
                                        <Col md={6} className="text-left p-0">
                                            <FormControlLabel
                                                control={
                                                    <Checkbox
                                                        checked={this.state.rememberMe}
                                                        value={this.state.rememberMe}
                                                        onChange={handleChange('rememberMe')}
                                                        className="text-green"
                                                        color="primary"
                                                        size="small"
                                                    />
                                                }
                                                label={data.rememberMe}
                                            />
                                        </Col>
                                        <Col md={6} className="text-right p-0">
                                            <Link
                                                className="text-green"
                                                to={{
                                                    pathname: '/confirmation',
                                                    state: "passwordresets"
                                                }}
                                            >{data.forgetPassword}</Link>
                                        </Col>
                                        <Col md={12} className=" p-0">
                                            <Button
                                                fullWidth
                                                type="submit"
                                                className="btn-green">
                                                {data.loginBtn}
                                            </Button>
                                        </Col>
                                        <Col md={12} className="text-center pt-3">
                                            <div>
                                                <p className="">{data.dontHaveAcc}
                                                    <Link
                                                        className="text-green"
                                                        to='/registration'>
                                                        {data.createNew}
                                                    </Link>
                                                </p>
                                            </div>
                                        </Col>
                                    </Row>
                                </form>
                            </div>
                        </Col>
                    </Row>
                } 
            />
        )
    }
}

export default withRouter(Login);



