import React from 'react';
import text from '../../../languages/english/translate.json';


export default function TableList(props) {
    let pageText = text.instractor.earnings;

    return (
        <table class="table caption-top table-borderless table-responsive-md">
            <thead>
                <tr className="text-grey">
                    <th scope="col">{pageText.table.date}</th>
                    <th scope="col">{pageText.table.preference}</th>
                    <th scope="col">{pageText.table.Product}</th>
                    <th className="text-right" scope="col">{pageText.table.earnings}</th>
                </tr>
            </thead>
            <tbody>
                {props.data?.map(ele=>
                <tr className="shadow-sm p-3 mt-5 bg-body rounded ">
                    <th scope="row">{ele.date}</th>
                    <td>#2907249</td>
                    <td className="text-green">{ele.course_type.name}</td>
                    <td className="text-right">{ele.rate_salary}{ele.currency_type.symbol}</td>
                </tr>
                )} 
            </tbody>
        </table>
    )
}
